import libreriaFunciones from '../libreriaFuncionalidades/funcionesLib';
// import comun from '../../comun/metodosComunes';
const {funcionesLib} = libreriaFunciones;

// configura la validación dado un objeto de validación
function establecerComportamiento(validacion,inputControl,controlEspecifico){
    let valorInput = validacion.valor;
    let parametros = validacion.parametros ?? [];
    parametros = [valorInput,inputControl,controlEspecifico,...parametros]
    let nombre = validacion.nombre;
    return{
        parametros,nombre
    }
}

export default {

    //ejecuta las funcionalidades de un campo
    ejecutarFunciones: function(funciones,comportamientos){
        funciones.funcionalidadesArreglo.map((x)=>{
            x.valor = funciones.valor;
            let {parametros,nombre} = establecerComportamiento(x,comportamientos);
            funcionesLib[nombre](...parametros);
        })
    },
    //ejecuta una funcion de un campo
    ejecutarFuncion: function(funcionalidades,inputControl,controlEspecifico,funcionalidad,fuente,terminaCarga,terminaCargaComp,variablesForm, parametro){
        try{            
            const funciones = funcionalidades.funcionalidadesArreglo.filter(f => f.nombre == funcionalidad && (f.parametrosCrudos.some(y => y.parametro == parametro) || parametro == null ));
            funciones.forEach(f => {
                if(f){
                    f.valor = funcionalidades.valor;
                    let {parametros,nombre} = establecerComportamiento(f,inputControl,controlEspecifico);
                    funcionesLib[nombre](...parametros,[terminaCarga,fuente,terminaCargaComp,variablesForm]);
                }
            });            
        }catch(err){
            console.log("funcionalidad:"+funcionalidad+"- controlEspecifico: - ");
            console.log(controlEspecifico);
            console.groupEnd();
        }
    },
    ejecutarFuncionManual(nombre,parametros){
        funcionesLib[nombre](...parametros,[true,"funcionManual",true,{}]);
    } 
}
