import axios from "axios";
import global from "../Config";

let headers =  {
    "Content-Type":"application/json"
}

class ContactosService {

    /**
     * Método que obtiene la información del caso positivo.
     * @param {*} params
     */
    obtenerContacto(params) {
        return axios.post( global.apiUrl + "contactos/consultarInfoContacto",params, {headers})
    }

    /**
     * Método que obtiene la lista de todas las aseguradoras.
     * @param {*} params
     */
    obtenerAseguradoras(params) {
        return axios.post(global.apiUrl + "individuales/retornarAseguradoras",params, {headers})
    }

    /**
     * Método que obtiene la lista de todas las ocupaciones.
     * @param {*} params
     */
    obtenerOcupaciones(params) {
        return axios.post(global.apiUrl + "individuales/retornarOcupaciones",params, {headers})
    }

    /**
     * Método que obtiene la lista de todos los paises.
     * @param {*} params
     */
    obtenerPaises(params) {
        return axios.post(global.apiUrl + "individuales/retornarPaises",params, {headers})
    }

    /**
     * Método que obtiene la lista de todos los paises con sus siglas.
     * @param {*} params
     */
    obtenerPaisesSigla(params) {
        return axios.post(global.apiUrl + "individuales/retornarPaisesSigla",params, {headers})
    }

    /**
     * Método que obtiene la lista de departamentos.
     * @param {*} params
     */
    obtenerDep(params) {
        return axios.post(global.apiUrl + "individuales/retornarDepartamentos",params, {headers})
    }

    /**
     * Método que obtiene la lista de municipios.
     * @param {*} params
     */
    obtenerMun(params) {
        return axios.post(global.apiUrl + "individuales/retornarMunicipios",params, {headers})
    }

    /**
     * Método que guarda la información de un contacto.
     * @param {*} params
     */
    GuardarContacto(params) {
        return axios.post(global.apiUrl + "contactos/guardarContacto",params,{headers:global.headersToken})
    }

    /**
     * Método que obtiene la información de un contacto.
     * @param {*} params
     */
    consultarContacto(params){
        return axios.post(global.apiUrl + "contactos/ConsultarDetalleContacto",params,{headers})
    }

    /**
     * Metodo que retorna los tipos de identificacion que estan parametrizados para datos basicos
     * @returns 
     */
    retornarTipIde(){
        return axios.post(global.apiUrl + "contactos/retornarTipIde",{headers})
    }
}

export default new ContactosService();


