import LoginProvider from "../../providers/LoginProvider";
import sincroD from '../../utilidades/offline/sincronizacionDex'
import global from "../../Config";
import metodosComunes from "../../utilidades/comun/metodosComunes";

export default {
    name: "Login",
    data() {
        return {
            email: null,
            password: null,
            dialog: false,
            msg: null,
            loading: null
        };
    },
    methods: {
        onSubmit() {
            this.loading = true
            let params = {
                Username: this.email,
                Password: this.password
            }
            LoginProvider.iniciarSesion(params).then(res => {
                if (res.data.activo == 'A') {
                    const userDataString = JSON.stringify(res.data)
                    localStorage.setItem('app_userSivigila40', userDataString)
                    metodosComunes.enviarUsuarioAlServiceWorker(userDataString);
                    global.headersToken.Authorization = 'Bearer ' + res.data.token;
                    this.$router.push({ path: 'Inicio' })
                    sincroD.sincronizar();
                } else {
                    this.loading = false
                    this.dialog = true;
                    this.msg = "Su usuario se encuentra inactivo o pendiente de activacion, por favor contacte con un administrador";
                }
            }, err => {
                this.loading = false
                switch (err.response.status) {
                    case 401:
                        this.dialog = true;
                        this.msg = "Usuario o contraseña erroneos, por favor intente de nuevo";
                        break;

                    default:
                        this.dialog = true;
                        this.msg = "Error interno, por favor intente mas tarde";
                        break;
                }
            });
        }
    },
    mounted() {
        this.$emit("disenoPagina", false);
    },
};