import OfflineProvider from "../../providers/OfflineProvider";
import IndividualesProvider from "../../providers/IndividualesProvider";
import ColectivosProvider from "../../providers/ColectivosProvider";
import comun from "../../utilidades/comun/metodosComunes";
import global from "../../Config";
import * as moment from "moment/moment";

import constantes from './../constantes'
import { db } from './dbModule';
import { Loading, Notify } from 'quasar'
let countSyncTables = 0;
/**
 * Libreria de metodos para la sincronizacion offline */

export default {
    /**
     * Metodo inicial de sincronizacion
     */
    sincronizar: async function () {
        try {
            countSyncTables = 0;
            await this.realizarReinicios();
            const result = await db.Sincronizacion.toArray(); // consulta tablas de control existentes
            let tablas = result;
            await this.crearTablaDeControl(tablas);
            const resultActual = await db.Sincronizacion.toArray(); // consulta tablas de control
            tablas = resultActual;
            await this.sincronizarTablas(tablas);
            await this.eliminarInactivos();
            if (countSyncTables > 0) {
                Notify.create({
                    message: 'Sincronización exitosa',
                    type: 'positive',
                    timeout: 0,
                    actions: [{ label: 'Ok', color: 'white' }]
                })
            }
        }
        catch (err) {
            console.log(err);
            Notify.create({
                message: 'Algunas tablas no pudieron ser sincronizadas',
                caption: err,
                type: 'negative',
                timeout: 0,
                actions: [{ label: 'Ok', color: 'white' }]
            })
        }
        finally {
            Loading.hide()
        }
    },


    /**
     * Crea los registros de las tablas que llevan control de la sincronización para
     * cada tabla parametrica
     */
    crearTablaDeControl: async function (tablas) {
        let promesas = [];
        const tablasExistentes = tablas.map(x => x.nombre); //obtiene los nombres de las tablas existentes
        const tablasNuevas = constantes.tablasParametricas.filter(n => !tablasExistentes.includes(n)); //obtiene solo las tablas que aun no tienen tabla de control
        //const tablasNuevas2 = db.tables.map(x => x.name) //constantes.tablasParametricas.filter(n => !tablasExistentes.includes(n)); //obtiene solo las tablas que aun no tienen tabla de control
        tablasNuevas.forEach((val) => {
            var doc = {
                nombre: val,
                tipo: "sincronizacion",
                fechaCreacion: new Date().toLocaleString(),
                fechaActualizacion: new Date('1990-01-01T00:00:00')
            };
            promesas.push(db.Sincronizacion.add(doc)); // crea las tablas de control faltantes
        })
        return Promise.all(promesas);
    },

    /**
     * Meetodo que recorre las tablas a sincronizar y las va actualizandi una a una en el localmente
     */
    sincronizarTablas: async function (tablas, mostrarAlerta = true) {
        for (let tabla of tablas) { //recorre las tablas a sncronizar
            let fecha = moment(tabla.fechaActualizacion).format("YYYY-MM-DD HH:mm:ss");
            let params = { tabla: tabla.nombre, parametros: "@fechaCliente = '" + fecha + "'" }
            const res = await OfflineProvider.sincronizar(params); //consulta los datos que se van a sincronizar
            console.log("Dexie: Se insertaran " + res.data.datos.length + " resgistros en " + tabla.nombre);
            if (res.data.datos.length > 0 && mostrarAlerta) {
                countSyncTables++;
                Loading.show({
                    message: "Sincronizando tabla '" + tabla.nombre + "' (" + res.data.datos.length + " registros) <br> Por favor espere...",
                    customClass: 'opaco'
                })
            }
            await this.sincronizarTabla(res.data.datos, res.data.fechaIntentoSincronizacion, tabla.nombre); //llama la función de sincronización para la tabla  
        }
    },


    eliminarInactivos: async function () {
        //Paso 1 - Obtener los elementos inactivos
        const peticionElementosInactivos = await OfflineProvider.retornarInactivos();
        //Paso 2 - Agrupar los elementos inactivos por tabla
        const elementosAEliminar = peticionElementosInactivos.data.datos.reduce((tablas, elemento) => {
            // Verificar si ya existe una entrada para la tabla actual
            const entradaExistente = tablas.find(item => item.tabla === elemento.tabla);
            // Si existe, agregar el id al arreglo existente
            if (entradaExistente) {
                entradaExistente.ids.push(elemento.id.toString());
            } else {
                // Si no existe, crear una nueva entrada con la tabla y el primer id
                tablas.push({
                    tabla: elemento.tabla,
                    ids: [elemento.id.toString()]
                });
            }
            return tablas;
        }, []);
        //Paso 3 - Eliminar los elementos inactivos de la base de datos
        for (let elemento in elementosAEliminar) {
            try {
                const { tabla, ids } = elementosAEliminar[elemento];
                await db[tabla].bulkDelete(ids);
            } catch(e) {
                console.log(e);
            }
        }
    },

    async sincronizarTabla(datos, fechaActualizacion, nombre) {
        try {
            if (datos.length > 0) { // si hay datos para sincronizar
                let ids = datos.map(x => x._id);
                ids = await db[nombre].bulkGet(ids);
                const registros = ids.reduce((r, x, indice) => {
                    r[x == undefined ? 'nuevos' : 'existentes'].push(indice);
                    return r;
                }, { nuevos: [], existentes: [] });

                const nuevos = registros.nuevos.map(x => datos[x]);
                const existentes = registros.existentes.map(x => datos[x]);

                await db.transaction('rw', db[nombre], async () => {
                    if (nuevos.length > 0) await db[nombre].bulkAdd(nuevos);
                    if (existentes.length > 0) await db[nombre].bulkPut(existentes);
                });

                const idSincro = await db.Sincronizacion.where("nombre").equals(nombre).first();
                await db.Sincronizacion.update(idSincro, { fechaActualizacion: fechaActualizacion });//establece la fecha de sincronización actual
            }
        }
        catch (err) {
            console.log('Se presento un error al sincronizar los datos de la tabla: ', nombre);
            console.log(err)
        }
    },

    enviarColectivos: async function () {
        if (global.isOnline) {
            global.enviandoRegistros = true;
            let cs = await db.EventosColectivosSemana.toArray();// consulta eventos Colectivos sin sincronizar
            let cm = await db.EventosColectivosMes.toArray();
            let br = await db.Brotes.toArray();
            for (let registro of cs) {
                try {
                    let colectivoSemana = registro;
                    let key = colectivoSemana._id;
                    colectivoSemana.id = 0;
                    delete colectivoSemana._id;
                    comun.formatearFechasColectivo(colectivoSemana, 's');
                    await ColectivosProvider.guardarDatosColectivos(colectivoSemana, 's')
                    await db.EventosColectivosSemana.delete(key);
                }
                catch (er) {
                    //guareda registro del error
                }
            }
            for (let registro of cm) {
                try {
                    let colectivoMes = registro;
                    let key = colectivoMes._id;
                    colectivoMes.id = 0;
                    delete colectivoMes._id;
                    comun.formatearFechasColectivo(colectivoMes, 'm');
                    await ColectivosProvider.guardarDatosColectivos(colectivoMes, 'm')
                    await db.EventosColectivosMes.delete(key);
                }
                catch (er) {
                    //guareda registro del error
                }
            }
            for (let registro of br) {
                try {
                    console.log('Guardando Brotes sincro')
                    let brotes = registro;
                    let key = brotes._id;
                    brotes.id = 0;
                    delete brotes._id;
                    comun.formatearFechasColectivo(brotes, 'b');
                    await ColectivosProvider.guardarDatosColectivos(brotes, 'b')
                    await db.Brotes.delete(key);
                }
                catch (er) {
                    //guarda registro del error
                }
            }
            global.colectivosLocales = await this.enumerarColectivosLocales();


            global.enviandoRegistros = false;
        }
    },

    async enumerarColectivosLocales() {
        let ColectivosSemana = await db.EventosColectivosSemana.toArray();
        let ColectivosMes = await db.EventosColectivosMes.toArray();
        let Brotes = await db.Brotes.toArray();
        let Colectivos = [...ColectivosSemana, ...ColectivosMes, ...Brotes];
        return Colectivos.length;
    },

    async enumerarLaboratoriosLocales() {
        let laboratorios = await db.AnalisisLaboratorio.toArray();

        return laboratorios.length;
    },

    enviarIndividuales: async function () {
        if (global.isOnline) {
            global.enviandoRegistros = true;
            let individuales = await db.Eventos.toArray(); // consulta eventos sin sincronizar
            for (let evento of individuales) { //recorre los eventos pendientes de sincronizar
                try {
                    const key = evento._id; //obtiene el registro local 
                    const complementario = evento.complementario;
                    evento.id = 0;
                    delete evento.complementario;
                    delete evento._id;
                    complementario.idEvento = null;
                    complementario.id = 0;
                    comun.formatearFechasEventoGuardado(evento, complementario);
                    await IndividualesProvider.guardarIndividual({ evento, complementario }); //sincroniza con el servidor
                    await db.Eventos.delete(key); //borra el registro local
                    global.eventosLocales = await this.enumerarEventosLocales();
                }
                catch (er) { //guarda registro del error

                }
            }
            global.enviandoRegistros = false;
        }
    },

    async enumerarEventosLocales() {
        let numEventos = await db.EventosColectivosSemana.toArray();
        numEventos = numEventos.length;
        return numEventos;
    },


    async borrarTablasParametricas() {
        Loading.show({ message: "Reiniciando sincronización..." });
        for (let tabla of constantes.tablasParametricas) {
            try {
                await db[tabla].clear();
            } catch (err) {
                console.log(tabla);
                console.log(err);
            }
        }
        await db.Sincronizacion.clear();
        Loading.hide();
    },

    async borrarTabla(nombreTabla, fechaReinicio = null) {
        const sincroTabla = await db.Sincronizacion.where("nombre").equals(nombreTabla).first();
        const fechaActualizacion = new Date(sincroTabla.fechaActualizacion).getTime();
        if (fechaActualizacion < fechaReinicio || fechaReinicio == null) {
            console.log(nombreTabla);
            await db[nombreTabla].clear();
            await db.Sincronizacion.update(sincroTabla, { fechaActualizacion: new Date('1990-01-01T00:00:00') });
            console.log("reinicia: " + nombreTabla);
        }
    },

    async realizarReinicios() {
        for (let tabla of constantes.reinicioTablas) {
            try {
                this.borrarTabla(tabla.nombre, tabla.fecha);
            } catch (err) {
                const sincroTabla = await db.Sincronizacion.where("nombre").equals(tabla.nombre).first();
                console.log(sincroTabla, tabla.nombre, ': ', err);
            }
        }
    }
}