import axios from "axios";
import global from "../Config";
import { db } from "../utilidades/offline/dbModule";
// 'Authorization': 'Bearer ' + token

class GridLaboratoriosUIService {

    /**
     * Esta función consulta los últimos laboratorios para no saturar la grilla con todos
     * @param {*} params
     */
    retornarLaboratorios(params) {
        return axios.post(global.apiUrl + "laboratorios/consultarUltimosLaboratorios", params, { headers: global.headersToken })
    }
    async retornarLaboratoriosLocales() {
        return db.AnalisisLaboratorio.toArray();
    }


}

export default new GridLaboratoriosUIService();
