
export default {
  name: "DropDownControl",
  props: ["options", "deshabilitar", "tooltip", "mask", "valorCampo","hayError","mensajeError","esTabla"],
  data() {
    return {
      valorCrudo: this.valorCampo,
      claseTabla: this.esTabla ? "inputsTablas" : ""
    };
  },
  methods: {
    emitirSeleccion() {
      const opcionActual = this.options.filter(x => x.value == this.valor.toString());
      const texto = opcionActual.length > 0 ? opcionActual[0].label : "";
      this.$emit("seleccion", this.valor,texto);
      this.$emit("blur");
    },
    establecerTexto(){
      try{
        const opcionActual = this.options.filter(x => x.value == this.valorCrudo.toString());
        const texto = opcionActual.length > 0 ? opcionActual[0].label : "";
        this.$emit("estableceTexto",texto);
      }catch(er){
        console.log("error de escritura de variables");
      }
    },
    cambiaEstadoOpciones(estado,opciones){
      const opcionesArreglo = opciones.split(',');
      opcionesArreglo.forEach(element => {
        this.options[element].disable = !estado; //niega el estado porque true es habilitado y false deshabilitado
      });
      console.log(this.options);
    }
  },
  computed: {
    valor: {
      get () {
        return this.valorCrudo == null ? "" : this.valorCrudo
      },
      set (val) {
          this.valorCrudo = val        
      }
    }
  },
};
