import Vue from 'vue'
import App from './App.vue'
import './quasar'
import router from './router/index.js'
import { Vue2Storage } from 'vue2-storage'
import Vuelidate from 'vuelidate'
import _ from "lodash";
import PopUpSiv from './utilidades/plugins/alertas/Alerta';
import global from "./Config";
import jwt_decode from "jwt-decode";
import './registerServiceWorker'
import sincroDex from './utilidades/offline/sincronizacionDex'
import comun from './utilidades/comun/metodosComunes'
let ignorarApi = false;
let ApiCargada = false;

/**
 * Inicio del aplicativo (Vinculacion con sivigila 4.0 y configuracion de las variables globales)
 */
fetch(process.env.BASE_URL + "config.json", { credentials: 'same-origin' }).then(resp => resp.json()).then(data => {
  Object.assign(global, data); //se le asigna el valor del json de configuracion al archivo de variables globales
  global.isOnline = window.navigator.onLine //Detecta si el app esta online u offline
  global.isInstalled = (window.matchMedia("(display-mode: standalone)").matches || !global.isProd);
  global.enviandoRegistros = false;
  global.version = '1.1.1';
  if (!global.isOnline) {
    ignorarApi = true;
    InicializarVue(localStorage.getItem('app_userSivigila40'));
    return;
  }
  const pathArrayURL = new URL(global.apiUrl) //Se crea un objeto tipo url a partir de la url del api
  let helperFrameWindow = document.getElementById('helperFrame');
  helperFrameWindow.src = (pathArrayURL.href.replace('/api/', '')) + "/Inicio/Index"
  helperFrameWindow.addEventListener("load", function () {
    helperFrameWindow.contentWindow.postMessage('getUserId', '*');
  });
  setTimeout(() => {
    if (!(ignorarApi || ApiCargada)) console.log("...")
  }, 10000)
  window.addEventListener('message', (event) => {
    if (event.origin == pathArrayURL.origin) {
      if (event.data != null && !global.isInstalled) { //toma el token de la variable de sesion del api
        ApiCargada = true;
        let user = JSON.parse(jwt_decode(event.data).Data);
        user.token = event.data;
        localStorage.setItem('app_userSivigila40', JSON.stringify(user));
        global.headersToken.Authorization = 'Bearer ' + event.data;
        InicializarVue(user);
      } else if (localStorage.getItem('app_userSivigila40') != null) { //toma el token del dominio de captura en linea
        const lsUser = localStorage.getItem('app_userSivigila40');
        let user = JSON.parse(lsUser);
        global.headersToken.Authorization = 'Bearer ' + user.token;
        InicializarVue(user);
      } else {
        localStorage.removeItem("app_userSivigila40");
        if (global.isInstalled) {
          InicializarVue();
        } else window.location.href = global.siv40;
      }
    }
  });
});

/**
 * Inicializacion de vue
 */
function InicializarVue(user) {
  Vue.prototype._ = _;
  Vue.config.productionTip = false
  Vue.use(PopUpSiv)
  Vue.use(Vue2Storage)
  Vue.use(require("moment"));
  Vue.use(Vuelidate)
  Vue.use(router)

  new Vue({
    router,
    Vue2Storage,
    created() {
      validarExistencia(user, this.$route);
      if (user != null && user != undefined) {
        const tokenValido = validarVencimiento(user);
        if (tokenValido) sincroDex.sincronizar();
      }
    },
    render: h => h(App),
  }).$mount('#app')
}
/**
 * Hook previo a cada navegacion entre rutas
 */
router.beforeEach((to, from, next) => {
  const lsUser = localStorage.getItem('app_userSivigila40');
  if (lsUser != null) {
    let user = JSON.parse(lsUser);
    const tokenValido = validarVencimiento(user);
    if (tokenValido) next(); else return;
    if (to.name === 'Login') { //Restringir Login
      next({ name: 'Inicio' })
    }
    if ((to.name !== 'Inicio' && to.name !== 'laboratorios' && to.name !== 'gridLaboratoriosUI') && user.laboratorio) { //Restringir laboratorios
      next({ name: 'Inicio' })
    }
    if ((to.name === 'colectivos' || to.name === 'gridColectivos') && user.TipoEntidad === "EAPB") { //Restringir Colectivos
      next({ name: 'Inicio' })
    }
    if ((to.name === 'caracterizacion' || to.name === 'gridCaracterizacion') && !(user.TipoEntidad === "INS" || user.admin)) { //Restringir Caracterizacion
      next({ name: 'Inicio' })
    }
  } else next()
})

/**
 * Valida la fecha de expiracion del token para que cuando se venza retorne o solicite de nuevo el login
 * @param {*} user Token del usuario
 * @param {*} next instancia del hook para permitir el acceso a la ruta
 */
function validarVencimiento(user) {
  const jwtPayload = jwt_decode(user.token);
  if ((jwtPayload.exp < Date.now() / 1000) && global.isOnline) { //Si el token ya expiro muestra un modal para renovar la sesion
    if (global.isInstalled) {
      comun.reloguear(user);
      return false
    } else window.location.href = global.siv40;
  } else return true
}

/**
 * Funcion para evitar que se inicie la aplicacion sin una variable de sesion disponible
 * @param {*} user Variable de sesion del usuario
 * @param {*} route Instancia de la ruta para validar el nombre
 */
function validarExistencia(user, route) {
  if (user != null && (route.name === 'Default' || route.name == null)) {
    router.push('/Inicio')
  } else if (user == null && route.name !== 'Login') {
    router.push('/Login')
  }
}