import axios from "axios";
import global from "../Config";
import { db } from "../utilidades/offline/dbModule";

let headers = {
  "Content-Type": "application/json"
}

class LaboratoriosService {
  /**
   * Guarda el laboratorio contenido en el objeto data
   */
  async guardarLaboratorios(data, editar) {
    var params = Object.assign({}, data);
    // return axios.post(global.apiUrl + "SvcCrud/Laboratorios", params, { headers:global.headersToken })
    let res = { data: { response: true } };
    if (editar) {
      try {
        await db.AnalisisLaboratorio.put(params);
        return res
      } catch (err) {
        console.log(err);
        res.data.response = false;
        res.data.mensaje = "No pudo guardarse el registro, por favor consulte al administrador del sistema";
        return res;
      }
    } else {
      try {
        await db.AnalisisLaboratorio.add(params);
        return res
      } catch (err) {
        console.log(err);
        res.data.response = false;
        res.data.mensaje = "No pudo guardarse el registro, por favor consulte al administrador del sistema";
        return res;
      }
    }
  }

  async guardarLaboratoriosOnline(data) {
    return axios.post(global.apiUrl + "SvcCrud/Laboratorios", data, { headers: global.headersToken })
  }

  /**
   * Lista los tipos de indentificación
   */
  async retornarTipIde(cod_eve) {
    const metadataBasicos = await (await axios.post(global.apiUrl + "Individuales/retornarDatosBasicos", { cod_eve }, { headers: global.headersToken })).data;
    const tipIde = metadataBasicos.find(x => x.campo == 'Tip_ide').opciones;
    return JSON.parse(tipIde);
  }

  /**
   * Busca el laboratorio dado por parámetro
   * @param {*} params 
   */
  obtenerLaboratorio(params) {
    return axios.post(global.apiUrl + "laboratorios/consultarLaboratorio", params, { headers })
  }

  /**
 * Busca el laboratorio dado por parámetro
 * @param {*} param
 */
  async consultarLaboratorioOffLine(params) {
    const idLaboratorio = params.idLab.replace('OF', '')
    const laboratorio = await db.AnalisisLaboratorio.get(parseInt(idLaboratorio));
    return laboratorio;
  }

  /**
   * Obtiene la lista de eventos contenida en la tabla ElementosLaboratorio
   * @param {*} params 
   */
  obtenerEventosConLaboratorios(params) {
    return axios.post(global.apiUrl + "laboratorios/consultarEventosConLaboratorios", params, { headers: global.headersToken })
  }

  /**
   * Retorna el elemento de laboratorio con codigo de evento y aliasLista dado
   * @param {*} params 
   */
  obtenerRequerido(params) {
    return axios.post(global.apiUrl + "comunCaptura/retornarElementosLaboratorio", params, { headers })
  }

  /**
   * Método que trae los ajustes de laboratorios que se encuentran en la tabla lista maestra
   * @returns 
   */
  retornarTipoAjuste() {
    var params = null;
    return axios.post(global.apiUrl + "Individuales/retornarAjustesLaboratorios", params, { headers })
  }
  /**
   * Método que verifica que el laboratorio a ingresar con las 7 llaves tenga información en detalle evento
   * @param {*} params 
   * @returns 
   */
  retornarDetalleEventoParaLaboratorio(params) {
    return axios.post(global.apiUrl + "Laboratorios/RetornarDetalleEventoParaLaboratorio", params, { headers })
  }
  /**
   * Método que cuenta la cantidad de registros bajo las 7 llaves en la tabla de análisis laboratorios
   * @param {*} params 
   * @returns 
   */
  retornarCantidadAnalisisLaboratorio(params) {
    return axios.post(global.apiUrl + "Laboratorios/RetornarCantidadAnalisisLaboratorio", params, { headers })
  }

  async retornarIdEventoReal(idEventoActual) {
    const relacionEventosLab = await db.RelacionEventosLaboratorios.where("idEventoOffline").equals(idEventoActual).first();
    if (relacionEventosLab) { // si ya existe la relacion entre el evento local y el evento online
      if (relacionEventosLab.idEventoOnline != null) { //valida que el id de evento online no sea nulo
        await db.RelacionEventosLaboratorios.delete(relacionEventosLab._id); //borra la relacion ya que no es necesaria
        return relacionEventosLab.idEventoOnline // y retorna el id Real del evento online
      }
    }
    return idEventoActual // de lo contrario retorna el id offline actual
  }
}

export default new LaboratoriosService();
