
import ComunProvider from "@/providers/ComunProvider";

export default {
  name: "GeoAutocomplete",
  props: ["deshabilitar", "tooltip", "max", "mask", "fillmask","valorCampo","hayError","mensajeError","valoresAdmitidos","longitud","esTabla"],
  data() {
    return {
      valorCrudo: this.valorCampo,
      // valor: null,
      options: [],
      optionsData: [],
      pais: "",
      depto: "",
      mun: "",
      claseTabla: this.esTabla ? "inputsTablas" : ""
    }
  },
  methods: {
    filterFn (val, update, abort) {
      if(val == "" || val.length < 7){
        abort()
        return;
      }
      update(async () => {
        let options = await ComunProvider.obtenerGeoautocomplete(this.valor.toUpperCase(),this.depto,this.pais,this.mun)
        if(options != undefined && options.data != undefined && options.data.predictions != undefined){
          this.optionsData = options.data.predictions;
          let formated_options = [];
          for(let i=0; i<options.data.predictions.length; i++){
            formated_options[i] = options.data.predictions[i].description;
          }
          this.options = formated_options;
          update()
        }
      });
    },
    setvalor (val) {
      this.valor = val
    },
    obtenerLatLng(){
      this.emitirComportamiento();
      this.revisarSinInfo();
      this.emitirSeleccion();
      let id;
      for(let i=0; i<this.optionsData.length; i++){
        if(this.optionsData[i].description == this.valor){
          id = this.optionsData[i].place_id
          break
        }
      }
      if(id != undefined && id != "")
        this.$emit('enviarLatLng', id);
    },
    recibirDatos(pais, depto, mun){
      this.pais = pais;
      this.depto = depto;
      this.mun = mun;
    },
    emitirComportamiento() {
      this.valor = this.valor.replace(eval(this.valoresAdmitidos),'')
      this.$emit("blur", this.valor.replace(eval(this.valoresAdmitidos),''));
    },
    emitirSeleccion() {
      this.$emit("seleccion", this.valor.replace(eval(this.valoresAdmitidos),'').toUpperCase());
    },
    revisarSinInfo(){
      if(this.valor == "" || this.valor == "SIN INFORMACIÓN"){
        this.valor = "SIN INFORMACIÓN";
        this.placeholder = "SIN INFORMACIÓN";
      }
    },
  },
  mounted() {
  },
    computed: {
    valor: {
      get () {
        return this.valorCrudo == null ? "" : this.valorCrudo;
      },
      set (val) {
        this.valorCrudo = val;
      }
    }
  }
}
