    /**
     * Funcion que agrupa un arreglo de objetos por medio de una llave, y generando la forma del json para el formulario dinámico.
     * @param {*} array 
     * @param {*} key 
     */
    agruparArreglo(array, key) {
        return new Promise((resolver) => {
            let datos = array.reduce((result, currentValue) => {
                (result[currentValue[key]] = result[currentValue[key]] || []).push(
                    currentValue
                );
                return result;
            }, {});
            let res = Object.keys(datos).map((b, index) => {
                return { key: index, nombreGrupo: b, controles: datos[b] };
            });
            resolver(res);
        })
    },
    /**
     * 
     * @param {*} fechaNacimiento 
     * @param {*} fechaActual 
     */
    CalcularEdad(fechaNacimiento, fechaActual) {
        return new Promise((resolver) => {
            var fecha1 = moment(fechaNacimiento);
            var fecha2 = moment(fechaActual);

            var anos = fecha2.diff(fecha1, 'years');
            var meses = fecha2.diff(fecha1, 'months');
            var dias = fecha2.diff(fecha1, 'days');
            var horas = fecha2.diff(fecha1, 'hours');
            var minutos = fecha2.diff(fecha1, 'minutes');
            var segundos = fecha2.diff(fecha1, 'seconds');

            let res = { anos, meses, dias, horas, minutos, segundos }
            resolver(res);
        })
    },

    /**
     * Método que calcula el número de días de nacido
     * @param {*} fechaNacimiento 
     * @param {*} fechaActual 
     */
    CalcularDiferenciaDiasEdad(fechaNacimiento, fechaActual) {
        return new Promise((resolver) => {
            var nacimiento = fechaNacimiento
            var hoy = fechaActual

            var fechaini = moment(nacimiento);
            var fechafin = moment(hoy);
            var resultado = fechafin.diff(fechaini, 'days');
            resolver(resultado);
        })
    },
    /**
     * Método que calcula el número de meses entre dos fechas
     * @param {*} fechaNacimiento 
     * @param {*} fechaNotificacion 
     */
    CalcularDiferenciaMesesEdad(fechaNacimiento, fechaNotificacion) {
        return new Promise((resolver) => {
            var nacimiento = fechaNacimiento.split("/");
            var hoy = fechaNotificacion.split("/");
            var diferencia = 0;
            var resultado = 0;
            var aproximacion = 0;
            var tiempoPasado = moment([hoy[2], hoy[1], hoy[0]]).diff(moment([nacimiento[2], nacimiento[1], nacimiento[0]]), 'months', true);
            
            diferencia = Math.round(tiempoPasado * 10) / 10;

            aproximacion = diferencia - 0.5;
            if (aproximacion > parseInt(aproximacion)) {
                resultado = Math.round(tiempoPasado);
            }
            else {
                resultado = aproximacion;
            }
            resolver(resultado);
        })
    },
    /**
* Calcula el número de meses (sin decimales) de diferencia entre dos
fechas en formato dd/mm/yyyy
* @param {*} fechaNacimiento
* @param {*} fechaNotificacion
*/
    CalcularDiferenciaMesesEdadSinAproximar(fechaNacimiento,
        fechaNotificacion) {
        return new Promise((resolver) => {

            // Define variables necesarias para hacer conversiones a partir de diferencias de fechas en milisegundos
            const minute = 1000 * 60;
            const hour = minute * 60;
            const day = hour * 24;
            const month = day * 30.42;
            //const year = day * 365;

            // Crea objetos de la clase Date tomando como base los parámteros fechaNacimiento, fechaNotificacion
            const partesFechaNacimiento = fechaNacimiento.split("/");
            const objetoFechaNacimiento = new
                Date(partesFechaNacimiento[2], partesFechaNacimiento[1] - 1,
                    partesFechaNacimiento[0]);

            const partesFechaActual = fechaNotificacion.split("/");
            const objetoFechaActual = new Date(partesFechaActual[2],
                partesFechaActual[1] - 1, partesFechaActual[0]);

            //Calcula la diferencia en meses
            const diferenciaenMeses = Math.trunc((objetoFechaActual -
                objetoFechaNacimiento) / month);

            resolver(diferenciaenMeses);
        })
    },

    agruparTabla(objeto) {
        return new Promise((resolver) => {
            let tablas = [];
            objeto.forEach((element) => {
                let datos = element.controles.reduce((result, currentValue) => {
                    if (currentValue["tabla"] != null) {
                        (result[currentValue["tabla"]] = result[currentValue["tabla"]] || []).push(
                            currentValue
                        );
                    }
                    return result;
                }, {});
                Array.prototype.push.apply(tablas, Object.values(datos))
            });
            resolver([].concat.apply([], tablas));
        })
    },
