    /**
     * Metodo que obtiene información actualizada para la validación del campo
     * @param {*} validacionesArreglo arreglo de validaciones del campo
     * @param {*} capturaVariables objeto de captura de variables (es el que se envía al crear o actualizar un registro)
     */
    procesarObjetoValidaciones(validacionesArreglo, capturaVariables, capturaBasicos, variablesFormulario, capturaCompl) {
        validacionesArreglo.map((v) => {
            v.parametros = [];
            //v.parametrosCrudos.sort((a,b)=>{return a.orden-b.orden}); //organiza las validaciones por orden
            v.parametrosCrudos.map((p) => {
                switch (p.tipo) {
                    case "dependiente":
                        v.parametros.push(capturaVariables[p.parametro] ?? "")
                        break;
                    case "dependienteBasicos":
                        v.parametros.push(capturaBasicos[p.parametro] ?? "")
                        break;
                    case "variablesFormulario":
                        v.parametros.push(variablesFormulario[p.parametro] ?? "")
                        break;
                    case "dependienteCompl":
                        v.parametros.push(capturaCompl[p.parametro] ?? "")
                        break;
                    default:
                        v.parametros.push(p.parametro)
                }
            })
        })
    },

    procesarObjetoFuncionalidades(funcionalidades, capturaVariables, capturaBasicos, capturaCompl) {
        funcionalidades.map((f) => {
            f.parametros = [];
            f.parametrosCrudos.sort((a, b) => { return b.orden - a.orden }); //organiza los parametros por orden
            f.parametrosCrudos.map((p) => {
                switch (p.tipo) {
                    case "dependiente":
                        f.parametros.push(capturaVariables[p.parametro] ?? "")
                        break;
                    case "dependienteBasicos":
                        f.parametros.push(capturaBasicos[p.parametro] ?? "")
                        break;
                    case "dependienteCompl":
                        f.parametros.push(capturaCompl[p.parametro] ?? "")
                        break;
                    default:
                        f.parametros.push(p.parametro)
                }
            })
        })
    },

    procesarObjetoFuncionalidad(funcionalidades, capturaVariables, funcionalidad, capturaBasicos, variablesFormulario, capturaCompl, referencias) {
        const func = funcionalidades.filter(f => f.nombre == funcionalidad);
        func.forEach(f => {
            if (f) {
                f.parametros = [];
                try {
                    f.parametrosCrudos.map((p) => {
                        p.tipo = p.tipo == "dependiente" ? (p.parametro[0] == p.parametro[0].toUpperCase() ? "dependienteBasicos" : "dependienteComp") : p.tipo;
                        switch (p.tipo) {
                            case "dependiente":
                                f.parametros.push(capturaVariables[p.parametro] ?? "")
                                break;
                            case "dependienteBasicos":
                                f.parametros.push(capturaBasicos[p.parametro[0].toUpperCase() + p.parametro.substring(1).toLowerCase()] ?? "")
                                break;
                            case "variablesFormulario":
                                f.parametros.push(variablesFormulario[p.parametro] ?? "")
                                break;
                            case "dependienteComp":
                                f.parametros.push(capturaCompl[p.parametro] ?? "");
                                break;
                            case "referencia":
                                f.parametros.push(referencias[p.parametro][0] ?? "")
                                break;
                            default:
                                f.parametros.push(p.parametro)
                        }
                    })
                } catch (err) {
                    console.group();
                    console.log(referencias);
                    console.log(f);
                    console.groupEnd();
                }
            }
            //return f; 
        });
    },

