
import LookUpTableControlN from "../controles/LookUpTableControlN";
import global from "../../Config";
export default {
  name: "SearchForm",
  components: { LookUpTableControlN },
  data() {
    return {
      ide: ['Registro Civil', 'Tarjeta de Identidad', 'Cédula de ciudadania', 'Cédula de extranjeria',
        'Pasaporte', 'Permiso especial de permanencia', 'Menor sin identificación', 'Adulto sin Identificación'],
      Numide_pos: "",
      Num_ide: "",
      Cod_evepos: "",
      Pri_nom: "",
      Seg_nom: "",
      Pri_ape: "",
      Seg_ape: "",
      llaves: [
        "Numide_pos",
        "Num_ide",
        "Cod_evepos",
        "Pri_nom",
        "Seg_nom",
        "Pri_ape",
        "Seg_ape",
      ],
      linkeventos: global.apiUrl + "Individuales/retornarListaEventos",
      componentKey: 0,
      componentKey2: 100, //la key se usa para re-renderizar el componente y que se limpie cuando se presione limpiar
    };
  },
  methods: {
    /**
     * Crea un objeto con los campos que no estén vacíos y lo emite al evento "busqueda"
     */
    buscar() {
      let objeto = {};
      let contador = 0;
      for (const key of this.llaves) {
        if (this[key] != "") {
          objeto[key] = this[key];
          contador ++;
        }
      }
      if(contador === 0){
        this.$modal.alertar({
          titulo: "Alerta",
          mensaje: "Establezca los criterios para hacer la busqueda.",
          tipo: 3
        })
      }
      else {
        this.$emit("setFiltro", objeto);
        this.$emit("busqueda");
      }
    },
    buscaDesdeBarra(valorString) {
      let objeto = {};
      for (const key of this.llaves) {
        objeto[key] = valorString;
      }

      objeto.limitaRegistros= '1';
      return objeto;
      // this.$emit("busqueda", objeto);
    },
    /**
     * Limpia los campos y emite reinicio para ser manejado por la grilla
     */
    reiniciar() {
      for (const key of this.llaves) {
        this[key] = "";
      }

      this.componentKey += 1;
      this.componentKey2 += 1;

      this.$emit("reinicio");
    },
    /**
     * Cierra el componente
     */
    cerrar() {
      this.$emit("cerrar");
    },
    /**
     * Obtiene el evento de la tabla de lookup
     */
    obtenerEvento(valor) {
      this.Cod_evepos = valor;
    },
    obtenerTipIde(valor){
      switch (valor){
        case this.ide[0]: this.Tipide_pos = 'RC'; break
        case this.ide[1]: this.Tipide_pos = 'TI'; break
        case this.ide[2]: this.Tipide_pos = 'CC'; break
        case this.ide[3]: this.Tipide_pos = 'CE'; break
        case this.ide[4]: this.Tipide_pos = 'PA'; break
        case this.ide[5]: this.Tipide_pos = 'PE'; break
        case this.ide[6]: this.Tipide_pos = 'MS'; break
        case this.ide[7]: this.Tipide_pos = 'AS'; break
      }
    },
    obtenerTipIdeCtc(valor){
      switch (valor){
        case this.ide[0]: this.Tip_ide = 'RC'; break
        case this.ide[1]: this.Tip_ide = 'TI'; break
        case this.ide[2]: this.Tip_ide = 'CC'; break
        case this.ide[3]: this.Tip_ide = 'CE'; break
        case this.ide[4]: this.Tip_ide = 'PA'; break
        case this.ide[5]: this.Tip_ide = 'PE'; break
        case this.ide[6]: this.Tip_ide = 'MS'; break
        case this.ide[7]: this.Tip_ide = 'AS'; break
      }
    },
    regexcheck2(campo,regexp){
      this[campo] = this[campo].replace(regexp, '').toUpperCase();
      // this[campo] = this[campo].replace(/[^a-zA-Z\u00f1\u00d1\s]+/g, '').toUpperCase();
    }
  },
  computed: {},
};
