
import ComunProvider from "../../providers/ComunProvider";

export default {
  name: "DropDownControl",
  props: ["options", "deshabilitar", "tooltip", "mask", "valorCampo","hayError","mensajeError","esTabla","campo"],
  data() {
    return {
      valorCrudo: this.valorCampo,
      opciones: [],
      claseTabla: this.esTabla ? "inputsTablas" : "",
      valorMostrar:this.valorCampo,
      tooltipCrudo: this.tooltip,
      cargandoLista: false,
      opcionesHabilitadas: [],
      opcionesDesabilitadas: [],
    };
  },
  methods: {
    emitirSeleccion() {
      const texto = this.opciones.filter(x => x.value == this.valor.toString())[0].label;
      this.valorMostrar = texto;
      this.$emit("seleccion", this.valor,texto);
      this.$emit("seleccionDescripcion", this.valor,texto);
      this.$emit("blur");
    },
    async CargarOpciones(params) {
      try 
      {
        const res = await ComunProvider.retornarPOST(this.options, params)
        if (res?.data == null) return;
        this.opciones = res.data.map((b) => {
          return { value: b[Object.keys(b)[0]],  label: b[Object.keys(b)[1]],disable: b[Object.keys(b)[2] ?? false]};
        });
        if(this.valor && res.data.length > 0){
          const opcionActual = this.opciones.filter(x => x.value == this.valor.toString());
          const texto = opcionActual.length > 0 ? opcionActual[0].label : "";
          this.valorMostrar = texto;
          this.$emit("estableceTexto",texto);
        }
        if(this.tooltip != undefined && this.tooltip.indexOf('|dinamico|') != -1){
          let toolT = this.opciones.map(x => x.value + ' - '+ x.label).join().replace(/,/g,'<br>');
          this.cambiarTooltips(toolT);
        }
        this.opcionesHabilitadas.forEach(element => {
          this.opciones[element].disable = false;
        });
        this.opcionesDesabilitadas.forEach(element => {
          this.opciones[element].disable = true;
        });
      }
      catch (error) {
        console.log("error", error?.message);
      }
    },
    logOpciones(){
      console.log(this.opciones);
    },
    cambiarTooltips(nuevoValor){
      this.toolTipD = nuevoValor;
    },
    cambiaEstadoOpciones(opcionesHabilitadas,opcionesDesabilitadas){
      this.opcionesHabilitadas = opcionesHabilitadas;
      this.opcionesDesabilitadas = opcionesDesabilitadas;

      // const opcionesArreglo = opciones.split(',');
      // opcionesArreglo.forEach(element => {
      //   console.log(this.options,"   -   ",element);
      //   this.options[element].disable = !estado; //niega el estado porque true es habilitado y false deshabilitado
      // });
      // console.log(this.options);
    }
    // establecerTexto(){
    //   const texto = this.opciones.filter(x => x.value == this.valorCrudo.toString())[0].label;
    //   this.$emit("estableceTexto",texto);
    // }
  },
  computed: {
    valor: {
      get () {
        return this.valorCrudo == null ? "" : this.valorCrudo
      },
      set (val) {
          this.valorCrudo = val        
      }
    },
    toolTipD: {
      get () {
        return this.tooltipCrudo == null ? "" : this.tooltipCrudo
      },
      set (val) {
          this.tooltipCrudo = val        
      }
    }
  },
  mounted() {
    this.CargarOpciones();
  }
};
