
import TextoControl from '../controles/TextoControl';
import TelefonoControl from '../controles/TelefonoControl';
import SeparadorControl from '../controles/SeparadorControl';
import DropDownControl from '../controles/DropDownControl';
import DropDownRequestControl from '../controles/DropDownRequestControl';
import LookUpTableControl from '../controles/LookUpTableControl';
import LookUpTableControlNumIde from '../controles/LookUpTableControlNumIde';
import LookUpTableControlMultiSeleccion from '../controles/LookUpTableControlMultiSeleccion';
import RadioGroupControl from '../controles/RadioGroupControl';
import FechaControl from '../controles/FechaControl';
import CheckBoxControl from '../controles/CheckBoxControl';
import validador from '../../utilidades/validaciones/interpreteValidaciones/validador';
import comun from '../../utilidades/comun/metodosComunes';
import interpreteFunciones from '../../utilidades/funcionalidadesInput/interpreteFuncionalidades/interpreteFunciones';
import GeoAutocomplete from "../controles/GeoAutocomplete";
import * as moment from "moment/moment";
import { Notify } from 'quasar'

export default {
    name:'InputControl',
    props:['valorDefecto','tipo','label','campo','opciones','deshabilitarProp','tooltip','mask', 'fillmask','valorCampo','validaciones','funcionalidades','indiceControl','esTabla',
           'indiceGrupo','jsonAsociado','terminaCarga','valoresAdmitidos', 'max','descripcionLook', 'ocultarCampo', 'cl_lutValor', 'cl_lutDesc','terminaCargaComp','variablesForm','clase'],
    components: { SeparadorControl, TextoControl, LookUpTableControl, LookUpTableControlMultiSeleccion, DropDownControl, DropDownRequestControl, RadioGroupControl, FechaControl, CheckBoxControl, LookUpTableControlNumIde, GeoAutocomplete,TelefonoControl},
    data(){
        return{
            resultadoValidacion:{ valido:true,mensaje:""},
            mensaje:"",
            ejecutaValidacion:false,
            valorCrudo:this.valorCampo,
            tipoCrudo:this.tipo,
            ocultarCrudo: this.ocultarCampo,
            deshabilitarCrudo:this.deshabilitarProp,
            requerido: false,
            puedeEjecutarAfectantes: true,
            valorTexto:null,
            puedeValidar:true,
            deshabilitadoObligatorio: this.deshabilitarProp,
        }
    },
    methods:{
        enviarLatLng(place_id){
          this.$emit('establecerLatLng', place_id)
        },
        obtenerRespuesta(valor,texto) {
            if(this.tipoCampo == "ctrlDdl" || this.tipoCampo == "ctrlDdlr")
                this.valorTexto = texto;
            this.valor = valor;
        },
        estableceTexto(texto){
            if(texto != null && texto != undefined){
                this.valorTexto = texto;
                this.$emit("estableceTexto",{clave:this.campo,texto:this.valorTexto});
            }
        },
        mostrarAlerta(titulo, mensaje, tipo, onConfirm, cancelButton, onCancel,textoConfirm,textoCancel) {
            this.$modal.alertar({titulo,mensaje,tipo, onConfirm, cancelButton, onCancel,textoConfirm,textoCancel})
        },
        notificar(mensaje, tiempo) {
            Notify.create({
                spinner: true,
                message: mensaje,
                timeout: tiempo ?? 2000,
                position: 'bottom-right'
            })
        },
        reload(id) {
            if (this.$route.path != '/Individuales/' + id) {
                this.$router.push({ path: '/Individuales/' + id });
                location.reload();
            }
        },
        obtenerRespuestaDescripcion(campoAsociado,valor){
            this.valorTexto = valor;
            this.$emit("estableceTexto",{clave:this.campo,texto:valor});
            // this.$emit("seleccion",{clave:this.campo,valor,jsonAsociado:this.jsonAsociado});
        },
        // ejecutarFuncionalidades(valor) {
        //     this.$emit("blur",{clave:this.campo,valor});
        // },
        configurarFuncionalidad({valor,funcionalidad}) {
            this.$emit("configuraFuncionalidad",{clave:this.campo,valor,funcionalidad});
        },
        validar(){  
                this.$emit("configurarValidacion",{clave:this.campo,jsonAsociado:this.jsonAsociado});
                return new Promise((resolve) => {
                    if(this.puedeValidar){  
                        if(!this.ocultar){                
                            validador.ejecutarValidaciones(this.validaciones).then((retono) =>{
                                // if(retono.mensajePersonalizado == undefined)
                                this.resultadoValidacion = retono;
                                resolve(retono);
                            },err=>{
                                console.log(err);
                            });
                        }else{
                            const retorno = {valido:true, mensaje: ""};
                            this.resultadoValidacion = retorno;
                            resolve(retorno);
                        }
                    }else{
                        const retorno = {valido:true, mensaje: ""};
                        this.resultadoValidacion = retorno;
                        resolve(retorno);
                    }
                }); 
            },           
        ejecutarComportamiento(funcionalidad,fuente, parametro){
            interpreteFunciones.ejecutarFuncion(this.funcionalidades,this,this.$refs[this.campo+"Ref"],funcionalidad,fuente,this.terminaCarga,this.terminaCargaComp,this.variablesForm, parametro); //,
        },
        ejecutarFuncionesAfectantesBlur(){
            this.emitirFuncionesAfectantes("blur");
        },
        ejecutarComportamientoDesdeControl(funcionalidad,parametros){
            interpreteFunciones.ejecutarFuncionManual(funcionalidad,[this.valor,this,this.$refs[this.campo+"Ref"],...parametros]); //,
        },
        emitirFuncionesAfectantes(fuente){
            this.funcionalidades.valor = this.valor;
            this.$emit("emitirFuncionesAfectantes",this.funcionalidades.funcionalidadesAfectantes,this.jsonAsociado,fuente,this.validaciones.validacionesAfectantes);
        },
        emitirFuncion(funcion,...params){
            this.$emit('emitirMetodo',funcion,...params);
        },
        cambiaModelo(){
            if(this.tipoCampo == 'ctrlCb'){
                this.valorTexto =  ((this.valor.toString() == '1' ) ? 'Si' : 'No'); // reemplaza 1 y 2 por Si y No para la ficha de impresión
                this.valor = (!comun.tieneValor(this.valor)) ? '2' : this.valor;
            }
            this.$refs[this.campo+"Ref"].valor = this.valor;
            this.$emit("seleccion",{clave:this.campo,valor:this.valor,jsonAsociado:this.jsonAsociado,texto:this.valorTexto});
            if(this.encadenaAfectantes)this.emitirFuncionesAfectantes("change");
            if(this.tipoCampo != "ctrlText" || this.campo == 'Num_ide' || this.campo == 'Vereda'){
                this.validar();
            }
            this.encadenaAfectantes = true;
        },
        FocoSiguienteInput(){
            this.$emit("FocoSiguienteInput",this.campo, this.indiceGrupo,this.indiceControl);
        },
        ponerFoco(){
            this.$refs[this.campo+"Ref"].ponerFoco();
        },
        ponerFocoYLimpiar(){
            this.valor = '';
            this.$refs[this.campo+"Ref"].ponerFoco();
        },
        ponerFocoYValorDefecto(){
            if(typeof this.$refs[this.campo+"Ref"].ponerFoco() === 'function')
            this.$refs[this.campo+"Ref"].ponerFoco();
            this.valor = this.valorPorDefecto;
        },
        blur(){
            this.ejecutarFuncionesAfectantesBlur();
            setTimeout(() => {
                this.validar();
            }, 100);
        },
        cambiaEstadoOpciones(opcionesHabilitadas,opcionesDesabilitadas){
            this.$refs[this.campo+"Ref"].cambiaEstadoOpciones(opcionesHabilitadas,opcionesDesabilitadas);
        }
    },
    mounted(){
        this.ocultar = this.ocultarCrudo;
        let requerido = this.validaciones.validacionesArreglo.find(function(ev){
            return ev.nombre == "requerido"
        });
        this.requerido = (requerido != undefined) ? true : false;
    },
    computed: {
        valor: {
            get () {
                return this.valorCrudo
            },
            set (val) {
                if(val != "" && this.tipoCampo == 'ctrlFecha'){
                    this.valorCrudo = ((val.length > 10) ? moment(val).format("DD/MM/YYYY") : (val.length == 10) ? (val.indexOf('-')!=-1) ?  moment(val, "YYYY-MM-DD").format("DD/MM/YYYY") : moment(val, "DD/MM/YYYY").format("DD/MM/YYYY") : val)
                }else{
                    try{ 
                        this.valorCrudo =  val.toString();
                    }catch(e){
                        console.log(this.campo);
                        console.log(val);
                    }
                }
            }
        },
        deshabilitar:{
            get () {
                return Boolean(this.deshabilitarCrudo)
            },
            set (val) {
                if(this.tipoCampo != null){
                    if(this.tipoCampo.includes("ctrlLut")){
                        this.$refs[this.campo+"Ref"].deshabilitarValor = val
                    }else{
                        this.deshabilitarCrudo = Boolean(val)
                    }
                }
            }
        },
        ocultar:{
            get () {
                return this.ocultarCrudo
            },
            set (val) {
                if(val){
                    this.$el.parentElement.style.display = 'none';
                }
                else
                {
                    this.$el.parentElement.style.display = 'block';
                }
                this.ocultarCrudo = val
            }
        },
        tipoCampo:{
            get () {
                return this.tipoCrudo
            },
            set (val) {
                this.tipoCrudo = val
            }
        },
        valorPorDefecto: function () {
            return this.tipoCampo == "ctrlCb" ? "2": this.valorDefecto;
        },
        encadenaAfectantes: {
            get () {
                return this.puedeEjecutarAfectantes;
            },
            set (val) {
                this.puedeEjecutarAfectantes = val;
            }
        },
    },
    watch: {
        valor() {
            this.cambiaModelo();
        }
    }
}
