import InputControl from "../../componentes/controles/InputControl";
import DropDownControl from "../../componentes/controles/DropDownControl";
import SearchFormContactos from "../../componentes/controles/SearchFormContactos";
//import moment from "moment";
import GridContactosProvider from "../../providers/GridContactosProvider";
import { QPagination } from "quasar";

export default {
    name: "GridContactos",
    components: { InputControl, DropDownControl, SearchFormContactos, QPagination  },
    data() {
        return {
            pagesNumber:0,
            filtro:null,
            filter: '',
            loading: false,
            /**
             * Configuración inicial de la grilla
             */
            pagination: {
                sortBy: 'Numide_pos',
                descending: true,
                page: 1,
                rowsPerPage: 50,
                rowsNumber: 0,
            },
            columns: [
                { name: 'name', align: 'center', label: '', field: row => row.id, sortable: false },
                { name: 'Tipide_pos', align: 'center', label: 'Tipo doc. positivo', field: 'Tipide_pos', sortable: false },
                { name: 'Numide_pos', align: 'center', label: 'Número documento positivo', field: 'Numide_pos', sortable: false },
                { name: 'Cod_evepos', align: 'center', label: 'Código evento positivo', field: 'Cod_evepos', sortable: false },
                { name: 'Tip_ide', align: 'center', label: 'Tipo doc.', field: 'Tip_ide', sortable: false },
                { name: 'Num_ide', align: 'center', label: 'Numero documento', field: 'Num_ide', sortable: false },
                { name: 'Pri_nom', align: 'center', label: 'Pri_nom', field: 'Pri_nom', sortable: false, format: (val) => val.toUpperCase() },
                { name: 'Seg_nom', align: 'center', label: 'Seg_nom', field: 'Seg_nom', sortable: false, format: (val) => val.toUpperCase() },
                { name: 'Pri_ape', align: 'center', label: 'Pri_ape', field: 'Pri_ape', sortable: false, format: (val) => val.toUpperCase() },
                { name: 'Seg_ape', align: 'center', label: 'Seg_ape', field: 'Seg_ape', sortable: false, format: (val) => val.toUpperCase() }
            ],
            data: [],
            original: [],
            vacio: true,
            filas: 0,
            busq: false,
            drawerLeft: false,
        }
    },
    mounted() {
        this.loading = true;
        this.onRequest({pagination: this.pagination,filter: undefined});
        this.$emit("disenoPagina", true);
    },
    methods: {
        avanza(){
            this.onRequest({pagination: this.pagination, filter:this.filter, filtro:this.filtro});
        },
        /**
         * Abre el laboratorio con el id dado
         * @param {*} id
         */
        enviar(id) {
            let ruta = "Contactos/" + id;
            this.$router.push(ruta);
        },

        /**
         * va para atrás
         */
        atras() {
            this.$router.go(-1);
        },

        onRequest(props) {
            const { page, rowsPerPage} = props.pagination;

            let filtros = {};
            if(props.filter != '' && props.filter != undefined){
                const filtro = this.$refs['buscador'].buscaDesdeBarra(props.filter);
                filtros = {filtros,...filtro};
            }else if(props.filtro != undefined && props.filtro != null){
                filtros = props.filtro;
            }

            // calculate starting row of data
            let startRow = (page - 1) * rowsPerPage
            if(startRow < 0)
                startRow = 0
            this.fetchFromServer(startRow, rowsPerPage,filtros,page);
        },

        // Filtra solo por las llaves dadas
        fetchFromServer(startRow, count, parametros, page) {
            this.loading = true;
            parametros = {startRow, numeroReg: count, ...parametros};
            GridContactosProvider.retornarContactos(parametros).then(res => {
                this.data = res.data;
                this.pagination.rowsNumber = this.data.length > 0 ? this.data[0].Oportuna : 0;
                this.pagination.page = page;
                this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
                this.loading = false;
            }, err => {
                if (err.response.status == 401) {
                    this.$modal.alertar({
                        titulo: "Atención", mensaje: "Sesión vencida, por favor vuelva a ingresar", tipo: 2, onConfirm: () => {
                            window.location.href = global.siv40;
                        }
                    })
                }
            })
        },
        /**
         * Hace el toggle para abrir y cerrar la búsqueda avanzada
         */
        search() {
            this.drawerLeft = !this.drawerLeft;
        },
        /**
         * Busca filas que tengan los valores dados en las llaves dadas
         */
        buscar() {
            this.filter = '';
            this.fetchFromServer(0,50,{limitaRegistros:'0',...this.filtro},1);
            this.loading = false;
        },
        /**
         * Limpia la búsqueda avanzada
         */
        reinicio(){
            this.filtro = '';
            this.onRequest({
                pagination: this.pagination,
                filter: undefined
            })
        },
        setFiltro(objeto){
            this.filtro = objeto;
        }
    }
}
