import axios from "axios";
import global from "../Config";
import {db} from "../utilidades/offline/dbModule";
import _ from "lodash";
// 'Authorization': 'Bearer ' + token

class GridCaracterizacionService {

    retornarColectivos(params) {
        return axios.post(global.apiUrl + "colectivos/obtenerUltimosColectivos",params,{headers:global.headersToken})
    }

    async retornarColectivosLocales(parametros) {
        let params = JSON.parse(JSON.stringify(parametros, function(a, b) {
            return typeof b === "string" ? b.toUpperCase() : b
        }));
        let ColectivosSemana;
        let ColectivosMes;
        let Brotes;
        let Colectivos;
        if((params.Ano == params.Cod_eve && params.Cod_eve == params.Cod_pre) ){
            let busqueda = params.Cod_eve
            ColectivosSemana = await db.EventosColectivosSemana.filter(x => !('SyncError' in  x)).or("SyncError").equals(0).and(x => x.Cod_eve == busqueda || x.Cod_pre == busqueda || x.Ano == busqueda || x.Semana == busqueda || x.Municipio == busqueda).toArray()
            ColectivosMes = await db.EventosColectivosMes.filter(x => !('SyncError' in  x)).or("SyncError").equals(0).and(x => x.Cod_eve == busqueda || x.Cod_pre == busqueda || x.Ano == busqueda || x.Semana == busqueda || x.Municipio == busqueda).toArray()
            Brotes = await db.Brotes.filter(x => !('SyncError' in  x)).or("SyncError").equals(0).and(x => x.Cod_eve == busqueda || x.Cod_pre == busqueda || x.Ano == busqueda || x.Semana == busqueda || x.Municipio == busqueda).toArray()//(pend)iente ajustar busqueda brotesm desde la barra)
            Brotes = Brotes.map(x=>_.mapKeys(x, function (v, k) { return _.upperFirst(_.toLower(k)); }))

        }else{
            Object.keys(params).forEach(key => {
                if( key == "startRow" || key == "numeroReg" || key == "limitaRegistros"){
                    delete params[key]
                }
            });
            ColectivosSemana = await db.EventosColectivosSemana.where(params).and(x => !('SyncError' in  x) || x.SyncError == 0).toArray();
            ColectivosMes = await db.EventosColectivosMes.where(params).and(x => !('SyncError' in  x) || x.SyncError == 0).toArray();
            for(var i in params){
                params[i.toUpperCase()] = params[i];
                delete params[i];
            }
            Brotes = await db.Brotes.where(params).and(x => !('SyncError' in  x) || x.SyncError == 0).toArray();
            Brotes = Brotes.map(x=>_.mapKeys(x, function (v, k) { return _.upperFirst(_.toLower(k)); }))
        }
        Colectivos = [...ColectivosSemana,...ColectivosMes, ...Brotes]
        Colectivos.sort((a, b) => {b.Fec_aju - a.Fec_aju})
        return Colectivos;

    }

}

export default new GridCaracterizacionService();
