
export default {
  name: "DropDownControl",
  props: ["options", "deshabilitar", "tooltip", "mask", "valorCampo","hayError","mensajeError"],
  data() {
    return {
      valorCrudo: this.valorCampo,
    };
  },
  methods: {
    emitirSeleccion() {
      const texto = this.options.filter(x => x.value == this.valor.toString())[0].label;
      this.$emit("seleccion", this.valor,texto,'controlutil');
    },
  },
  computed: {
    valor: {
      get () {
        return this.valorCrudo == null ? "" : this.valorCrudo
      },
      set (val) {
          this.valorCrudo = val        
      }
    }
  },
  watch: {
      valor() {
          this.emitirSeleccion();
      }
  }
};
