
import metodosComunes from "./utilidades/comun/metodosComunes";
import TableSyncControl from "./componentes/utilidad/TableSyncControl.vue"
import VueOnlineOffline from "vue-online-offline";
import ComunProvider from "./providers/ComunProvider";
import sincro from "./utilidades/offline/sincronizacionDex";
import global from "./Config";
import { colors } from "quasar";
export default {
  name: "App",
  async mounted() {
    await this.sincroniza();
  },
  components: { VueOnlineOffline, TableSyncControl },
  data() {
    return {
      leftDrawerOpen: false,
      tieneDiseno: false,
      options: false,
      Nombre: null,
      Entidad: null,
      Rol: null,
      user: {},
      isOfflineCrudo: false,
      isInstalled: global.isInstalled, //display-mode: standalon sirve para saber si la PWA esta instalada o no
      basicosLocal: 0, // número de básicos guardados localmente
      colectivosLocal: 0,
      colectivosSemanaLocal: 0, // número de colectivos semana guardados localmente
      colectivosMesLocal: 0, // número de colectivos mes guardados localmente
      brotesLocal: 0, // número de colectivos 'brotes' guardados localmente
      LaboratoriosLocal: 0, // número de laboratorios guardados localmente
      hayLocalesPendientes: false,
      sincronizando: false,
      version: global.version,
      bdVersion: process.env.VUE_APP_VERSIONBD,
      mostrarModalTablas: false
    };
  },
  methods: {
    recargaSiMismaRuta(rutaActual, rutaNueva) {
      if (rutaNueva == rutaActual) {
        this.$router.go();
      }
    },
    poneDiseno(llevaDiseno) {
      this.tieneDiseno = llevaDiseno;
      this.user = metodosComunes.obtenerUsuario();
    },
    async cerrarSesion() {
      if (global.isInstalled) {
        metodosComunes.cerrarSesion();
      } else {
        window.location.href = global.siv40;
      }
    },
    async isOnline() {
      global.isOnline = true;
      console.log(`Now I'm online!`);
      this.isOfflineComputed = false;
      // sincro.enviarIndividuales();
      colors.setBrand("primary", "#BE0C4D");
      await this.sincroniza();
    },
    isOffline() {
      global.isOnline = false;
      console.log(`Now I'm offline.`);
      this.isOfflineComputed = true;
      colors.setBrand("primary", "#9FA8DA");
    },
    async contarRegistrosLocales() {
      const registros = await ComunProvider.contarRegistrosLocales();
      const registrosConError = await ComunProvider.contarRegistrosLocalesConError(); //8
      if (registros != "Error") {
        this.basicosLocal = registros[0];  //posición 0 básicos
        this.colectivosSemanaLocal = registros[1]; //posición 1 colectivos semana
        this.colectivosMesLocal = registros[2];//posición 2 colectivos mes
        this.brotesLocal = registros[3];//posición 3 brotes
        this.LaboratoriosLocal = registros[4]; //posición 4 laboratorios

      }
      this.hayLocalesPendientes = this.basicosLocal > 0 || this.colectivosSemanaLocal > 0 || this.colectivosMesLocal > 0 || this.brotesLocal > 0 || this.LaboratoriosLocal > 0 ? true : false;
      this.basicosLocal = registros[0] + registrosConError[0];
      this.colectivosLocal = this.colectivosSemanaLocal + this.colectivosMesLocal + this.brotesLocal;
    },
    async sincronizarLocales() {
      this.sincronizando = true;
      await this.contarRegistrosLocales(); //cuenta los registros locales
      if (this.hayLocalesPendientes && !this.isOfflineComputed) { //revisa si está offline y si hay pendientes por sincronizar
        await ComunProvider.sincronizarLocales(); // Sincroniza un registro de cada tabla
        this.sincronizarLocales(); //vuelve a ejecutar la función de sincronización
      } else {
        this.sincronizando = false;
      }
    },
    async sincroniza(notifica) {
      if (!this.sincronizando) {
        await ComunProvider.reiniciarErrores();
        await this.sincronizarLocales();
        if (notifica) notifica("Evento guardado con éxito");
      } else {
        if (notifica) notifica("Evento guardado. Se ha puesto en la cola de sincronización");
      }
    },

    async borrarTablasParametricas() {
      await sincro.borrarTablasParametricas();
      await sincro.sincronizar();
    }
  },
  computed: {
    isOfflineComputed: {
      get() {
        return this.isOfflineCrudo;
      },
      set(val) {
        this.isOfflineCrudo = val;
      },
    },
  },
};
