    retornarPOST(url, params, request) {
        params = params ?? {}
        url = (url.includes("http")) ? url : global.apiUrl + url
        if (url.includes("?")) {
            params = Object.assign(params, this.parseParams(url))
        }
        if(request != undefined){
            return axios.post(url, params, { headers:global.headersToken,cancelToken: request.token })
        }else{
            return axios.post(url, params, { headers:global.headersToken})
        }

    }

    parseParams(querystring) {
        const params = new URLSearchParams(new URL(querystring).search);
        const obj = {};
        for (const key of params.keys()) {
            if (params.getAll(key).length > 1) {
                obj[key] = params.getAll(key);
            } else {
                obj[key] = params.get(key);
            }
        }
        return obj;
    }

