    async contarRegistrosLocales() {
        try{
            const basicos = await db.Eventos.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).count();
            const semana = await db.EventosColectivosSemana.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).count();
            const mes = await db.EventosColectivosMes.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).count();
            const brotes = await db.Brotes.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).count();
            const laboratorios = await db.AnalisisLaboratorio.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).count();
            const cuentaDeRegistros = [basicos,semana, mes, brotes,laboratorios];
            return cuentaDeRegistros;
        }catch(err){
            console.log(err);
            return "Error";
        }
    }

    
