    async sincronizarLocales() {
        try{
            let basico = await db.Eventos.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).first();
            let semana = await db.EventosColectivosSemana.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).first();
            let mes = await db.EventosColectivosMes.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).first();
            let brotes = await db.Brotes.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).first();
            let laboratorios =  await db.AnalisisLaboratorio.toCollection().filter(x => !('SyncError' in  x)).or("SyncError").equals(0).first(); // (pend) sinc laboratorios
            if(basico!=undefined)  await this.sincronizarIndividual(basico);
            if(semana!=undefined)  await this.sincronizarSemana(semana);
            if(mes!=undefined)  await this.sincronizarMes(mes);
            if(brotes!=undefined)  await this.sincronizarBrotes(brotes);
            if(laboratorios!=undefined)   await this.sincronizarLaboratorio(laboratorios);
        }catch(err){
            console.log(err);
            return "Error";
        }
    }    

    async sincronizarIndividual(evento){
        let complementario;
        if(comun.tieneValor(evento.complementario)) complementario = evento.complementario;
        const _id = evento._id;
        try{
            evento.id = evento.id ?? 0;
            if(comun.tieneValor(evento.complementario))delete evento.complementario;
            if(evento._id)delete evento._id;
            if(evento.SyncError)delete evento.SyncError;
            if(evento.SyncErrorMessage)delete evento.SyncErrorMessage;
            if(comun.tieneValor(complementario)){
                complementario.idEvento = !isNaN(evento.id) ? evento.id : null;
                complementario.id = complementario.id ?? 0;
            }
            comun.formatearFechasEventoGuardado(evento,complementario);
            await db.Eventos.update(_id, { SyncError: "InProgress" });
            const respuestaGuardado = await IndividualesProvider.guardarIndividual({evento,complementario}); //sincroniza con el servidor
            const idEvento = respuestaGuardado.data.tipo; //El api responde con el id del evento en el campo "tipo"
            this.completarRelacionEventosLaboratorios(`OF${_id}`, idEvento);
            await db.Eventos.delete(_id); //borra el registro local //1 pasa el borrado al final
        }catch(err){
            evento.SyncError = 1;
            evento.SyncErrorMessage = err.response.data.ExceptionMessage; //2 Agrega campos que se habían borrado para la inserción
            if(comun.tieneValor(complementario)) evento.complementario = complementario;
            evento._id = _id;
            await db.Eventos.put(evento);
            console.log(err.response.data);
        }
    }

    async completarRelacionEventosLaboratorios(idEventoOffline, idEventoServidor){
        //Si existe una relacion de laboratorios con el evento sincronizado completa la relacion con el id del evento del servidor
        const relacionEventosLab = await db.RelacionEventosLaboratorios.where("idEventoOffline").equals(idEventoOffline).first();
        if (relacionEventosLab) {
            await db.RelacionEventosLaboratorios.update(relacionEventosLab._id, { idEventoOnline: idEventoServidor });
        }
    }

    async sincronizarSemana(evento){
        try{
            await db.EventosColectivosSemana.delete(evento._id); //borra el registro local
            evento.id = evento.id ?? 0;
            delete evento._id;
            delete evento.SyncError;
            comun.formatearFechasColectivo(evento,'s');
            await ColectivosProvider.guardarDatosColectivos(evento,'s'); //sincroniza con el servidor
        }catch(err){
            evento.SyncError = 1;
            await db.EventosColectivosSemana.add(evento);
            console.log(err);
        }
    }
