//Maneja la grilla que se presenta en la consulta del formulario
import InputControl from "../../componentes/controles/InputControl";
import SearchForm from "../../componentes/controles/SearchForm";
import DropDownControl from "../../componentes/controles/DropDownControl";
import GridLaboratoriosUIProvider from "../../providers/GridLaboratoriosUIProvider";
import moment from 'moment';
import global from "@/Config";
import sincro from "../../utilidades/offline/sincronizacionDex";
import VueOnlineOffline from "vue-online-offline";
export default {
  name: "GridLaboratoriosUI",
  components: { InputControl, DropDownControl, SearchForm,VueOnlineOffline },
  data() {
    return {
      pagesNumber:0,
      filter: '',
      filtro:null,
      loading: false,
      registrosLocales:0,
      /**
       * Configuración inicial de la grilla
       */
      pagination: {
        descending: false,
        page: 1,
        rowsPerPage: 50,
        rowsNumber: 0,
      },
      columns: [

        { name: 'name', align: 'center', label: '', field: row => row.id, sortable: true },
        { name: 'Cod_pre', align: 'center', label: 'Cod_pre', field: 'Cod_pre', sortable: true },
        { name: 'Cod_sub', align: 'center', label: 'Cod_sub', field: 'Cod_sub', sortable: true },
        { name: 'Ano', align: 'center', label: 'Año', field: 'Ano', sortable: true },
        { name: 'Semana', align: 'center', label: 'Semana', field: 'Semana', sortable: true },
        { name: 'Tip_ide', align: 'center', label: 'Tip ide', field: 'Tip_ide', sortable: true },
        { name: 'Num_ide', align: 'center', label: 'Num ide', field: 'Num_ide', sortable: true },
        { name: 'Pri_nom', align: 'center', label: 'Pri_nom', field: 'Pri_nom', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Seg_nom', align: 'center', label: 'Seg_nom', field: 'Seg_nom', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Pri_ape', align: 'center', label: 'Pri_ape', field: 'Pri_ape', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Seg_ape', align: 'center', label: 'Seg_ape', field: 'Seg_ape', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Cod_eve', align: 'center', label: 'Cod eve', field: 'Cod_eve', sortable: true },

        {
          name: 'Fec_exa', align: 'center', label: 'Fec_exa', field: 'Fec_exa', sortable: true, format: (val) => {
            return (val != null) ? moment(val).format("DD/MM/YYYY") : '';
          }
        },
        {
          name: 'Fec_rec', align: 'center', label: 'Fec_rec', field: 'Fec_rec', sortable: true, format: (val) => {
            return (val != null) ? moment(val).format("DD/MM/YYYY") : '';
          }
        },
        { name: 'Muestra', align: 'center', label: 'Muestra', field: 'Muestra', sortable: false },
        { name: 'Prueba', align: 'center', label: 'Prueba', field: 'Prueba', sortable: false },
        { name: 'Agente', align: 'center', label: 'Agente', field: 'Agente', sortable: false },
        { name: 'Resultado', align: 'center', label: 'Resultado', field: 'Resultado', sortable: false },
        {
          name: 'Fec_exp', align: 'center', label: 'Fecha Resultado', field: 'Fec_exp', sortable: true, format: (val) => {
            return (val != null) ? moment(val).format("DD/MM/YYYY") : '';
          }
        },
        { name: 'Valor', align: 'center', label: 'Valor', field: 'Valor', sortable: false, format: (val) => val.toUpperCase() },
      ],
      data: [],
      original: [

      ],
      vacio: true,
      filas: 0,
      busq: false,
      drawerLeft: false,
    }
  },
  //Momento dentro del ciclo de vida que permite acceder al DOM
 async mounted() {
    this.loading = true;
    this.onRequest({pagination: this.pagination,filter: undefined});
    this.$emit("disenoPagina", true);
    this.registrosLocales = await sincro.enumerarLaboratoriosLocales();
  },
  methods: {
    avanza(){
      this.onRequest({pagination: this.pagination, filter:this.filter, filtro:this.filtro});
    },
    /**
     * Abre el laboratorio con el id dado
     * @param {*} id
     */
    enviar(id) {
 
        let ruta = "LaboratoriosUI/" + id;
        this.$router.push(ruta);
     
  
    },
    /**
     * va para atrás
     */
    atras() {
      //let ruta = "../Individuales/" + this.idEvento;
      this.$router.go(-1);
    },
    onRequest(props) {
      const { page, rowsPerPage} = props.pagination;

      let filtros = {};
      if(props.filter != '' && props.filter != undefined){
        const filtro = this.$refs['buscador'].buscaDesdeBarra(props.filter);
        filtros = {filtros,...filtro};
      }else if(props.filtro != undefined && props.filtro != null){
        filtros = props.filtro;
      }

      // Calcular la fila inicial de datos
      let startRow = (page - 1) * rowsPerPage
      if(startRow < 0)
        startRow = 0
      this.fetchFromServer(startRow, rowsPerPage,filtros,page);
    },

    // Filtra solo por las llaves dadas
    async fetchData(startRow, count, parametros,page){
      await new Promise(resolve => setTimeout(resolve, 100));
      if(global.isOnline){
        this.fetchFromServer(startRow, count, parametros,page);
      }
      else{
        parametros = {startRow, numeroReg:count, ...parametros};
        try{
          this.data = await GridLaboratoriosUIProvider.retornarLaboratoriosLocales();
          this.pagination.rowsNumber = this.data.length > 0 ? this.data.length : 0;
          this.pagination.page = page.toString() == '0' ? 1 : page;
          this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
          this.loading = false;
          this.registrosEnPagina = this.pagination.page*this.pagination.rowsPerPage>this.pagination.rowsNumber ?
              this.pagination.rowsNumber : this.pagination.page*this.pagination.rowsPerPage;
        }catch(err){
          console.log(err);
        }
      }
    },
    fetchFromServer(startRow, count, parametros, page) {
      this.loading = true;
      parametros = {startRow, numeroReg: count, ...parametros};
      GridLaboratoriosUIProvider.retornarLaboratorios(parametros).then(res => {
        this.data = res.data;
        this.pagination.rowsNumber = this.data.length > 0 ? this.data[0].Oportuna : 0;
        console.log("🚀 ~ GridLaboratoriosUIProvider.retornarLaboratorios ~ this.pagination.rowsNumber:", this.pagination.rowsNumber)
        this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
        this.pagination.page = page;
        this.loading = false;
      }, err => {
        if (err.response.status == 401) {
          this.$modal.alertar({
            titulo: "Atención", mensaje: "Sesión vencida, por favor vuelva a ingresar", tipo: 2, onConfirm: () => {
              window.location.href = global.siv40;
            }
          })
        }
      })
    },
    /**
     * Hace el toggle para abrir y cerrar la búsqueda avanzada
     */
    search() {
      this.drawerLeft = !this.drawerLeft;
    },
    /**
     * Busca filas que tengan los valores dados en las llaves dadas
     */
    buscar() {
      this.filter = '';
      this.fetchFromServer(0,50,{limitaRegistros:'0',...this.filtro},1);
      this.loading = false;
    },
    /**
     * Limpia la búsqueda avanzada
     */
    reinicio(){
      this.filtro = '';
      this.onRequest({
        pagination: this.pagination,
        filter: undefined
      })
    },
    // Genera el filtro de valores
    setFiltro(objeto){
      this.filtro = objeto;
    }
  }
}
