import InputControl from "../../componentes/controles/InputControl";
import SearchFormColectivo from "../../componentes/controles/SearchFormColectivo";
import DropDownControl from "../../componentes/controles/DropDownControl";
import GridColectivosProvider from "../../providers/GridColectivosProvider";
import global from "@/Config";
import VueOnlineOffline from "vue-online-offline";
import sincro from "../../utilidades/offline/sincronizacionDex";
import moment from 'moment';



export default {
  name: "GridColectivos",
  components: { InputControl, DropDownControl, SearchFormColectivo,VueOnlineOffline },
  data() {
    return {
      block: false,
      pagesNumber:0,
      filter: '',
      filtro: null,
      loading: false,
      visible:false,
      /**
       * Configuración inicial de la grilla
       */
      pagination: {
        // sortBy: 'Cod_eve',
        descending: false,
        page: 1,
        rowsPerPage: 50,
        rowsNumber: 0
      },
      columns: [

        {
          name: 'name', align: 'center', label: '', field: row => row.idColectivo, sortable: false
        },
        { name: 'Cod_eve', align: 'center', label: 'Evento', field: 'Cod_eve', sortable: true },
        { name: 'Fec_not', align: 'center', label: 'Fecha Notificación', field: 'Fec_not', sortable: true},
        { name: 'Cod_pre', align: 'center', label: 'Código Prestador', field: 'Cod_pre', sortable: true },
        { name: 'Cod_sub', align: 'center', label: 'Subíndice', field: 'Cod_sub', sortable: true },
        { name: 'Ano', align: 'center', label: 'Año', field: 'Ano', sortable: true },
        { name: 'Semana', align: 'center', label: 'Semana/Mes', field: 'Semana', sortable: true },
        { name: 'Municipio', align: 'center', label: 'Municipio', field: 'Municipio', sortable: true },
        { name: 'Num_con', align: 'center', label: 'Consecutivo', field: 'Num_con', sortable: true },
        { name: 'Ajuste', align: 'center', label: 'Ajuste', field: 'Ajuste', sortable: true },
        { name: 'Fec_aju', align: 'center', label: 'Fecha Ajuste', field: 'Fec_aju', sortable: true},
      ],
      data: [],
      original: [

      ],
      filas: 0,
      busq: false,
      drawerLeft: false,
      form: {
        idColectivo: '',
        Cod_eve: '',
        Cod_pre: '',
        Ano: '',
        Semana: '',
        Municipio: '',
      },
      registrosEnPagina:'',
      registrosLocales:0,
    }
  },

  async mounted() {
    this.onRequest({pagination: this.pagination,filter: undefined},"mounted");
    this.$emit("disenoPagina", true);
    this.registrosLocales = await sincro.enumerarColectivosLocales();
  },
  methods: {
    avanza(){
      this.onRequest({pagination: this.pagination, filter:this.filter, filtro:this.filtro},"avanza");
    },
    /**
     * Abre el formulario de colectivos enviando el id y el código seleccionado por el usuario
     * @param {*} idColectivo
     */
    enviar(id, Cod_eve) {
      let ruta = "Colectivos/" + id + "/" + Cod_eve;
      this.$router.push(ruta);
    },
    async onRequest(props, fuente) {
      
      if(!this.loading){
        const { page, rowsPerPage} = props.pagination;

        let filtros = {};
        if(props.filter != '' && props.filter != undefined){
          const filtro = this.$refs['buscador'].buscaDesdeBarra(props.filter);
          filtros = {filtros,...filtro};
        }else if(props.filtro != undefined && props.filtro != null){
          filtros = props.filtro;
        }

        // calculate starting row of data
        let startRow = (page - 1) * rowsPerPage
        if(startRow < 0)
          startRow = 0
        await this.fetchData(startRow, rowsPerPage,filtros,page);
      }
    },

    async fetchData(startRow, count, parametros,page){
      //await this.sincronizarEventos();
      await new Promise(resolve => setTimeout(resolve, 100));
      if(global.isOnline){
        this.fetchFromServer(startRow, count, parametros,page);
      }
      else{
        parametros = {startRow, numeroReg:count, ...parametros};
        try{
          this.data = await GridColectivosProvider.retornarColectivosLocales(parametros);
          this.data = this.data.map((x)=>{
            var Fec_aju = new Date(x.Fec_aju);
            x.Fec_aju =  moment(Fec_aju).format("DD/MM/YYYY");

            var Fec_not = new Date(x.Fec_not);
            x.Fec_not =  moment(Fec_not).format("DD/MM/YYYY");
            return x;
          })
          this.pagination.rowsNumber = this.data.length > 0 ? this.data.length : 0;
          this.pagination.page = page.toString() == '0' ? 1 : page;
          this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
          this.loading = false;
          this.registrosEnPagina = this.pagination.page*this.pagination.rowsPerPage>this.pagination.rowsNumber ?
              this.pagination.rowsNumber : this.pagination.page*this.pagination.rowsPerPage;
        }catch(err){
          console.log(err);
        }
      }
    },

    // Filtra solo por las llaves dadas
    fetchFromServer(startRow, count, parametros, page, fuente) {
      this.loading = true;

      if(fuente != "avanza")
      this.visible = true;
      // this.$q.loading.show({
      //   message: 'Recopilando todos los colectivos',
      //   customClass: 'opacoColect'
      // });

      parametros = {startRow, numeroReg: count, ...parametros};
      GridColectivosProvider.retornarColectivos(parametros).then(res => {
        let data = res.data;
        this.data = data;
        console.log(this.data);
        // this.$q.loading.hide();
        this.visible = false;
        // setTimeout(() => {
          this.loading = false
          // this.$q.loading.hide();
          this.visible = false;
          this.pagination.rowsNumber = this.data.length > 0 ? this.data[0].Oportuna : 0;
          this.pagination.page = page.toString() == '0' ? 1 : page;
          this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
          this.registrosEnPagina = this.pagination.page*this.pagination.rowsPerPage>this.pagination.rowsNumber ? 
                                 this.pagination.rowsNumber : this.pagination.page*this.pagination.rowsPerPage;
        // }, 300)
      }, err => {
        this.visible = false;
        if (err.response.status == 401) {
          this.$modal.alertar({
            titulo: "Atención", mensaje: "Sesión vencida, por favor vuelva a ingresar", tipo: 2, onConfirm: () => {
              window.location.href = global.siv40;
            }
          })
        }
      })
    },

    // Cuenta el número de filas
    getRowsNumberCount(filter) {
      if (!filter) {
        this.filas = this.original.length
      }
    },
    /**
    * Hace el toggle para abrir y cerrar la búsqueda avanzada
    */
    search() {
      this.drawerLeft = !this.drawerLeft;
    },
    /**
     * Busca filas que tengan los valores dados en las llaves dadas
     */
    buscar() {
      this.filter = '';
      this.fetchData(0,50,{limitaRegistros:'0',...this.filtro},1);
      this.loading = false;
      this.$q.loading.hide()
      this.visible = false;
    },
    /**
     * Limpia la búsqueda avanzada
     */
    reinicio(){
      this.filtro = '';
      this.onRequest({
        pagination: this.pagination,
        filter: undefined
      },"reinicio")
    },
    setFiltro(objeto){
      this.filtro = objeto;
    },

    async sincronizarEventos(){
      this.sincronizando = true;
      await sincro.enviarColectivos();
      this.sincronizando = false;
      this.registrosLocales = await sincro.enumerarColectivosLocales();
    }
  }
}
