import axios from "axios";
import global from "../Config";
let headers = {
    "Content-Type": "application/json"
}

// 'Authorization': 'Bearer ' + token

class CaracterizacionServicio {
    /**
     * Método que lista los códigos prestador para saber si ya se encuentra en el sistema
     * @param {*} params 
     * @returns 
     */
    retornarListaUpgds(params) {
        return axios.post(global.apiUrl + "Upgds/retornarCodPrestador", params, { headers: global.headersToken })
    }
    /**
     * Busca el laboratorio dado por parámetro
     * @param {*} params 
     */
    obtenerUpgdPorId(params) {
        return axios.post(global.apiUrl + "comunCaptura/obtenerUpgdPorID", params, { headers })
    }
    /**
     * Busca una upgd mediante el codigo (codpre + codsub)
     * @param {*} params 
     */
    obtenerUpgdPorCodigo(params) {
        return axios.post(global.apiUrl + "upgds/obtenerUpgdPorCodigo", params, { headers: global.headersToken })
    }
    /**
     * Método para validar tanto si existe como si no el código prestador y el código sub
     * @param {*} params 
     */
    consultarCodigo(params) {
        return axios.post(global.apiUrl + "Individuales/consultarCodigoUpgds", params, { headers })
    }
    /**
     * Procedimiento que permite el guardar un registro nuevo de caracterización UPGDS en la base de datos
     * @param {*} params 
     */
    guardarCaracterizacion(params) {
        return axios.post(global.apiUrl + "Individuales/guardarCaracterizacion", params, { headers })
    }
    /**
     * Método que consulta si el upgd existe e ingresa la información en la consulta
     * @param {*} params 
     */
    consultarCodigoPrestador(params) {
        return axios.post(global.apiUrl + "Individuales/retornarCodPrestador", params, { headers })
    }
    /**
     * Método que consulta la información de las Ucis asociadas a una Upgd
     * @param {*} params 
     */
    consultarUcis(params) {
        return axios.post(global.apiUrl + "Individuales/consultarUcis", params, { headers })
    }
    /**
     * Método que guarda/modifica una Uci en la base de datos
     */
    guardarCaracterizacionUci(params) {
        return axios.post(global.apiUrl + "Individuales/guardarCaracterizacionUCI", params, { headers })
    }
    /**
     * Método que carga la lista de upgds en el modal de búsqueda
     * @param {*} params 
     */
    consultarUpgdsParametros(params) {
        return axios.post(global.apiUrl + "Individuales/retornarUpgdsParametros", params, { headers })
    }
    /**
     * Verifica cuantas camas existen en la upgd
     * @param {*} params 
     * @returns 
     */
    consultarCamasCaracterizadas(params) {
        return axios.post(global.apiUrl + "upgds/retornaCamasUpgdsCaracterizadas", params, { headers })
    }
    /**
     * Método para verificar la cantidad de camas uci
     * @param {*} params 
     * @returns 
     */
    consultarCamasCaracterizadasUci(params) {
        return axios.post(global.apiUrl + "upgds/retornaCamasUciCaracterizadas", params, { headers })
    }
    /**
     * Verifica si la uci a modificar se encontraba en estado desactiva para sumar este valor a la sumatoria
     * total y evitar que se pase al número permitido
     * @param {*} params 
     * @returns 
     */
    consultarAjusteAnteriorUci(params) {
        return axios.post(global.apiUrl + "upgds/retornaAjusteAnteriorUci", params, { headers })
    }
    /**
     * Valida la cantidad de casos nuevos en colectivos con base a la cantidad de camas
     * @param {*} params 
     * @returns 
     */
    consultarTotalCasosNuevos(params) {
        console.log(params);
        return axios.post(global.apiUrl + "upgds/retornarCasosNuevos", params, { headers })
    }

    
    /**
     * Cálculos para el caso colectivo
     * @param {*} params 
     * @returns 
     */
    consultarDenominador(params) {
        return axios.post(global.apiUrl + "upgds/RetornarDenominador", params, { headers })
    }
    /**
     * Permite el ingreso de la información de servicios y talento humano de una upgd
     * @param {*} params 
     * @returns 
     */
    guardarTalentoHumano(params) {
        return axios.post(global.apiUrl + "upgds/guardarTalento", params, { headers })
    }
}

export default new CaracterizacionServicio();
