import axios from "axios";
import global from "../Config";
import { db } from "../utilidades/offline/dbModule";

// let headers =  { 
//     "Content-Type":"application/json"
// }


// 'Authorization': 'Bearer ' + token

class GridIndividualesService {

    retornarIndividuales(params) {
        return axios.post(global.apiUrl + "Individuales/ListarBasicos", params, { headers: global.headersToken })
    }

    async enumerarEventosLocales() {
        const individuales = await db.Eventos.toArray(); // consulta eventos sin sincronizar
        return individuales.length;
    }
    async retornarIndividualesLocales(parametros) {
        let params = JSON.parse(JSON.stringify(parametros, function (a, b) {
            return typeof b === "string" ? b.toUpperCase() : b
        }));
        const docs = await db.Eventos.filter(x => {
            return (x.Num_ide.includes(params.Num_ide) || ((params.Num_ide || '') == '') &&
            (x.Pri_nom.includes(params.Pri_nom) || ((params.Pri_nom || '') == '')) &&
            (x.Seg_nom.includes(params.Seg_nom) || ((params.Seg_nom || '') == '')) &&
            (x.Pri_ape.includes(params.Pri_ape) || ((params.Pri_ape || '') == '')) &&
            (x.Seg_ape.includes(params.Seg_ape) || ((params.Seg_ape || '') == '')) &&
            (x.Cod_eve == params.Cod_eve || ((params.Cod_eve || '') == '')) &&
            (x.Cod_pre == params.Cod_pre || ((params.Cod_pre || '') == '')) &&
            (x.Cod_sub == params.Cod_sub || ((params.Cod_sub || '') == '')) &&
            (x.Año == params.Ano || ((params.Ano || '') == '')) &&
            (x.Semana == params.Semana || ((params.Semana || '') == '')) &&
            (x.Nom_dil_fi.includes(params.Nom_dil_fi) || ((params.Nom_dil_fi || '') == '')) &&
            ((x.Cod_dpto_o + x.Cod_mun_o) == (params?.Cod_mun_o) || ((params.Cod_mun_o || '') == '')) &&
            ((x.Cod_dpto_r + x.Cod_mun_r) == (params?.Cod_mun_r) || ((params.Cod_mun_r || '') == '')))
        }).toArray();
        return docs;
    }

    async retornarIndividualesSyncError() {
        const docs = await db.Eventos.where("SyncError").equals(1).toArray();
        return docs;
    }
}

export default new GridIndividualesService();
