
import LookUpTableControlN from "../controles/LookUpTableControlN";
import global from "../../Config";
import moment from 'moment'
export default {
  /**
   * Este componente está aparte porque la búsqueda avanzada de Colectivos es más sencilla
   */
  name: "SearchFormColectivo",
  components: { LookUpTableControlN },
  data() {
    return {
      form:{
        Cod_eve: "",
        Cod_pre: "",
        Cod_sub: "",
        Ano: "",
        Semana: "",
        Municipio: "",
      },
      link: global.apiUrl + "Individuales/retornarUpgds",
      linkeventos: global.apiUrl + "colectivos/retornarListaEventosColectivos",
      linkmunicipios: global.apiUrl + "Individuales/retornarMunicipios",
      Cod_eve: "",
      Cod_pre: "",
      Ano: "",
      Semana: "",
      Municipio: "",
      Mes: "",
      llaves: ["Cod_eve", "Cod_pre", "Ano", "Mes", "Semana", "Municipio"],
      componentKey2: 100, //la key se usa para re-renderizar el componente y que se limpie cuando se presione limpiar,
      validations: {
        ano: false,
        mes: false,
        semana: false,
      },
      block_mes: false,
      block_semana: false
    };
  },
  mounted() {},
  methods: {
    /**
     * Obtiene el municipio de la tabla de lookup
     */
    obtenerMunicipio(valor) {
      this.Municipio = valor;
    },
    /**
     * Crea un objeto con los campos que no estén vacíos y lo emite al evento "busqueda"
     */
    buscar() {
      let objeto = {};
      let contador = 0;
      for (const key of this.llaves) {
        if (this[key] != "") {
          objeto[key] = this[key];
          contador += 1;
        }
      }

      if(objeto.Cod_pre != '' && objeto.Cod_pre != null){
        objeto.Cod_sub = objeto.Cod_pre.slice(10, 12);
        objeto.Cod_pre = objeto.Cod_pre.slice(0, 10);
      }
      if (contador > 0) {
        this.$emit("setFiltro", objeto);
        this.$emit("busqueda");
      }
    },
    buscaDesdeBarra(valorString) {
      let objeto = {};
      for (const key of this.llaves) {
        if(key != "Mes") objeto[key] = valorString;
      }

      objeto.limitaRegistros= '1';
      return objeto;
    },
    /**
     * Limpia los campos y emite reinicio para ser manejado por la grilla
     */
    reiniciar() {
      for (const key of this.llaves) {
        this[key] = "";
      }
      this.componentKey2 += 1;
      this.$emit("reinicio");
    },
    /**
     * Cierra el componente
     */
    cerrar() {
      this.$emit("cerrar");
    },
    obtenerEvento(valor) {
      this.Cod_eve = valor;
      const arr_mes = ['349', '621', '650', '652', '830', '901', '994', '995', '998']
      const arr_semana = ['354', '359', '362'];
      if(valor.length > 2) {
        if(arr_mes.includes(valor)){
          this.block_mes = true
        }
        if(arr_semana.includes(valor)){
          this.block_semana = true
        }
      } else {
        this.block_mes = false
        this.block_semana = false
      }
    },
    obtenerUpgd(valor) {
      this.Cod_pre = valor;
    },
    validar_campo(campo){
      if(campo == 'Ano') {
        const now = Date.now()
        if(this.Ano != undefined && this.Ano.length> 3 && moment(this.Ano).format('YYYY') > moment(now).format('YYYY')){
          this.validations.ano = true
        } else {
          this.validations.ano = false
        }
      }
      else if(campo == 'Mes') {
        if(this.Mes != undefined && parseInt(this.Mes) > 12 || parseInt(this.Mes) < 1){
          this.validations.mes = true
        } else {
          this.validations.mes = false
        }
      }
      else if(campo == 'Semana') {
        if(this.Semana != undefined && parseInt(this.Semana) > 53 || parseInt(this.Semana) < 1){
          this.validations.semana = true
        } else {
          this.validations.semana = false
        }
      }
    }
  },
  computed: {},
};
