
import moment from 'moment';
import { db } from '../../utilidades/offline/dbModule';
import sincroDex from '../../utilidades/offline/sincronizacionDex';
export default {
    name: "TableSyncControl",
    props: { value: { type: Boolean, required: true } },
    data() {
        return {
            filter: '',
            tablas: [],
            columnas: [
                { align: 'left', name: 'nombre', label: 'Tabla', field: 'nombre', sortable: true },
                { align: 'left', name: 'fechaActualizacion', label: 'Ultima sincronización', field: 'fechaActualizacion', sortable: true, format: val => `${moment(val).format('DD/MM/YYYY hh:mm a')}`, },
                { name: 'name', align: 'center', label: '', field: row => row.nombre, sortable: false },
            ],
            pagination: {
                page: 1,
                rowsPerPage: 0
            },
        };
    },
    created() {
        this.cargarInfoTablas();
    },
    computed: {
        localOpenModal: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit("input", value);
            }
        }
    },
    methods: {
        async cargarInfoTablas() {
            const data = await db.Sincronizacion.toArray();
            // Initialize the iconState property for each row
            this.tablas = data.map((row) => ({
                ...row,
                iconState: 'fas fa-sync',
                disableButton: false,
            }));
        },
        emitirCerrado() {
            this.$emit("input", false);
        },
        async resincronizar(tabla) {
            tabla.iconState = 'fas fa-sync fa-spin';
            tabla.disableButton = true;
            try {
                await this.resincronizarTabla(tabla.nombre);
                tabla.fechaActualizacion = moment();
                tabla.iconState = 'fas fa-check';
                await new Promise(r => setTimeout(r, 2000));
                await this.cargarInfoTablas();
            } catch (error) {
                console.log(error);
            } finally {
                tabla.iconState = 'fas fa-sync';
                tabla.disableButton = false;
            }
        },
        async resincronizarTabla(nombreTabla) {
            await sincroDex.borrarTabla(nombreTabla);
            await sincroDex.sincronizarTablas([{ nombre: nombreTabla, fechaActualizacion: new Date('1990-01-01T00:00:00') }], false);
        },
        async resincronizarTodasLasTablas() {
            await sincroDex.borrarTablasParametricas();
            await sincroDex.sincronizar();
            await this.cargarInfoTablas();
        }
    }
};
