
export default {
  name: "TextoControl",
  props: ["deshabilitar", "tooltip", "max", "mask", "fillmask","valorCampo","hayError","mensajeError","valoresAdmitidos","longitud","esTabla","clase"],
  data() {
    
    return {
      valorCrudo: this.valorCampo,
      claseTabla: this.esTabla && /\(([^)]*)\)/.exec(this.clase) ? /\(([^)]*)\)/.exec(this.clase)[1] : ""
    };
  },
  methods: {
    emitirSeleccion() {
      this.$emit("seleccion", this.valor.replace(eval(this.valoresAdmitidos),''));
    },
    emitirComportamiento() {
      this.valor = this.valor.replace(eval(this.valoresAdmitidos),'').toUpperCase();
      this.emitirSeleccion();
      this.$emit("blur", this.valor.replace(eval(this.valoresAdmitidos),'').toUpperCase());
    },
    regexcheck(){
      this.valor = this.valor.replace(eval(this.valoresAdmitidos),'');
    },
    ponerFoco(){
      setTimeout(() => {
        this.$refs.inputT.$el.focus();  
      }, 100);
    }
  },
  computed: {
    valor: {
      get () {
        return this.valorCrudo == null ? "" : this.valorCrudo;
      },
      set (val) {
        this.valorCrudo = val;
      }
    }
  }
};
