import ComunProvider from '../../../providers/ComunProvider';
import Comun from '../../comun/metodosComunes' 
export default{
    /**
     * 
     * @param {*} valor 
     * @param {*} inputControl 
     * @param {*} control 
     * @param {valor que indica si el formulario ha sido cargado o si ya se ha modificado} complementariosCargadosAntes 
     * @param {valor del campo dependiente} valorDependencia 
     * Funcion que precarga el valor de un checkbox de solo utilidad (no de metadata) dependiendo de si otros campos se encuentran llenos o no
     * está función solo se ejecuta una vez al cargar el formulario
     */
    // eslint-disable-next-line no-unused-vars
    checkearSiDependenciaNoNull: function(valor,inputControl,control,complementariosCargadosAntes,valorDependencia,_campoAnteriorDisparador){
        if(!complementariosCargadosAntes){
            inputControl.valor = Comun.tieneValor(valorDependencia) ? "1" : "2"
        }
    },
    /**
     * 
     * @param {valor del input} valor 
     * @param {referencia a inputcontrol} inputControl 
     * @param {referencia a control} control 
     * @param {*} valorConsulta 
     */
    
    cambiaValorLookCondicionalDependienteParametro2: function(valor,inputControl,control,valorCampoDependiente,valorEsperadoDependiente,valorParametro,_adicional){// eslint-disable-line no-unused-vars
        if(valorCampoDependiente != valorEsperadoDependiente){
            const parametrosConsulta = {Param2:valorParametro}
            inputControl.valor = inputControl.valorPorDefecto ?? "";
            control.CargarOpciones(parametrosConsulta);
        }
    },

    cambiaValorLookCondicionalDependienteParametro2Igual: function(valor,inputControl,control,valorCampoDependiente,valorEsperadoDependiente,valorParametro1,valorParametro2,_adicional){// eslint-disable-line no-unused-vars
        if(valorCampoDependiente == valorEsperadoDependiente && _adicional[0] && !_adicional[3].cambioTab){
            const parametrosConsulta = {Param1:valorParametro1,Param2:valorParametro2}
            inputControl.valor = inputControl.valorPorDefecto ?? "";
            control.CargarOpciones(parametrosConsulta);
        }
    },

    establecerPaisDeDepartamento: function (valor, inputControl, control, municipio, adicionales) {
        if (!adicionales[0] && municipio != "") {
            ComunProvider.obtenerPaisPorDepartamento({cod_mun:municipio}).then(res => {
                inputControl.encadenaAfectantes = false;
                inputControl.valor = res.data;
            })
            // const parametrosConsulta = { Param1: valorConsulta }
            // inputControl.valor = inputControl.valorPorDefecto ?? "";
            // control.CargarOpciones(parametrosConsulta);
        }
    },

    establecerDepartamentoPorMunic: function (valor, inputControl, control, municipio, adicionales) {
        if (!adicionales[0] && municipio != "") {
            ComunProvider.retornarEsDistrito({cod_mun:municipio}).then(res => {
                inputControl.encadenaAfectantes = false;
                inputControl.valor = res.data == 1 ? municipio : municipio.substring(0, 2);
            })
            // const parametrosConsulta = { Param1: valorConsulta }
            // inputControl.valor = inputControl.valorPorDefecto ?? "";
            // control.CargarOpciones(parametrosConsulta);
        }
    },
    establecerPaisDeDepartamentoColect: function (valor, inputControl, control, municipio) {
        if (municipio != "") {
            ComunProvider.obtenerPaisPorDepartamento({cod_mun:municipio}).then(res => {
                inputControl.encadenaAfectantes = false;
                inputControl.valor = res.data;
            })
            // const parametrosConsulta = { Param1: valorConsulta }
            // inputControl.valor = inputControl.valorPorDefecto ?? "";
            // control.CargarOpciones(parametrosConsulta);
        }
    },
    alertarYCorregirCampoSiMayorA: function (valor, inputControl, control,campoActivador, limite,titulo,mensaje,adicionales) {
        if(adicionales[0] && !adicionales[3].cambioTab){
            if(parseFloat(valor)>parseFloat(limite)){
                mensaje = mensaje.replace('|',valor);
                inputControl.mostrarAlerta(titulo,mensaje,3,inputControl.ponerFocoYLimpiar,true,()=>{},'Si','No');
            }
        }
    },

    estableceValorLookPorValorDependiente: function (valor, inputControl, control, dependencia,adicionales) {
        if (dependencia != "" && !adicionales[0] ) {
                ComunProvider.obtenerBarrioPorNombre({nombreBarrio:dependencia}).then(res => {
                    inputControl.encadenaAfectantes = false;
                    inputControl.valor = res.data;
                })
        }
    },
}
