/**
 * Valida si el año actual es menor o igual al actual
 * @param {*} value año que se quiere revisar
 */
const validYear = (value) => {
  // Usa la validacion dinamica de basicos para validar el año
  if (value === '') return true // Si el campo está vacío genera un error por dato obligatorio
  else if (value.length < 4) return true // Valida que el año tenga los 4 campos requeridos
  else {
    return new Promise((resolve,) => { // Llama a la función anoPeriodoActual que se encuentra en fechasValida
      // para validar que la fecha seleccionada corresponda a la vigencia o al año inmediatamente anterior
      FechasValida.anoPeriodoActual("", value).then(res => {
        resolve(res.valido);
      });
    })
  }
};
// Método que valida que la semana seleccionada corresponda al calendario epidemiológico
const semanaValidaAnoEpidemiologico = (value, vm) => {
  // Usa la validacion dinamica de basicos para validar el año
  if (value === '') return true // Sí la fecha es vacía error
  else if (value.length < 2) return true // Sí la fecha no tiene los cuatro digitos error
  else {
    return new Promise((resolve,) => { // Realiza la validación llamando al método de fechasValidas
      FechasValida.validarSemanaCalendarioEpidemiologico("", value, vm.Ano).then(res => {
        resolve(res.valido);
      });
    })
  }
};

// Método que consulta la cantidad de caracteres que debe tener una cadena de acuerdo al tipo de documento
let maxNumideLenght;
const maxNumide = (value, vm) => {
  maxNumideLenght = (tiposIdentidad[vm.Tip_ide]?.longitud ?? 0)
  return value.length <= maxNumideLenght;
}

// Método que consulta la tabla Calendario para determinar si corresponde a la semana correcta
const semanaValidaFechaNot = (value, vm) => {
  const params = { fecha: vm.Fec_aju }
  if (value === '') return true // Fecha vacía error
  else {
    return new Promise((resolve,) => {
      if (parseInt(vm.Ano) > parseInt(moment(vm.Fec_aju, 'DD/MM/YYYY').format("YYYY")))
        resolve(false); // Si el año digitado es mayor a la fecha ajuste
      else if (parseInt(vm.Ano) == parseInt(moment(vm.Fec_aju, 'DD/MM/YYYY').format("YYYY"))) {
        ComunProvider.obtenerFechaEpidemiologica(params).then(res => {
          // Verifica sí la semana es menor o igual a la ingresada de acuerdo a la fecha de ajuste
          resolve(res.data != null && parseInt(value) <= parseInt(res.data['semana']))
        });
      }
      else
        resolve(true);
    });
  }
};

/**
 * Valida que la fecha dada por parámetro sea menor a la del servidor
 * @param {*} value fecha a revisar
 */
const beforeCurrentYear = (value) => {
  if (value === '') return true // Sí fecha vacía error por dato obligatorio
  else if (value.length < 10) return true // Si fecha en formato incorrecto error
  else {

    // Simular llamada asíncrona, falla para todos los inicios de sesión con longitud uniforme
    return new Promise((resolve,) => {
      // Valida la fecha en la función que se encuentra en fechasValida, como parámetros envía cadena vacía de mensaje, fecha
      FechasValida.fechaMenorActual("", value)
        .then(
          (val) => {
            resolve(val.valido);
          })
        .catch((val) => {
          resolve(false);
        });

    })
  }

};


/**
 * Si el tipo de aseguradora es N o I hace que esta no sea requerida. De lo contrario, se revisa que no esté vacía la aseguradora.
 * @param {*} param 
 */
// const requiredNegateParams = (param) => {
//     return (param === 'N' || param === 'I')
//   };


/**
 * Revisa que la fecha dada por parámetro sea una fecha. Por ejemplo 88/88/8888 no es una fecha válida.
 * @param {*} value 
 */
const isDate = (value) => {
  if (value === '' || value === "Invalid date") return true
  else if (value.length < 10) return true
  else {
    return moment(value, 'DD/MM/YYYY').isValid();
  }
}

/**
 * Revisa que la fecha en value esté después de la fecha en param
 * @param {*} param 
 */
const afterDate = (param) =>
  (value) => {
    if (value === '' || value === "Invalid date" || param === '' || param === "Invalid date") return true
    else if (value.length < 10) return true
    else {
      if (param === "") {
        return false;
      }
      else {
        // simular llamada asíncrona para verificar que la fecha sea mayor o igual en los campos de fecha
        // toma examen y recpeción
        return new Promise((resolve,) => {
          FechasValida.fechaMayorIgual("", value, param)
            .then(
              (val) => {
                resolve(val.valido);
              })
            .catch((val) => {
              resolve(false);
            });

        })
      }
    }
  };

/**
 * Si el resultado es 0, 3 o 4 no se requiere fecha de resultado ni valor.
 * @param {*} param 
 */
const resultRequired = (param, cod_eve) =>
  (value) => {
    if (param === '0' || param === '3' || param === '4' || cod_eve == "352") {
      return true;
    }
    else {
      return !!value;
    }
  };

/**
 * Hace obligatorio el campo value solo si el param es true
 * @param {*} param 
 */
const dependentRequired = (param) =>
  (value) => {
    if (param === false) { //si no es requerido entonces pasa
      return true;
    }
    else {
      return !!value;
    }
  };


