
import validacionesLib from '../libreriaValidaciones/validacionesLib';
const {IconValida} = validacionesLib;

// configura la validación dado un objeto de validación
function establecerComportamiento(validacion){
    let valorInput = validacion.valor;
    let parametros = validacion.parametros ?? [];
    parametros = [validacion.mensaje,valorInput,...parametros]
    let nombre = validacion.nombre;
    return{
        parametros,nombre
    }
}

export default {

    //ejecuta las validaciones de un campo segun un arreglo de validaciones entregado
    ejecutarValidaciones: function(validaciones){
            let retorno = {
                valido:true,
                mensaje:""
            };
        return new Promise((resolve) => {
            let promesasDeValidacion = [];
            validaciones.validacionesArreglo.map((x)=>{
                x.valor = validaciones.valor;
                let {parametros,nombre} = establecerComportamiento(x);
                try{
                    promesasDeValidacion.push(IconValida[nombre](...parametros).catch(e => e));
                }catch(err){
                    console.log(err);
                    console.log(parametros);
                    console.log(nombre);
                }
            })
            Promise.all(promesasDeValidacion).then(result => {
                result.map((resultadoGenerado)=>{
                    if(retorno.valido) retorno = resultadoGenerado.valido ? retorno : resultadoGenerado;
                })
                resolve(retorno);
            },err => console.log(err))
        })
    },
    ejecutarValidacion: function(validaciones,validacion){ //fuente
        let retorno = {
            valido:true,
            mensaje:""
        };
        return new Promise((resolve) => {
            try{
                const v = validaciones.validacionesArreglo.filter(f => f.nombre == validacion)[0];
                if(v){
                    v.valor = validaciones.valor;
                    let {parametros,nombre} = establecerComportamiento(v);
                    //console.log(nombre);
                    IconValida[nombre](...parametros).then(result => {
                        retorno = result.valido ? retorno : result;
                        return resolve(retorno);
                    })
                }
            }catch(err){
                console.log(err);
            }
        });
    }       
}
