import axios from "axios";
import global from "../Config";

class GridContactosService {

    /**
     * Esta función retorna la lista de todos los contactos.
     * @param {} params
     */
    retornarContactos(params) {
        return axios.post(global.apiUrl + "contactos/listarContactos",params,{headers:global.headersToken})
    }


}

export default new GridContactosService();
