import Vue from 'vue';
import Router from 'vue-router';
import Individuales from '../paginas/Individuales';
import Inicio from '../paginas/Inicio';
import Caracterizacion from '../paginas/Caracterizacion';
import Laboratorios from '../paginas/Laboratorios';
import LaboratoriosCB from '../paginas/LaboratoriosCB';
import Login from '../paginas/Login';
import GridIndividuales from '../paginas/GridIndividuales';
import GridLaboratorios from '../paginas/GridLaboratorios';
import GridLaboratoriosUI from '../paginas/GridLaboratoriosUI';
import GridCaracterizacion from '../paginas/GridCaracterizacion';
import Colectivos from '../paginas/Colectivos';
import GridColectivos from '../paginas/GridColectivos';
import Contactos from "../paginas/Contactos";
import Seguimientos from "../paginas/Contactos/Seguimiento";
import GridContactos from "../paginas/GridContactos";
// './' './paginas/Individuales'

Vue.use(Router)

const routes = [
    { 
        path: '/Inicio', 
        name: 'Inicio',
        component: Inicio
    },
    { 
        path: '/Individuales/:idEvento', 
        name: 'Individuales',
        component: Individuales,
        props: true
    },
    {
        path: '/Login', 
        name:'Login', 
        component: Login
    },
    {
        path: '/LaboratoriosUI/:idLaboratorio', 
        name:'laboratorios', 
        component: Laboratorios,
        props: true
    },
    {
        path: '/Laboratorios/:idEventoProp/:idLaboratorio', 
        name:'laboratoriosCB', 
        component: LaboratoriosCB,
        props: true
    },
    {
        path: '/Laboratorios/:idEvento', 
        name:'gridLaboratorios', 
        component: GridLaboratorios,
        props: true
    },
    {
        path: '/Individuales', 
        name:'gridindividuales', 
        component: GridIndividuales
    },
    {
        path: '/Caracterizacion', 
        name:'gridCaracterizacion', 
        component: GridCaracterizacion
    },
    {
        path: '/Caracterizacion/:idEntidad', 
        name:'caracterizacion', 
        component: Caracterizacion,
        props: true
    },
    {
        path: '/LaboratoriosUI', 
        name:'gridLaboratoriosUI', 
        component: GridLaboratoriosUI
    },
    {
        path: '/Colectivos/:idEvento/:cod_evePre', 
        name:'colectivos', 
        component: Colectivos,
        props: true
    },
    {
        path: '/Colectivos', 
        name:'gridColectivos', 
        component: GridColectivos
    },
    {
        path: '/Contactos/Seguimientos',
        name:'seguimientos',
        props: true,
        component: Seguimientos
    },
    {
        path: '/Contactos/:idContacto',
        name:'contactos',
        component: Contactos,
        props: true
    },
    {
        path: '/Contactos',
        name:'gridContactos',
        component: GridContactos
    },
  ]
  

export default new Router({
    base: process.env.VUE_APP_BASEURL,//"/CapturaEnLineaPruebas/",  //'P' ? "/" : "/testVocacional/", global.amb == "/testVocacional/"
    routes,
    mode: 'history'
  });