import InputControl from "../../componentes/controles/InputControl";
import DropDownControl from "../../componentes/utilidad/DropDownControlUtil";
import individualesProvider from "../../providers/IndividualesProvider";
import ComunProvider from "../../providers/ComunProvider";
import MetodosComunes from "../../utilidades/comun/metodosComunes";
import * as moment from "moment/moment";
import ColectivosProvider from "../../providers/ColectivosProvider";
import LaboratoriosProvider from "../../providers/LaboratoriosProvider";
import global from "../../Config";

export default {
  name: "Colectivos",
  components: { InputControl, DropDownControl },
  props: ['idEvento','cod_evePre'],
  data() {
    return {
      // capturaVariables: {},
      guardando: false,
      inactivar: false,
      ajustar: (this.idEvento == 0 || this.idEvento.startsWith("OF")),
      capturaVariablesComp: {},
      capturaVariablesCompTipos:[],
      listaEventos: null,
      metaDatos: [],
      metaDatosComp: [],
      tablas: [],
      tab: "basicos",
      load: false,
      loadComp:false,
      alert: false,
      msg: null,
      envio: false,
      cod_eve: this.cod_evePre != 0 ? this.cod_evePre: null,
      cod_sub:null,
      tipoColectivo:'',
      eventoPermitido: true,
      tieneLabs:false,
      eventoCrudo: this.idEvento,
      camposComplementarios:[],
      puedeEjecutarAfectantes: true,
      camposFechaFormatear:[],
      variablesFormulario:{
        cod_eve: null,
        complementariosCargados: false,
        IndividualesCargados: false,
        ajustar: (this.idEvento == 0 || this.idEvento.startsWith("OF")),
        idEvento:this.idEvento,
        cambioTab: this.idEvento != 0 ? true : false,
        colectivo:true,
        esNuevo: this.idEvento == 0 ? true : false,
      },
      bloqueaAjuste:null,
      textosListas:{}
    };
  },
  computed: {
    evento: {
      get () {
        return this.eventoCrudo
      },
      set (val) {
          this.eventoCrudo = val        
      }
    }
  },
  methods: {
    //Método que pasa los componentes de padre a hijo para que se puedan 
    //transmitir los métodos dinámicamente, ejemplo reload
    emitirMetodo(metodo, ...Paremetros){
      this[metodo](...Paremetros);
    },
    AjustarColectivo(ajuste){
      if (this.variablesFormulario.complementariosCargados) {
        this.variablesFormulario.complementariosCargados = false;
        if (ajuste.toString() == '7' && this.ajustar) {
            Object.keys(this.$refs).forEach(element => {
              if (element != 'cod_eve' && element != 'ajuste' && this.$refs[element][0] != null && !this.$refs[element][0].deshabilitadoObligatorio) {
                this.$refs[element][0].deshabilitar = false
              }
            });
            ComunProvider.obtenerFechaActual().then(res => {
              this.capturaVariablesComp.fec_aju = moment(res.data).format("YYYY-MM-DD");
            })

            var campos = Object.keys(this.capturaVariablesComp);
            this.ejecutarAfectantes(campos).then(() => {
              this.variablesFormulario.complementariosCargados = true;
              this.$refs.fec_aju[0].deshabilitar = true;
              this.$refs.cod_pre[0].deshabilitar = true;
              this.$refs.fec_not[0].deshabilitar = true;
              if(this.$refs.semana)
              this.$refs.semana[0].deshabilitar = true;
              if(this.$refs.mes)
              this.$refs.mes[0].deshabilitar = true;
              if(this.$refs.ano)
              this.$refs.ano[0].deshabilitar = true;
              if(this.$refs.año)
              this.$refs.año[0].deshabilitar = true;
              if(this.$refs.num_con)
              this.$refs.num_con[0].deshabilitar = true;
              if(this.$refs.pais)
              this.$refs.pais[0].deshabilitar = true;
              if(this.$refs.departamento)
              this.$refs.departamento[0].deshabilitar = true;
              if(this.$refs.cod_mun)
              this.$refs.cod_mun[0].deshabilitar = true;
              if(this.$refs.cla_ini_ca)
              this.$refs.cla_ini_ca[0].deshabilitar = true;
            });
        }else if (ajuste.toString() != '0' && this.ajustar && ajuste.toString() != ''){
          let campos =  Object.keys(this.$refs);
          campos = campos.filter((x) => x.match(/^[a-z].*/) && x !="btnGuardarB" && x !="btnGuardarC" );
          campos.forEach(element => {
            if(element != 'ajuste'){
              this.$refs[element][0].deshabilitar = true;
              this.$refs[element][0].puedeValidar = false;
            }
          });
          this.variablesFormulario.complementariosCargados = true;
        }else{
          this.variablesFormulario.complementariosCargados = true;
        }
      }
    },
    /**
     * Método que habilita el campo ajuste para que el usuario pueda seleccionar el valor a ajustar
     */
    cambiarEstadoAjuste() {
      this.$refs.ajuste[0].valor = '';
      this.ajustar = true;
      this.$refs.ajuste[0].$el.scrollIntoView({ block: 'end', scrollBehavior: 'smooth' });
      this.$refs.ajuste[0].deshabilitar = false;
    },
    establecerValor({ clave, valor, jsonAsociado,texto}) {
      this[jsonAsociado][clave] = valor;
      if(texto != null) this.textosListas[clave] = texto;
    },
    estableceTexto({ clave,texto }){
      if(texto != null && texto != undefined) this.textosListas[clave] = texto;
    },
    actualizarValidacion({clave, jsonAsociado}) {
      if (this.$refs[clave][0].validaciones){
        MetodosComunes.procesarObjetoValidaciones(this.$refs[clave][0].validaciones.validacionesArreglo, this[jsonAsociado],[], this.variablesFormulario)
        this.$refs[clave][0].validaciones.valor = this[jsonAsociado][clave];
      }
    },
    configuraFuncionalidad(clave, funcionalidad, jsonAsociado,fuente, parametro) { if (this.$refs[clave] && this.$refs[clave][0].funcionalidades ) {
        MetodosComunes.procesarObjetoFuncionalidad(this.$refs[clave][0].funcionalidades.funcionalidadesArreglo, this[jsonAsociado], funcionalidad,[],this.variablesFormulario,this[jsonAsociado],this.$refs);
        this.$refs[clave][0].ejecutarComportamiento(funcionalidad,fuente, parametro);
      }
    },
    configuraValidacion(clave) {//, validacion, jsonAsociado,fuente
      if(this.$refs[clave] != undefined){
        if (this.$refs[clave][0].validaciones) {
          this.$refs[clave][0].validar();
        }
      }
    },
    emitirFuncionesAfectantes(funcionesAfectantes, jsonAsociado,fuente,validacionesAfectantes) {
      if(this.puedeEjecutarAfectantes){
        funcionesAfectantes.filter(x=>x.evento == fuente).forEach(({ funcion, clave, parametro }) => {
          this.configuraFuncionalidad(clave, funcion, jsonAsociado,fuente, parametro);
        });
        if(this.variablesFormulario.complementariosCargados && fuente == 'change')
        validacionesAfectantes.forEach(({ clave }) => {//funcion
          this.configuraValidacion(clave); //, funcion, jsonAsociado,fuente
        });

      }
    },
    estableceEvento(res,texto) {
      this.capturaVariablesComp = {};
      this.capturaVariablesCompTipos = {};
      this.variablesFormulario.complementariosCargados = false;
      this.metaDatosComp= [];
      this.textosListas = {};
      this.variablesFormulario.cod_eve = res;
      this.$refs = {};
      
      this.cod_eve = res;
      this.variablesFormulario.cod_eve = res;
      this.tipoColectivo = this.listaEventos.filter(a => a.value == this.cod_eve)[0].tipoColectivo;
      const eventoSegunTipoEvento = this.tipoColectivo == 'b' ? 'Brotes' : res;
      this.obtenerFormularioComplementarios(res);
      if(texto != null) this.textosListas["cod_eve"] = texto;
      individualesProvider.obtenerCamposComplementarios(eventoSegunTipoEvento).then(respuesta =>{
        this.camposComplementarios = respuesta.data;
      })
    },
    obtenerEventos() {
      ComunProvider.retornarListaEventosColectivos()
        .then((res) => {
          this.listaEventos = res.data.map((a) => {
            return { value: a.cod_eve, label: a.nom_eve,tipoColectivo:a.tipoColectivo };
          });
          if (this.idEvento != 0 || this.idEvento.startsWith("OF")) {
            this.estableceEvento(this.cod_eve,this.listaEventos.filter(x=>x.value == this.cod_eve)[0].label);
          }
        });
    },
    obtenerFormularioComplementarios() {
      this.load = true;
      let cod_eve = this.cod_eve;
      cod_eve = this.tipoColectivo == 'b' ? 'Brotes' :cod_eve; 
      individualesProvider.retornarEstructuraBasicos({ cod_eve }).then(resA => {
        this.capturaVariablesCompTipos = resA.data.map(({nombre,cl_tipo}) => ({nombre:nombre.toLowerCase(),cl_tipo}));
        this.capturaVariablesComp = resA.data.reduce((a, b) => (a[b.nombre] = (''), a), {});
        let params = { cod_eve };
        individualesProvider.retornarDatosComplementarios(params).then((res) => {
          this.camposFechaFormatear = res.data.filter(x => x.tipo == "ctrlFecha").reduce((a,b) => (a[b.campo] = b.campo, a), {});
          MetodosComunes.obtenerValidaciones(cod_eve).then((validaciones) => {
            MetodosComunes.obtenerFuncionalidades(cod_eve).then((funcionalidades) => {
              MetodosComunes.obtenerFuncionalidadesAfectantes(cod_eve).then((funcionalidadesAfectantes) => {
                MetodosComunes.obtenerValidacionesAfectantes(cod_eve).then((validacionesAfectantes) => {
                funcionalidadesAfectantes = MetodosComunes.agruparArregloPorLlave(funcionalidadesAfectantes, "parametro");
                validacionesAfectantes = MetodosComunes.agruparArregloPorLlave(validacionesAfectantes, "parametro");
                var datos = MetodosComunes.procesarJsonCrudo(res.data, validaciones, funcionalidades, funcionalidadesAfectantes,validacionesAfectantes);
                MetodosComunes.agruparArreglo(datos, 'grupo').then(respuesta => {
                  MetodosComunes.agruparTabla(respuesta).then(tablas => {
                    this.tablas = tablas;
                    this.metaDatosComp = respuesta;
                    this.agregarFuncionAjustar();
                    this.$nextTick(() => {
                      if (this.idEvento == 0 && this.$refs.ajuste) {
                        this.$refs.ajuste[0].deshabilitar = true
                        this.$refs.ajuste[0].valor = 0           
                        this.metaDatosComp.ajuste = 0;                       
                        this.$refs.fec_aju[0].deshabilitar = true;    
                        ComunProvider.obtenerFechaActual().then(res=>{
                          this.$refs.fec_aju[0].valor = moment(res.data).format("DD/MM/YYYY");
                        })          
                      }else if(this.$refs.ajuste){
                        ComunProvider.obtenerFechaActual().then(res=>{
                          this.$refs.fec_aju[0].valor = moment(res.data).format("DD/MM/YYYY");
                        })  
                      }  
                      this.load = false;
                      this.ObtenerComplementario().then(()=>{
                        this.variablesFormulario.cambioTab = false;
                        this.puedeEjecutarAfectantes = true;
                        console.log('Evento: ',this.variablesFormulario.cod_eve,' Ajuste:',this.$refs.ajuste[0].valor, " Ajuste en json:",this.capturaVariablesComp.ajuste," Ó ",this.capturaVariablesComp.Ajuste);
                        this.bloqueaAjuste = global.BloqueoAjustes[this.variablesFormulario.cod_eve].includes(this.capturaVariablesComp.ajuste)
                      })
                    });
                  });
                });
                });
              });
            });
          });
        })
      })
    },
    agregarFuncionAjustar(){
      const funcionalidadAfectante = {
        clave: "ajuste",
        evento: "change",
        funcion: "AjustarColectivo",
        parametro: "ajuste"
      };
      const funcionalidadAjustar = {
        campo: "ajuste",
        nombre: "AjustarColectivo",
        parametros:[],
        parametrosCrudos:[{orden: "1",parametro: "ajuste",tipo: "dependiente"}]
      }
      this.metaDatosComp[0].controles.filter(x=>x.campo == 'ajuste')[0].funcionalidades.funcionalidadesAfectantes.push(funcionalidadAfectante);
      this.metaDatosComp[0].controles.filter(x=>x.campo == 'ajuste')[0].funcionalidades.funcionalidadesArreglo.push(funcionalidadAjustar);
    },
    reload(id){
      if(this.$route.path != '/Colectivos/'+id+'/'+this.cod_eve){
        this.$router.push({ path: '/Colectivos/'+id+'/'+this.cod_eve});
        location.reload();
      }
    },
    ObtenerComplementario(){
      return new Promise((resolve, reject) => {
        ColectivosProvider.obtenerComplementarioColectivo({ idEvento: this.idEvento,cod_eve: this.cod_eve}).then((res) => {
          this.eventoPermitido = true;
          try{
            if(res.data.length >0){//si el evento ya tiene un complementario registrado
              //Se inactiva los campos que no pueden ser cambiados ni siquiera con los ajustes
              this.$refs.ajuste[0].deshabilitar = true;
              this.$refs.cod_pre[0].deshabilitar = true;
              this.$refs.fec_not[0].deshabilitar = true;
              this.$refs.ano[0].deshabilitar = true;
              this.$refs.fec_aju[0].deshabilitar = true; 
              if(this.$refs.semana != undefined){
                this.$refs.semana[0].deshabilitar = true; 
              }
              if(this.$refs.mes != undefined){
                this.$refs.mes[0].deshabilitar = true; 
              }
              
              let jsonEvento;
              if(!Object.hasOwn(res.data[0], 'JsonEvento')){
                jsonEvento = res.data[0]; //obtiene los campos del evento
              }else{
                jsonEvento = JSON.parse(res.data[0].JsonEvento);//obtiene el json del evento
              }
              jsonEvento = this._.mapKeys(jsonEvento, function (v, k) { return k.toLowerCase(); });
              jsonEvento.cod_pre = jsonEvento.cod_pre + jsonEvento.cod_sub;
              this.ajustarGeografias(jsonEvento);
              //obtiene los valores del complementario existente sin eliminar los campos de utilidad que ya existen en this.capturaVariables
              this.capturaVariablesComp = this._.mapKeys(this.capturaVariablesComp, function (v, k) { return k.toLowerCase(); });
              this.capturaVariablesComp = this._.assign(this.capturaVariablesComp, this._.pick(jsonEvento, this._.keys(this.capturaVariablesComp)));
              console.log("aun no resuelta");
              this.cargarValoresFormularioComplementarios();
              var campos = Object.keys(this.capturaVariablesComp);
              this.puedeEjecutarAfectantes = true;
              this.ejecutarAfectantes(campos).then(() =>{
                this.variablesFormulario.complementariosCargados = true;
                Object.keys(this.$refs).filter((x) => x.match(/^[a-z].*/)).forEach(element => {
                  if (element != 'Cod_eve' && this.$refs[element][0] != null && !this.idEvento.startsWith("OF")) {
                    this.$refs[element][0].deshabilitar = true
                  }
                });
              });
            }else{
              var camposs = Object.keys(this.capturaVariablesComp);
              this.puedeEjecutarAfectantes = true;
              this.ejecutarAfectantes(camposs).then(() =>{
                this.variablesFormulario.complementariosCargados = true;
              });
            }
            resolve(1); 
            console.log("resuelta");
        }catch(er){ console.log(er); reject(er)}
        }, err => {
          if (err.response){
            if(err.response.status == 403){
              this.eventoPermitido = false;
            }
          }else{
            console.log(err);
          }
        })
      });
    },
    setearValoresBasicos(camposBasicos,upgd,evento){
      switch(this.tipoColectivo){
        case 's':{
          const esRabia = (this.capturaVariablesComp.cod_eve == '650' || this.capturaVariablesComp.cod_eve == '652') ? true : false;
          camposBasicos.Semana = this.capturaVariablesComp.semana
          camposBasicos.Ano = this.capturaVariablesComp.ano
          camposBasicos.Cod_pre = upgd.substring(0, upgd.length - 2);
          camposBasicos.Cod_sub = upgd.substring(upgd.length - 2, upgd.length);
          camposBasicos.Cod_eve = evento
          camposBasicos.Num_con = esRabia ? "" : this.capturaVariablesComp.num_con
          camposBasicos.Fec_not = MetodosComunes.formatearCualquierFecha(this.capturaVariablesComp.fec_not);//moment(this.capturaVariablesComp.fec_not, "DD/MM/YYYY").format("MM/DD/YYYY");
          camposBasicos.Ajuste = this.capturaVariablesComp.ajuste
          camposBasicos.Fec_aju = MetodosComunes.formatearCualquierFecha(this.capturaVariablesComp.fec_aju);
          moment(this.capturaVariablesComp.fec_aju, "DD/MM/YYYY").format("MM/DD/YYYY");
          break
        }
        case 'm':
          camposBasicos.Mes = this.capturaVariablesComp.mes
          camposBasicos.Ano = this.capturaVariablesComp.ano
          camposBasicos.Cod_pre = upgd.substring(0, upgd.length - 2);
          camposBasicos.Cod_sub = upgd.substring(upgd.length - 2, upgd.length);
          camposBasicos.Cod_eve = evento;
          camposBasicos.Num_con = this.capturaVariablesComp.num_con
          camposBasicos.Fec_not = MetodosComunes.formatearCualquierFecha(this.capturaVariablesComp.fec_not);  // moment(this.capturaVariablesComp.fec_not, "DD/MM/YYYY").format("MM/DD/YYYY");
          camposBasicos.Ajuste = this.capturaVariablesComp.ajuste;
          camposBasicos.Fec_aju = MetodosComunes.formatearCualquierFecha(this.capturaVariablesComp.fec_aju);//moment(this.capturaVariablesComp.fec_aju, "YYYY-MM-DD").format("MM/DD/YYYY");
        break
        case 'b':
        break
      }
    },
    ejecutaValidaciones(){

    },
    async guardarColectivos() {
      let jsonEvento = {};
      let camposBasicos = {};
      let promesasValidacion = [];
      const upgd = this.capturaVariablesComp.cod_pre
      try{
        Object.keys(this.$refs).filter((x) => x.match(/^[a-z].*/)).forEach(element => {
            promesasValidacion.push(this.$refs[element][0].validar());
        });
        let resultado = await Promise.all(promesasValidacion);
        if (!resultado.filter(x => x.valido == false).length > 0){
          if(this.tipoColectivo != 'b')MetodosComunes.darFormatoInsAFechas(Object.keys(this.camposFechaFormatear),this.capturaVariablesComp);
          this.camposComplementarios.forEach(campoJson => {
            if(this.capturaVariablesComp[campoJson.toLowerCase()])
              jsonEvento[campoJson] = this.capturaVariablesComp[campoJson.toLowerCase()].toString();
            else{
              jsonEvento[campoJson] = '';
            }
          });
          this.setearValoresBasicos(camposBasicos,upgd,this.cod_eve);
          let colectivoGuardar = {}
          if(this.tipoColectivo != 'b'){
            jsonEvento[MetodosComunes.encontrarPropiedadOriginal('cod_pre',this.camposComplementarios)] = upgd.substring(0, upgd.length - 2);
            jsonEvento[MetodosComunes.encontrarPropiedadOriginal('cod_sub',this.camposComplementarios)] = upgd.substring(upgd.length - 2, upgd.length);
            jsonEvento[MetodosComunes.encontrarPropiedadOriginal('cod_eve',this.camposComplementarios)] = this.cod_eve;
            if(this.cod_eve.toString() == '650' || this.cod_eve.toString() == '652'){
              jsonEvento[MetodosComunes.encontrarPropiedadOriginal('cod_dep_pr',this.camposComplementarios)] = jsonEvento[MetodosComunes.encontrarPropiedadOriginal('cod_mun_pr',this.camposComplementarios)].substring(0,2);
              jsonEvento[MetodosComunes.encontrarPropiedadOriginal('cod_mun_pr',this.camposComplementarios)] = jsonEvento[MetodosComunes.encontrarPropiedadOriginal('cod_mun_pr',this.camposComplementarios)].substring(2);
            }
            colectivoGuardar = {
              id: this.variablesFormulario.idEvento,
              ...camposBasicos,
              JsonEvento: JSON.stringify(jsonEvento),
              Semcarga: "1",
              Oportuna: "1",
              Semanal: "1",
              Fechahora: MetodosComunes.fechaActual()
            };
          }else{
            jsonEvento.FEC_NOT =  moment(this.capturaVariablesComp.fec_not, "DD/MM/YYYY").format("MM/DD/YYYY");
            jsonEvento.FEC_AJU =  moment(this.capturaVariablesComp.fec_aju, "DD/MM/YYYY").format("MM/DD/YYYY");
            jsonEvento.COD_PRE =  upgd.substring(0, upgd.length - 2);
            jsonEvento.COD_SUB = upgd.substring(upgd.length - 2, upgd.length);
            jsonEvento.COD_EVE = this.cod_eve;
            jsonEvento.DEP_PRO = jsonEvento.MUN_PRO.substring(0,2);
            jsonEvento.MUN_PRO = jsonEvento.MUN_PRO.substring(2);
            colectivoGuardar = {
              id: this.variablesFormulario.idEvento,
            ...jsonEvento,
            Semcarga: "1",
            Oportuna: "1",
            Semanal: "1",
            Fechahora: MetodosComunes.fechaActual()
            }
          }
          const res = await ColectivosProvider.guardarColectivosLocal(colectivoGuardar,this.tipoColectivo);
          if(res.data.response){
            this.$emit("sincroniza", (mensaje) => this.$modal.alertar({
              titulo: "Exito", mensaje: mensaje, tipo: 1, onConfirm: () => {
                this.$router.push('/Colectivos');
              }
            }));
          }
        }else{
          const validacionFallida = resultado.filter(x => x.valido == false)[0];
          const msg = (validacionFallida.mensajePersonalizado != null) ? validacionFallida.mensajePersonalizado : "Existen validaciones que debe realizar"
          this.$modal.alertar({titulo: "Atención",mensaje: msg,tipo: 2})
        } 
      }catch(err){
        if(this.tipoColectivo != 'b')MetodosComunes.darFormatoCapturaAFechas(Object.keys(this.camposFechaFormatear),this.capturaVariablesComp);
        if(global.isOnline){
          if(err.response.status == 401){
            this.$modal.alertar({ titulo: "Atención", mensaje: "Sesión vencida, por favor vuelva a ingresar", tipo: 2, onConfirm: () => {
              console.log(global.siv40);
              window.location.href = global.siv40;
            }})
          }else{
            this.$modal.alertar({ titulo: "Atención", mensaje: "Error al guardar, revise su conexión a internet y si su sesión está activa y vuelva a intentarlo", tipo: 2 })
          }
        }else{
          console.log(err)
        }
      }
    },
    
    cargarValoresFormularioComplementarios(){
      this.puedeEjecutarAfectantes = false;
      var campos = Object.keys(this.capturaVariablesComp);
      campos.forEach(campo => {
        if (this.$refs[campo]) {
          if(this.capturaVariablesCompTipos.filter(x => x.nombre == campo).length > 0 && this.capturaVariablesCompTipos.filter(x => x.nombre == campo)[0].cl_tipo == 'ctrlFecha'){
            this.$refs[campo][0].valor = MetodosComunes.formatearCasosFecha(this.capturaVariablesComp[campo]);
          }else{
            this.$refs[campo][0].valor = this.capturaVariablesComp[campo];
          }
        }
      })
      this.$refs.cod_pre[0].valor = this.capturaVariablesComp["cod_pre"];
    },
    ejecutarAfectantesBack(){
      return new Promise((resolve) => {
            this.puedeEjecutarAfectantes = true;
            var campos = Object.keys(this.capturaVariablesComp);
            campos = campos.filter((x) => x.match(/^[a-z].*/));
            campos.forEach((nombreCampo) =>{
              if (this.$refs[nombreCampo]) {
                this.$nextTick(() => {
                  this.$refs[nombreCampo][0].emitirFuncionesAfectantes("change");
                })
              }
          resolve(true);
        })
      })
    },
    ejecutarAfectantes(campos) {
      this.variablesFormulario.complementariosCargados = false;
      return new Promise((resolve) => {
        this.puedeEjecutarAfectantes = true;
        let longitudMenosNoPintados = campos.length;
        for (let index = 0; index < longitudMenosNoPintados; index++) {
          const nombreCampo = campos[index];
            this.$nextTick(() => {
              if (this.$refs[nombreCampo] && this.$refs[nombreCampo][0] != null){
                // this.llenaVariablesParaFichaImpresion(nombreCampo);
                this.$refs[nombreCampo][0].emitirFuncionesAfectantes("change");
              }
              if(index == longitudMenosNoPintados-1){
                resolve(true);
              }
            })
        }
      })
    },
    abrirLaboratorios(){
      this.irA("../Laboratorios/" + this.eventoCrudo);
    },
    irA(ruta) {
      this.$router.push(ruta);
    },
    revisarLaboratorios(codigo){
      LaboratoriosProvider.obtenerEventosConLaboratorios(this.evento).then(res =>{
        var evs = res.data;
        var names = evs.map(function(item) {
          return item['cod_eve'];
        });
        if(names.includes(codigo)){
          this.tieneLabs = true;
        }
      })
    },
    atras() {
      //let ruta = "../Individuales/" + this.idEvento; 
      this.$router.go(-1);
    },
    ajustarGeografias(jsonEvento){
      if(this.cod_eve.toString(jsonEvento) == '650' || this.cod_eve.toString() == '652'){
        jsonEvento.cod_mun_pr = jsonEvento.cod_dep_pr + jsonEvento.cod_mun_pr;
        jsonEvento.cod_dep_pr = MetodosComunes.completarCod_dpto(jsonEvento.cod_dep_pr,jsonEvento.cod_mun_pr);
      }
      if( this.tipoColectivo == 'b'){
        jsonEvento.mun_pro = jsonEvento.dep_pro + jsonEvento.mun_pro;
        jsonEvento.dep_pro = MetodosComunes.completarCod_dpto(jsonEvento.dep_pro,jsonEvento.mun_pro);
      }
    },

    loguearValorCapo(){
      console.log(this.capturaVariablesComp.fec_aju.toString() );
    }
  },
  mounted() {
    this.$emit("disenoPagina", true);
    this.obtenerEventos();
  },
};