
import ComunProvider from "../../providers/ComunProvider";

export default {
  name: "LookUpTableControlMultiSeleccion",
  props: [
    "options",
    "deshabilitar",
    "tooltip",
    "mask",
    "valorCampo",
    "hayError",
    "mensajeError",
    "campo",
    "MostrarDescripcion",
    "campoCaptura",
    "extraparams",
    "bloqueante",
    "cl_lutValor",
    "cl_lutDesc",
    "max",
    "esTabla",
  ],
  data() {
    return {
      valorCrudo: this.valorCampo ?? "",
      valorCrudo2: null,
      Rawitems: [],
      navigationActive: false,
      filter: "",
      selected: [],
      pagination: {},
      modal: false,
      confirm: false,
      columns: [],
      deshabilitarCrudo: this.deshabilitar,
      textoUnido: "",
      valorUnido: "",
      claseTabla: this.esTabla ? "inputsTablas" : "",
    };
  },
  computed: {
    valor: {
      get() {
        return this.valorCrudo == null ? "" : this.valorCrudo;
      },
      set(val) {
        this.valorCrudo = val;
        //    var a = this.Rawitems.filter((o) =>
        //   Object.keys(o).some((k) =>
        //     o[k].toLowerCase() == val.toLowerCase()
        //   )
        // );
        //this.valor2 = a.length == 1 ? a[0][this.cl_lutDesc ?? Object.keys(a[0])[1]] : "";
        this.valor2 = this.textoUnido;
        console.log("cambia multiple 1",this.valor);
        console.log("multiple 1",this.valor2);
        this.$emit("seleccionDescripcion", this.campoCaptura, this.valor);
        if (val == '') {
          this.selected = [];
        }
      },
    },
    valor2: {
      get() {
        return this.valorCrudo2 == null ? "" : this.valorCrudo2;
      },
      set(val) {
        this.valorCrudo2 = val;
      },
    },
    tableClass() {
      return this.navigationActive === true ? "shadow-8 no-outline" : void 0;
    },
    deshabilitarValor: {
      get() {
        return this.deshabilitarCrudo;
      },
      set(val) {
        this.deshabilitarCrudo = val;
      },
    },
  },
  methods: {
    seleccion(details) {
      if (details.length > 0) {
        var i = 0;
        if (details.length > 1) {
          for (i = 0; i < details.length; i++) {
            this.textoUnido =
              details.length > 0
                ? details[i][this.cl_lutDesc ?? Object.keys(details[0])[1]]
                : "";
            this.valorUnido =
              details.length > 0 ? details[i][this.cl_lutValor ?? this.cl_lutValor] : "";
            if (i == 0) {
              this.valor2 = this.textoUnido;
              this.valor = this.valorUnido;
            } else {
              this.valor2 = this.valor2 + " + " + this.textoUnido;
              this.valor = this.valor + " + " + this.valorUnido;
            }
          }
        } else {
          this.valor2 =
            details.length > 0
              ? details[0][this.cl_lutDesc ?? Object.keys(details[0])[1]]
              : "";
          this.valor =
            details.length > 0 ? details[0][this.cl_lutValor ?? this.cl_lutValor] : "";
        }
      } else {
        this.valor = "";
        this.valor2 = "";
      }
      console.log("cambia multiple 2",this.valor);
      this.$emit("seleccion", this.valor);
      this.$emit("seleccionDescripcion", this.campoCaptura, this.valor);
    },
    keepFocus() {
      if (this.bloqueante) {
        this.$refs["input"].focus();
      } else {
        this.$refs["input"].blur();
      }
      this.confirm = false;
    },
    activateNavigation() {
      this.navigationActive = true;
    },
    deactivateNavigation() {
      this.navigationActive = false;
    },

    onKey(evt) {
      if (
        this.navigationActive !== true ||
        [33, 34, 35, 36, 38, 40].indexOf(evt.keyCode) === -1 ||
        this.$refs.myTable === void 0
      ) {
        return;
      }

      evt.preventDefault();

      const { computedRowsNumber, computedRows } = this.$refs.myTable;

      if (computedRows.length === 0) {
        return;
      }

      const currentIndex =
        this.selected.length > 0 ? computedRows.indexOf(this.selected[0]) : -1;
      const currentPage = this.pagination.page;
      const rowsPerPage =
        this.pagination.rowsPerPage === 0
          ? computedRowsNumber
          : this.pagination.rowsPerPage;
      const lastIndex = computedRows.length - 1;
      const lastPage = Math.ceil(computedRowsNumber / rowsPerPage);

      let index = currentIndex;
      let page = currentPage;

      switch (evt.keyCode) {
        case 36: // Home
          page = 1;
          index = 0;
          break;
        case 35: // End
          page = lastPage;
          index = rowsPerPage - 1;
          break;
        case 33: // PageUp
          page = currentPage <= 1 ? lastPage : currentPage - 1;
          if (index < 0) {
            index = 0;
          }
          break;
        case 34: // PageDown
          page = currentPage >= lastPage ? 1 : currentPage + 1;
          if (index < 0) {
            index = rowsPerPage - 1;
          }
          break;
        case 38: // ArrowUp
          if (currentIndex <= 0) {
            page = currentPage <= 1 ? lastPage : currentPage - 1;
            index = rowsPerPage - 1;
          } else {
            index = currentIndex - 1;
          }
          break;
        case 40: // ArrowDown
          if (currentIndex >= lastIndex) {
            page = currentPage >= lastPage ? 1 : currentPage + 1;
            index = 0;
          } else {
            index = currentIndex + 1;
          }
          break;
      }

      if (page !== this.pagination.page) {
        this.pagination = {
          ...this.pagination,
          page,
        };

        this.$nextTick(() => {
          const { computedRows } = this.$refs.myTable;
          this.selected = [computedRows[Math.min(index, computedRows.length - 1)]];
        });
      } else {
        this.selected = [computedRows[index]];
      }
    },
    validarDato() {
      if (this.valor == "" || this.valor == null || this.valor == undefined) {
        if (!this.modal && this.bloqueante && this.bloqueante != undefined) {
          this.confirm = true;
        }
      } else {
        var a = this.Rawitems.filter((o) =>
          o[Object.keys(o)[0]].toLowerCase() == this.valor.toLowerCase()
        );
        if (a.length != 1 && !this.modal) {
          this.confirm = true;
        }
      }
    },
    emitirSeleccion() {
      console.log("selección multiple valor", this.valor);
      this.$emit("seleccion", this.valor, this.valor2);
      this.$emit("seleccionDescripcion", this.campoCaptura, this.valor2);
    },
    CargarOpciones(params) {
      ComunProvider.retornarPOST(this.options, params).then((res) => {
        this.Rawitems = res.data;
        this.columns = Object.keys(res.data[0]).map((b) => {
          return {
            name: b,
            label: b,
            field: b,
            align: "center",
            style: "table-layout: fixed;white-space: normal;",
          };
        });
        if (this.Rawitems.length == 1) {
          this.valor = this.Rawitems[0][this.cl_lutValor ?? this.cl_lutValor];
          this.valor2 = this.Rawitems[0][
            this.cl_lutDesc ?? Object.keys(this.Rawitems[0])[1]
          ];
          this.$emit("seleccion", this.valor);
          this.$emit("seleccionDescripcion", this.campoCaptura, this.valor2);
          this.deshabilitarValor = true;
        } else {
          this.deshabilitarValor = false;
          var a = res.data.filter((o) =>
            o[Object.keys(o)[0]].toLowerCase() == this.valor.toLowerCase()
          );
          if (a.length == 1) {
            this.valor2 = a[0][this.cl_lutDesc ?? Object.keys(a[0])[1]];
            console.log("cambia multiple 3",this.valor);
            this.$emit("seleccionDescripcion", this.campoCaptura, this.valor);
          }
        }
        this.$emit("estableceTexto", this.valor);
        console.log("cambia multiple 4",this.valor);
      });
    },
    blur() {
      this.$emit("emitirFuncionesAfectantes", "LookUp");
      this.$emit("blur", this.valor);
    },
    cerrarVentana() {
      this.modal = false;
    },
    establecerTexto() {
      console.log("cambia multiple 5",this.valor);
      this.$emit("estableceTexto", this.valor);
    },
    filtraSinAcento(lista,filtro){
      const filtrado = lista.filter(x => x[this.cl_lutDesc ?? Object.keys(x)[1]].toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, "").includes(filtro.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, ""))   
                                     || x[this.cl_lutValor ?? Object.keys(x)[0]].toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, "").includes(filtro.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, "")));
      return filtrado;
    }
  },
  mounted() {
    if (this.extraparams != null) {
      this.CargarOpciones(this.extraparams);
    } else {
      this.CargarOpciones();
    }
  },
};
