import LookUpTableControlN from "../../../componentes/controles/LookUpTableControlN";
import { required, minLength } from 'vuelidate/lib/validators'
import SeguimientosProvider from "../../../providers/SeguimientosProvider";
import moment from "moment";

export default {
    name: "Seguimientos",
    components: { LookUpTableControlN },
    props: ['id_persona', 'Fec_not', 'Sintomatic', 'cod_eve'],
    data() {
        return {
            ide: [],
            form: {
                Id_persona: this.id_persona !== undefined ? this.id_persona : '',
                Fec_segumi: this.Fec_not !== undefined ? this.Fec_not : "",
                Hora_segui: "00:00",
                Ajuste: "0",
                Fec_aju: "",
                Sintomatic: false,
                Tos: false,
                Fiebre: false,
                Dif_res: false,
                Odinofagia: false,
                Adinamia: false,
                Hospitaliz: false,
            },
            tooltipFecCar: "Formato DD/MM/AAAA",
            Fec_inicio: "",
            block_sintom: !this.Sintomatic,
            block_sintomatic: false,
            block_Fec_seg: false,
            showModalSeguimientos: false,
            itemsSeguim: [],
            columnsSeguim: [],
            selected: [],
            pagination: {},
            seguimientos: [],

        }
    },
    validations: {
        form: {
            Fec_segumi: {
                required,
                minLength: minLength(10)
            },
            Hora_segui: {
                required,
                minLength: minLength(5)
            },
            Ajuste: {},
            Fec_aju: {},
            Sintomatic: {},
            Tos: {},
            Fiebre: {},
            Dif_res: {},
            Odinofagia: {},
            Adinamia: {},
            Hospitaliz: {},
        },
    },
    methods: {

        /**
         * Método que verifica la validez de la hora de seguimiento.
         */
        verificarHora() {
            if (this.form.Hora_segui.length > 4) {
                let data0 = this.form.Hora_segui.split(':')
                if (data0[0] > 23 || data0[1] > 59)
                    return false
            }
            return true
        },

        /**
         * Método que obtiene la fecha actual del sistema.
         */
        getFecha() {
            const today = new Date();
            const day = (today.getDate()) < 10 ? '0' + today.getDate() : today.getDate();
            const month = ((today.getMonth() + 1)) < 10 ? '0' + (today.getMonth() + 1) : (today.getMonth() + 1);
            const date = day + '/' + month + '/' + today.getFullYear();
            this.form.Fec_inicio = date;
            this.form.Fec_aju = date;
        },

        /**
         * Método que valida que la fecha de seguimiento no puede ser superior a la fecha del sistema y no sea inferior a 01/01/2020
         */
        validarFechaSeg() {
            if (this.form.Fec_segumi != null && this.form.Fec_segumi.length > 9) {
                let minDate = new Date(2020, 1, 1)

                let data = this.form.Fec_inicio.split('/')
                let actual_date = new Date(data[2], data[1], data[0])

                let data2 = this.form.Fec_segumi.split('/')
                let verify_date = new Date(data2[2], data2[1], data2[0])
                if (actual_date < verify_date) {
                    this.$modal.alertar({
                        titulo: "Alerta",
                        mensaje: "La fecha de seguimiento no debe ser mayor a la fecha del sistema.",
                        tipo: 2
                    })
                    this.form.Fec_segumi = this.form.Fec_inicio;
                }
                else if (minDate > verify_date) {
                    this.$modal.alertar({
                        titulo: "Alerta",
                        mensaje: "La fecha de seguimiento debe ser mayor a 01/01/2020.",
                        tipo: 2
                    })
                    this.form.Fec_segumi = '01/01/2020';
                }

                return true
            }
            return false

        },

        validarFechaAjuste() {
            if (this.form.Fec_aju != null && this.form.Fec_aju.length > 9) {
                let minDate = new Date(2020, 1, 1)

                let data = this.form.Fec_inicio.split('/')
                let actual_date = new Date(data[2], data[1], data[0])

                let data2 = this.form.Fec_aju.split('/')
                let verify_date = new Date(data2[2], data2[1], data2[0])
                if (actual_date < verify_date) {
                    this.$modal.alertar({
                        titulo: "Alerta",
                        mensaje: "La fecha de ajuste no debe ser mayor a la fecha del sistema.",
                        tipo: 2
                    })
                    this.form.Fec_aju = this.form.Fec_inicio;
                }
                else if (minDate > verify_date) {
                    this.$modal.alertar({
                        titulo: "Alerta",
                        mensaje: "La fecha de ajuste debe ser mayor a 01/01/2020.",
                        tipo: 2
                    })
                    this.form.Fec_aju = '01/01/2020';
                }
            }

        },
    
        /**
         * cambio de valores del set de variables al activar sintomatico
         */
        validarSintom() {
            this.block_sintom = !this.form.Sintomatic;

            if (!this.block_sintom) {
                this.form.Tos = false
                this.form.Dif_res = false
                this.form.Fiebre = false
                this.form.Odinofagia = false
                this.form.Adinamia = false
            }
            else {
                this.form.Tos = ''
                this.form.Fiebre = ''
                this.form.Dif_res = ''
                this.form.Odinofagia = ''
                this.form.Adinamia = ''
            }
        },

        /**
         * Método que retorna el mensaje de error de un campo.
         * @param {*} campo -> Campo del formulario.
         */
        mensajeError(campo) {
            if (campo === 'Hora_seg' && this.form.Hora_segui.length !== 0) {
                return 'La hora debe ser un valor entre 0 y 24 o Los minutos deben ser un valor entre 0 y 59.'
            }
            else if (campo === 'Sintom' && this.form.Sintomatic.length !== 0) {
                return 'Valor invalido. Digite un valor entre 1 y 2'
            }
            else if (campo === 'Hospitalizado' && this.form.Hospitaliz.length !== 0) {
                return 'Valor invalido. Digite un valor entre 1 y 2'
            }
            else if (campo === 'Fec_seg' && this.form.Fec_segumi.length !== 0) {
                return 'Fecha invalida.'
            }
            else
                return 'Campo requerido'
        },

        atras() {
            this.$router.go(-1);
        },

        limpiarFormulario() {
            this.form.Fec_segumi = ""
            this.form.Hora_segui = "00:00"
            this.form.Sintomatic = false
            this.validarSintom()
            this.block_sintomatic = false
            this.block_Fec_seg = false
            this.form.Hospitaliz = false
            this.$v.$reset()
        },

        /**
         * Método que transforma los valores dados por el usuario a los valores que recibe la base de datos.
         */
        normalizarCampos(formData) {
            formData.Sintomatic = (formData.Sintomatic === '') ? '' : (formData.Sintomatic ? 1 : 2);
            formData.Tos = (formData.Tos === '') ? '' : (formData.Tos ? 1 : 2);
            formData.Dif_res = (formData.Dif_res === '') ? '' : (formData.Dif_res ? 1 : 2);
            formData.Fiebre = (formData.Fiebre === '') ? '' : (formData.Fiebre ? 1 : 2);
            formData.Odinofagia = (formData.Odinofagia === '') ? '' : (formData.Odinofagia ? 1 : 2);
            formData.Adinamia = (formData.Adinamia === '') ? '' : (formData.Adinamia ? 1 : 2);
            formData.Hospitaliz = (formData.Hospitaliz === '') ? '' : (formData.Hospitaliz ? 1 : 2);
            formData.Fec_segumi = moment(formData.Fec_segumi, 'DD/MM/YYYY').format('YYYY-MM-DDTHH:mm:ss')
            formData.Fec_aju = moment(formData.Fec_aju, 'DD/MM/YYYY').format('YYYY-MM-DDTHH:mm:ss')
            return formData;
        },

        /**
         * Método que verifica la validez del formulario y envía dicha información al servidor del api.
         */
        guardar() {
            this.$v.$touch();
            if (this.$v.$invalid) {
                this.$modal.alertar({ titulo: "Alerta", mensaje: "Aún hay campos invalidos.", tipo: 2 });
            }
            else {
                const formData = this.normalizarCampos({ ...this.form });
                SeguimientosProvider.GuardarSeguimientoContacto(formData)
                    .then(() => {
                        this.$modal.alertar({ titulo: "Alerta", mensaje: "Se ha guardado el seguimiento con éxito.", tipo: 1 });
                        this.limpiarFormulario()
                        this.obtenerSeguimientosApi();
                    })
                    .catch(error => {
                        this.$modal.alertar({ titulo: "Alerta", mensaje: "Ha surgido un error y no se pudo guardar el seguimiento", tipo: 2 });
                        console.log(error);
                    });
            }
        },

        /**
         * Abre la ventana modal donde se listan los seguimientos.
         */
        showModalSeguim: function () {
            if (this.seguimientos.length !== 0) {
                this.itemsSeguim = this.seguimientos;
                this.columnsSeguim = Object.keys(this.seguimientos[0]).map((b) => {
                    return { name: b, label: b, field: b, align: "center" };
                });
                this.showModalSeguimientos = true;
            }
            else
                this.$modal.alertar({ titulo: "Alerta", mensaje: "El contacto todavía no tiene seguimientos.", tipo: 1 });
        },

        /**
         * Método que obtiene los seguimientos del contacto dado su id.
         */
        obtenerSeguimientosApi() {
            SeguimientosProvider.ConsultarSeguimientosContacto({ "id_persona": this.form.Id_persona })
                .then((res) => {
                    if (res.data.length > 0) {
                        let hora = '';
                        res.data.sort((a, b) => {
                            let num = new Date(a.Fec_segumi) - new Date(b.Fec_segumi)
                            if (num > 0)
                                return -1
                            else
                                return 1
                        })
                        for (let v in res.data) {
                            res.data[v].Fec_segumi = moment(res.data[v].Fec_segumi).format('DD/MM/YYYY')

                            res.data[v].Fec_aju = moment(res.data[v].Fec_aju).format('DD/MM/YYYY')

                            hora = res.data[v].Hora_segui.split(":")
                            res.data[v].Hora_segui = hora[0] + ":" + hora[1]
                        }
                        this.seguimientos = res.data
                        this.form.Fec_segumi = ""
                        this.form.Sintomatic = false
                        this.validarSintom();
                        this.block_sintomatic = false
                        this.block_Fec_seg = false
                    }
                    else {
                        this.form.Sintomatic = this.Sintomatic
                        this.block_sintomatic = true
                        this.block_Fec_seg = true
                        this.validarSintom();
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        },
    },
    beforeDestroy() {
        if (this.$router.currentRoute.name !== 'contactos')
            this.$storage.remove('id_persona');
    },
    mounted() {
        this.getFecha();
        this.obtenerSeguimientosApi();
    }
}
