
import PopUpSiv from './Alerta.js';

export default {
  name:"Alerta",
  data() {
    return {
      visible: false, 
      titulo: '',
      mensaje: '',
      tipo: 3,
      onConfirm: {},
      cancelButton: false,
      onCancel: {},
      textoConfirm:'Aceptar',
      textoCancel:'Cancelar'
    }
  },
  methods: {
    hide() {
      if(typeof this.onCancel === 'function') {
        this.onCancel();
        this.visible = false;
      } else {
        this.visible = false;
      }
    },
    confirm() {
      if(typeof this.onConfirm === 'function') {        
        this.onConfirm();
        this.visible = false;
      } else {
        this.visible = false;
      }
    },
    show(params) {
      this.visible = true;
      this.titulo = params.titulo;
      this.mensaje = params.mensaje;
      this.tipo = params.tipo;
      this.onConfirm = params.onConfirm;
      this.cancelButton = params.cancelButton;
      this.onCancel = params.onCancel;
      this.textoConfirm = params.textoConfirm ?? this.textoConfirm;
      this.textoCancel = params.textoCancel ?? this.textoCancel;
    }
  },
  beforeMount() {
    PopUpSiv.EventBus.$on('alertar', (params) => {
      this.show(params)
    })
  }
}
