            doc.setTextColor(0)
            //doc.setFontType('bold')


            doc.text(12, 120, 'Pers. en sit. de discapacidad  ' + json.Gp_discapa)
            doc.text(70, 125, 'Pob ICBF  ' + json.Gp_pobicfb)
            doc.text(70, 130, 'Victima de violencia armada  ' + json.Gp_vic_vio)
            doc.text(70, 120, 'Desplazados  ' + json.Gp_desplaz)
            doc.text(95, 125, 'Madres Comunitarias  ' + json.Gp_mad_com)
            doc.text(95, 120, 'Migrantes  ' + json.Gp_migrant)
            doc.text(121, 120, 'Carcelarios  ' + json.Gp_carcela)
            doc.text(143, 125, 'Desmovilizados  ' + json.Gp_desmovi)
            doc.text(143, 130, 'Otros  ' + json.Gp_otros)
            doc.text(143, 120, 'Gestantes  ' + json.Gp_gestan)
            doc.text(167, 120, 'Semanas  ' + json.Sem_ges)
            doc.text(12, 130, 'Pob. centros psiquiátricos  ' + json.Gp_psiquia)
            doc.text(12, 125, 'Habitante de calle  ' + json.Gp_indigen)

            //NotificationBackSquare
            doc.setFillColor(0)
            doc.rect(9.8, 132.5, 180.5, 5, 'F')
            doc.setFontSize(11)
            doc.setTextColor(255)
            doc.text(76, 136.5, 'N O T I F I C A C I Ó N')

            //NotificationForm
            doc.rect(10, 137.5, 50, 10)
            doc.setFontSize(9)
            doc.setTextColor(0)
            //doc.setFontType('bold')
            doc.text(12, 141, 'Residencia del paciente')
            //doc.setFontType('normal')
