            doc.rect(94, 137.5, 34, 10)
            doc.setTextColor(0)
            //doc.setFontType('bold')
            doc.text(97, 141, 'Inicio de Sintomas')
            //doc.setFontType('normal')
            doc.text(97, 145, json.Ini_sin)
            
            doc.rect(128, 137.5, 62, 10)
