            doc.rect(10, 147.5, 25, 10)
            doc.setTextColor(0)
            //doc.setFontType('bold')
            doc.text(12, 151, 'Hospitalizado')
            //doc.setFontType('normal')
            doc.text(12, 155.5, (json.Pac_hos.toString()) == "1" ? "SI":"NO")
            doc.rect(35, 147.5, 43, 10)
            doc.setTextColor(0)
            //doc.setFontType('bold')
            doc.text(36, 151, 'Fecha de Hospitalización')
            //doc.setFontType('normal')
            doc.text(36, 155.5, json.Fec_hos)

            doc.rect(78, 147.5, 29, 10)
            doc.setTextColor(0)
            //doc.setFontType('bold')
