import InputControl from "../../componentes/controles/InputControl";
import SearchForm from "../../componentes/controles/SearchForm";
import DropDownControl from "../../componentes/controles/DropDownControl";
import GridIndividualesProvider from "../../providers/GridIndividualesProvider";
import moment from 'moment';
import sincro from "../../utilidades/offline/sincronizacionDex";
import global from "@/Config";
import VueOnlineOffline from "vue-online-offline";
import { Notify } from 'quasar'

export default {
  name: "GridIndividuales",
  components: { InputControl, DropDownControl, SearchForm,VueOnlineOffline },
  props:['basicos'],
  data() {
    return {
      pagesNumber:0,
      filter: '',
      filtro:null,
      loading: false,
      initialPagination: {sortBy: 'Fec_aju',descending: true,page: 1,rowsPerPage: 50,rowsNumber: 50},
      pagination: {sortBy: 'Fec_aju',descending: true,page: 1,rowsPerPage: 50,rowsNumber: 50},
      columns: [
        {name: 'name', align: 'center', label: '', field: row => row.id, sortable: false},
        { name: 'Tip_ide', align: 'center', label: 'Tip ide', field: 'Tip_ide', sortable: false },
        { name: 'Num_ide', align: 'center', label: 'Num ide', field: 'Num_ide', sortable: false },
        { name: 'Pri_nom', align: 'center', label: 'Pri_nom', field: 'Pri_nom', sortable: false, format: (val) => val?.toUpperCase() },
        { name: 'Seg_nom', align: 'center', label: 'Seg_nom', field: 'Seg_nom', sortable: false, format: (val) => val?.toUpperCase() },
        { name: 'Pri_ape', align: 'center', label: 'Pri_ape', field: 'Pri_ape', sortable: false, format: (val) => val?.toUpperCase() },
        { name: 'Seg_ape', align: 'center', label: 'Seg_ape', field: 'Seg_ape', sortable: false, format: (val) => val?.toUpperCase() },
        { name: 'Cod_eve', align: 'center', label: 'Cod eve', field: 'Cod_eve', sortable: false },
        { name: 'Cod_postal', align: 'center', label: 'Evento', field: 'Cod_postal', sortable: false,style:'table-layout: fixed;white-space: normal;' },
        { name: 'Año', align: 'center', label: 'Año', field: 'Año', sortable: false },
        { name: 'Semana', align: 'center', label: 'Semana', field: 'Semana', sortable: false },
        { name: 'Cod_pre', align: 'center', label: 'Cod_pre', field: 'Cod_pre', sortable: false },
        { name: 'Cod_sub', align: 'center', label: 'Cod_sub', field: 'Cod_sub', sortable: false },
        { name: 'Ajuste', align: 'center', label: 'Ajuste', field: 'Ajuste', sortable: false },
        {name: 'Fec_aju', align: 'center', label: 'Fec_aju', field: 'Fec_aju', sortable: false, format: (val) => {
            var dateTime = new Date(val);
            return dateTime = moment(dateTime).format("DD/MM/YYYY");
          }
        },
      ],
      data: [],
      original: [],
      filas: 0,
      busq: false,
      drawerLeft: false,
      registrosEnPagina:'',
      registrosLocales:0,
      sincronizando:false,
      ingresaPrimeraVez:true
    }
  },

  async mounted() {
    this.loading = true;
    // this.onRequest({pagination: this.pagination,filter: undefined});
    this.$emit("disenoPagina", true);
    await this.enumerarEventosLocales();
  },

  methods: {

    enviar(id) {
      let ruta = "Individuales/" + id;
      this.$router.push(ruta);
    },

    async onRequest(props) {
      props.pagination.rowsPerPage = this.initialPagination.rowsPerPage;
      props.pagination.page = props.pagination.page == 0 ? 1 : props.pagination.page;
      const { page, rowsPerPage} = props.pagination;
      const fetchCount = rowsPerPage === 0 ? this.pagination.rowsNumber : rowsPerPage

      // calculate starting row of data
      const startRow = (page - 1) * rowsPerPage

      let filtros = {};
      if(props.filter != '' && props.filter != undefined){
        const filtro = this.$refs['buscador'].buscaDesdeBarra(props.filter);
        filtros = {filtros,...filtro};
      }else if(props.filtro != undefined && props.filtro != null){
        filtros = props.filtro;
      }

      await this.fetchData(startRow, fetchCount,filtros,page);
    },

    setRowColor(currentRow){
      let rowClass = '';
      if (currentRow.tmp == 1) //Registro pendiente por sincronizar online
        rowClass += ' registroTemporal ';
      else if (currentRow.SyncError == 1) // Registro con error de sincronizacion
        rowClass += ' noSincronizado ';
      else if (currentRow.Est_ingr == 2 && (currentRow.Cod_eve == '550' || currentRow.Cod_eve == '560')) //Registro de mortalidad materna 
        rowClass += ' registroPendiente ';
      return rowClass;
    },

    async fetchData(startRow, count, parametros,page){
      // await this.sincronizarEventos();
      await new Promise(resolve => setTimeout(resolve, 100));
      if(global.isOnline){
        await this.fetchFromServer(startRow, count, parametros,page);
      }else{
        parametros = {startRow, numeroReg:count, ...parametros};
        try{
          const registrosLocales = await GridIndividualesProvider.retornarIndividualesLocales(parametros); 
          const registrosConError = await this.obtenerRegistrosConError();
          this.data = [...registrosConError,...registrosLocales]; //7 agregar registros con error aa los registros de la grid
          this.pagination.rowsNumber = this.data.length > 0 ? this.data.length : 0;
          this.pagination.page = page.toString() == '0' ? 1 : page;
          this.pagination.rowsPerPage = this.pagination.rowsPerPage + registrosConError.length;
          this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
          this.loading = false;
          this.registrosEnPagina = this.pagination.page*this.pagination.rowsPerPage>this.pagination.rowsNumber ?
            this.pagination.rowsNumber : this.pagination.page*this.initialPagination.rowsPerPage + registrosConError.length;
        }catch(err){
          console.log(err);
        }

      }
    },

    // Filtra solo por las llaves dadas
    async fetchFromServer(startRow, count, parametros,page) {
      this.loading = true; 
      parametros = {startRow, numeroReg:count, ...parametros};
      try {
        const res = await GridIndividualesProvider.retornarIndividuales(parametros);
        const registrosLocales = res.data;
        const registrosConError = await this.obtenerRegistrosConError();
        this.data = [...registrosConError,...registrosLocales]; //6 agregar registros con error aa los registros de la grid ,
        this.pagination.rowsPerPage = this.pagination.rowsPerPage + registrosConError.length;
        this.pagination.rowsNumber = this.data.length > 0 ? this.data[this.data.length-1].Oportuna : 0;
        this.pagination.page = page.toString() == '0' ? 1 : page;
        this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
        this.loading = false;
        this.registrosEnPagina = this.pagination.page*this.pagination.rowsPerPage>this.pagination.rowsNumber ? 
                                 this.pagination.rowsNumber : this.pagination.page*this.initialPagination.rowsPerPage + registrosConError.length;
        if(global.AlertaRuaf)this.$modal.alertar({titulo: "Atención", mensaje: "Tiene "+this.data[0].Semanal+" registros pendientes por completar", tipo: 3})
        global.AlertaRuaf = false;
      } catch(err) {
        console.log(err);
        if (err.response.status == 401) {
          this.$modal.alertar({
            titulo: "Atención", mensaje: "Sesión vencida, por favor vuelva a ingresar", tipo: 2, onConfirm: () => {
              window.location.href = global.siv40;
            }
          })
        }
      }
    },

    async obtenerRegistrosConError(){
      const data = await GridIndividualesProvider.retornarIndividualesSyncError(); //5 Crea función de retornar registros con error
      return data;
    },

    search() {
      this.drawerLeft = !this.drawerLeft;
      // if(this.drawerLeft){
      //   this.filter = '';
      // }
    },

    /**
     * Busca filas que tengan los valores dados en las llaves dadas
     */
    buscar() {
      // valor.limitaRegistros = '0';
      this.filter = '';
      this.fetchData(0,50,{limitaRegistros:'0',...this.filtro},0);
      this.loading = false;
      this.search();
    },
    avanza(){
      this.onRequest({pagination: this.pagination,filter:this.filter,filtro:this.filtro});
    },
    reinicio() {
      this.filtro = null;
      this.onRequest({pagination: this.pagination,filter: undefined})
    },
    limpiarAvanzada(){
      this.filtro = null;
      this.$refs.buscador.reiniciarSinEmitir();
    },
    setFiltro(objeto){
      this.filtro = objeto;
    },

    async enumerarEventosLocales(){
      this.registrosLocales = await GridIndividualesProvider.enumerarEventosLocales();
    },

    // async sincronizarEventos(){
    //   this.sincronizando = true;
    //   await sincro.enviarIndividuales();
    //   this.sincronizando = false;
    //   await this.enumerarEventosLocales();
    // },

    mirarDatos(){
      console.log(this.data);
    },
    copyDataToClipboard(data){
      navigator.clipboard.writeText(JSON.stringify(data));
      Notify.create({
          message: 'Datos copiados al portapapeles',
          type: 'positive'
      })
    }

  }
}
