import { Dialog } from 'quasar';
import Dialogo from './Dialogo.vue';

let isDialogVisible = false;

export function showDialog(onOkEvent, onHideEvent, onCancelEvent = null) {
    if (!isDialogVisible) {
        isDialogVisible = true;
        Dialog.create({
            component: Dialogo,
            parent: this
        }).onOk(() => {
            isDialogVisible = false;
            onOkEvent();
        }).onCancel(() => {
            isDialogVisible = false;
            if (onCancelEvent != null) onCancelEvent();
        }).onDismiss(() => {
            isDialogVisible = false;
            onHideEvent();
        })
    }
}