    /**
     * Método que muestra un mensaje de error si no se cumple los requisitos del intervalo permitido
     * @param {*} mensaje 
     * @param {*} valor 
     * @param {*} fechaNacimiento 
     * @param {*} valorCondicion 
     */
    valorDependienteEdadMeses: function (mensaje, valor, edad, valorCondicion, unidadMedida) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: true,
                mensaje: null
            };
            try {
                if (!Comun.tieneValor(valor)) { resolve(retorno); return retorno; }
                let rawParams = valorCondicion.split(",");
                let meses;
                switch (unidadMedida) {
                    case '1':
                        meses = Comun.aniosAMeses(edad);
                        break;
                    case '2':
                        meses = Comun.diasAMeses(edad);
                        break;
                    case '3':
                        meses = Comun.horasAMeses(edad);
                        break;
                    case '4':
                        meses = Comun.minutosAMeses(edad);
                        break;
                    default:
                        meses = -1;
                        break;
                }
                if (parseFloat(rawParams[0]) <= meses && parseFloat(rawParams[1]) >= meses && parseInt(valor) == parseInt(rawParams[2])) {
                    retorno.valido = false;
                    retorno.mensaje = mensaje;
                }
                else {
                    retorno.valido = true;
                    retorno.mensaje = "";
                }
                //console.log("valorDependienteEdadMeses retorno ", retorno)  
                resolve(retorno);

            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },
    /**
     * Método que valida que las dosis de vacuna sean aceptables para la edad del paciente
     * @param {*} mensaje 
     * @param {*} valor 
     * @param {*} edad 
     * @param {*} unidadMedida 
     * @param {*} rangosCondiciones 
     * @param {*} valorDosisVacuna 
     */
    validarValorDependienteMayorEdadUnidad: function (mensaje, valor, edad, unidadMedida, rangosCondiciones, valorDosisVacuna) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: true,
                mensaje: null
            };
            try {
                if (!Comun.tieneValor(valor)) {
                    resolve(retorno);
                    return retorno;
                }

                //conversión de la edad a meses para realizar la comparación
                var edadEnMeses = 0;
                if (unidadMedida == 1) {
                    edadEnMeses = parseInt(edad) * 12;
                }
                else if (unidadMedida == 3) {
                    edadEnMeses = parseInt(edad) / 30;
                }
                else if (unidadMedida == 4) {
                    edadEnMeses = parseInt(edad) / 720;
                }
                else if (unidadMedida == 5) {
                    edadEnMeses = parseInt(edad) / 43200;
                }
                else {
                    edadEnMeses = parseInt(edad);
                }
                let condicion = rangosCondiciones.split('/');
                var i = 0;
                for (i = 0; i < condicion.length; i++) {
                    let condicionIntervalo = condicion[i].split(',')
                    if (valorDosisVacuna == condicionIntervalo[1]) {
                        if (parseInt(edadEnMeses) >= parseInt(condicionIntervalo[0])) {
                            retorno.valido = true;
                            retorno.mensaje = "-1";
                            retorno.mensajePersonalizado = "";
                        }
                        else {
                            i = condicion.length;
                            if (valorDosisVacuna == 1) {
                                mensaje = mensaje + "\nUna dosis de vacuna: Solo a mayores o igual a 2 meses de edad"
                            }
                            else if (valorDosisVacuna == 2) {
                                mensaje = mensaje + "\nDos dosis de vacuna: Solo a mayores o igual a 4 meses de edad"
                            }
                            else if (valorDosisVacuna == 3) {
                                mensaje = mensaje + "\nTres dosis de vacuna: Solo a mayores o igual a 6 meses de edad"
                            }
                            else if (valorDosisVacuna == 4) {
                                mensaje = mensaje + "\nPrimer refuerzo: Solo a mayores o igual a " + condicionIntervalo[0] + " meses de edad"
                            }
                            else if (valorDosisVacuna == 5) {
                                mensaje = mensaje + "\nSegundo refuerzo: Solo a mayores o igual a 5 años de edad"
                            }
                            mensaje = mensaje + "\n\nEn la ficha de datos básicos usted digitó unidad de medida = " + unidadMedida + " y edad = " + edad
                            retorno.valido = false;
                            retorno.mensaje = "-1";
                            retorno.mensajePersonalizado = mensaje;
                        }
                    }
                }
                //console.log("validarValorDependienteMayorEdadUnidad retorno ", retorno)
                resolve(retorno);
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },

