    validarOpcionVsEdad: function (mensaje, valor, valorAValidar, edad, unidadMedida, edadValida, unidadMedidaValida, validacion) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: true,
                mensaje: null
            };
            try {
                //Si el valor enviado en el campo a evaluar NO es el que activa la resrticción o esta vacio, se omite la validacion
                if ((!Comun.tieneValor(valor)) || (valor != valorAValidar)) { resolve(retorno); return retorno; }
                const edadEnAnos = Comun.convertirEdadAAnos(edad,unidadMedida);
                const edadValidaEnAnos = Comun.convertirEdadAAnos(edadValida,unidadMedidaValida);
                if ((validacion=="menor" && edadEnAnos > edadValidaEnAnos) || (validacion=="mayor" && edadEnAnos < edadValidaEnAnos)){
                    retorno.valido = false;
                    retorno.mensaje = mensaje;
                }
                resolve(retorno);
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = e.prototype.message;
                reject(retorno);
            }
        });
    },
