    /**
     * Método que determina que por lo menos en un grupo existan check seleccionados
     * @param  {...any} args 
     */
    validarMinimoCamposChequeados: function (mensaje, valor, opcionesNecesarias, ...opciones) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: null,
                mensaje: null
            };
            try {
                const tamanio = opciones.filter(x => x == 1).length; // Ubica cuantas opciones hay checkeadas
                if (tamanio != undefined && opcionesNecesarias != undefined) {
                    if (tamanio >= parseInt(opcionesNecesarias)) { //si hay el número suficiente de opciones checkeadas pasa la validación
                        retorno.valido = true;
                        retorno.mensaje = "-1";
                        retorno.mensajePersonalizado = "";
                    }
                    else {
                        retorno.valido = false;
                        retorno.mensaje = "-1";
                        retorno.mensajePersonalizado = mensaje;
                    }
                }
                else {
                    retorno.valido = false;
                    retorno.mensaje = "-1";
                    retorno.mensajePersonalizado = mensaje;
                }
                resolve(retorno);
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },
