                let segundoGrupo = args.slice(args.indexOf("NuevoGrupo") + 2, args.length);
                let cantidadSegundoGrupo = args.slice(args.indexOf("NuevoGrupo") + 1, args.indexOf("NuevoGrupo") + 2);
                // suma los campos marcados
