    /**
     * Método que valida que un valor no pueda ser <=, <, >=, >, ==, != dependiendo de uno o varios valores
     * @param {*} valor 
     * @param {*} inputControl 
     * @param  {...any} args 
     */
    ValidarRelacionCantidadEntreDosCampos: function (mensaje, inputControl, ...args) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: null,
                mensaje: null
            };
            try {
                //Variable array para la condición
                var arrayCondicion = args[1].split(',');
                //Valida que el valor digitado no se encuentre en el rango no permitido
                if (arrayCondicion.includes(args[0])) {
                    switch (args[3]) {
                        case 'menor igual':
                            if (inputControl <= parseFloat(args[2])) {
                                retorno.valido = false;
                                retorno.mensaje = mensaje;
                            }
                            break;
                        case 'menor':
                            if (inputControl < parseFloat(args[2])) {
                                retorno.valido = false;
                                retorno.mensaje = mensaje;
                            }
                            break;
                        case 'mayor igual':
                            if (inputControl >= parseFloat(args[2])) {
                                retorno.valido = false;
                                retorno.mensaje = mensaje;
                            }
                            break;
                        case 'mayor':
                            if (inputControl > parseFloat(args[2])) {
                                retorno.valido = false;
                                retorno.mensaje = mensaje;
                            }
                            break;
                        case 'igual':
                            if (inputControl == parseFloat(args[2])) {
                                retorno.valido = false;
                                retorno.mensaje = mensaje;
                            }
                            break;
                        default:
                            if (inputControl != parseFloat(args[2])) {
                                retorno.valido = false;
                                retorno.mensaje = mensaje;
                            }
                            break;
                    }
                }
                else {
                    retorno.valido = true;
                    retorno.mensaje = "";
                }
                //console.log("ValidarRelacionCantidadEntreDosCampos retorno ", retorno)
                resolve(retorno);
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },
