    validarCamposExcluyentes: function (...args) {
        const valorCondicion = args[args.length - 1]
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: null,
                mensaje: null
            };
            try {
                let rawParams = args.slice(2, args.length - 1);
                var i = 0;
                var cantidadTotal = 0;
                for (i = 0; i < rawParams.length; i++) {
                    if (rawParams[i] == parseInt(valorCondicion)) {
                        cantidadTotal = cantidadTotal + 1;
                    }
                }
                if (cantidadTotal >= 2) {
                    retorno.valido = false;
                    retorno.mensaje = "-1";
                    retorno.mensajePersonalizado = args[0];
                }
                else {
                    retorno.valido = true;
                    retorno.mensaje = "";
                    retorno.mensajePersonalizado = "";
                }
                //console.log("validarCamposExcluyentes retorno ", retorno)
                resolve(retorno);
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },
