    /**
     * Método que valida un rango de edad de acuerdo al evento a crear o modificar
     * @param {*} mensaje 
     * @param {*} check 
     * @param {*} evento 
     * @param {*} edad 
     * @param {*} unidadMedida
     */
    validarEdadDeAcuerdoEvento: function (mensaje, check, evento, edad, unidadMedida, tipoEvento) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: true,
                mensaje: ''
            };
            try {
                const variablesFormulario = tipoEvento; //variablesFormulario que se asignan en individuales.js
                const anos = Comun.convertirEdadAAnos(parseInt(edad), unidadMedida);
                const grupoEtareo = Comun.gruposEtareos(anos).toString();
                const gruposNoAdmitidos = variablesFormulario.gru_per.toString();
                if (gruposNoAdmitidos.includes(grupoEtareo)) {
                    const msg = `Según validación del INS la edad ingresada corresponde a un grupo etareo no permitido, ` +
                    `los grupos etareos admitidos para este evento <b> se marcan en color verde: </b>` +
                    `<ol>` +
                        `<li style='color: ${gruposNoAdmitidos.includes(1) ? 'red' : 'green'}'>Menores de un año</li>` +
                        `<li style='color: ${gruposNoAdmitidos.includes(2) ? 'red' : 'green'}'>De 1 a 4 años</li>` +
                        `<li style='color: ${gruposNoAdmitidos.includes(3) ? 'red' : 'green'}'>De 5 a 14 años</li>` +
                        `<li style='color: ${gruposNoAdmitidos.includes(4) ? 'red' : 'green'}'>De 15 a 44 años</li>` +
                        `<li style='color: ${gruposNoAdmitidos.includes(5) ? 'red' : 'green'}'>De 45 a 59 años</li>` +
                        `<li style='color: ${gruposNoAdmitidos.includes(6) ? 'red' : 'green'}'>Mayores de 60 años</li>` +
                    `</ol>`;
                    retorno.valido = false;
                    retorno.mensaje = "-1";
                    retorno.mensajePersonalizado = msg
                }
                else {
                    retorno.valido = true;
                    retorno.mensaje = "";
                    retorno.mensajePersonalizado = "";
                }
                resolve(retorno);
            } catch (e) {
                console.log(e);
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },
    /**
     * Método para validar un rango de edades específico en un evento
     * @param {*} mensaje 
     * @param {*} check 
     * @param {*} evento 
     * @param {*} edad 
     * @param {*} unidadMedida 
     * @param {*} condicionEvento 
     */
    validarEdadesEspecíficasDeAcuerdoEvento: function (mensaje, check, evento, edad, unidadMedida, condicionEvento) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: null,
                mensaje: null
            };
            try {
                if (edad != "") {
                    let rawParams = condicionEvento.split(",");
                    let edadesPermitidas = rawParams.slice(2, rawParams.length)
                    if (rawParams[0] == evento) {
                        if (rawParams[1] == unidadMedida) {
                            if (edadesPermitidas.includes(edad.toString())) {
                                retorno.valido = true;
                                retorno.mensaje = "";
                                retorno.mensajePersonalizado = "";
                            } else {
                                retorno.valido = false;
                                retorno.mensaje = "-1";
                                retorno.mensajePersonalizado = mensaje;
                            }
                        } else {
                            retorno.valido = false;
                            retorno.mensaje = "-1";
                            retorno.mensajePersonalizado = mensaje;
                        }
                    } else {
                        retorno.valido = true;
                        retorno.mensaje = "";
                        retorno.mensajePersonalizado = "";
                    }
                }
                else {
                    retorno.valido = true;
                    retorno.mensaje = "";
                    retorno.mensajePersonalizado = "";
                }
                //console.log("validarEdadesEspecíficasDeAcuerdoEvento retorno ", retorno)
                resolve(retorno);
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },
