    /**
     * Método que valida si la upgd seleccionada se encuentra activa o no
     * @param {*} mensaje 
     * @param {*} valor: valor actual del código de upgd (cod_pre + cod_sub) 
     */
    validarCodPreActiva: function (mensaje, valor) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: null,
                mensaje: null
            };
            try {
                if (!Comun.tieneValor(valor)) {
                    retorno.valido = true;
                    retorno.mensaje = "";
                    resolve(retorno);
                } else {
                    //Crear la entidad upgd a buscar
                    let params = null;
                    var codpre = "";
                    var codsub = "";
                    if (valor.length >= 11) {
                        codpre = valor.substring(0, 10);
                        codsub = valor.substring(10, 12);
                        params = { COD_PRE: codpre, COD_SUB: codsub }
                        caracterizacionProvider.consultarCodigo(params)
                            .then((res) => {
                                if (res.data.length > 0) {
                                    if (parseInt(res.data[0].ACT_SIV) != 1) {
                                        retorno.valido = false;
                                        retorno.mensaje = "UPGD Inactiva";
                                        resolve(retorno);
                                    }
                                    else {
                                        retorno.valido = true;
                                        retorno.mensaje = "";
                                        resolve(retorno);
                                    }
                                }
                                else {
                                    retorno.valido = true;
                                    retorno.mensaje = "";
                                    resolve(retorno);
                                }
                            })
                            .catch(error => {
                                reject(retorno);
                            });
                    } else {
                        retorno.valido = true;
                        resolve(retorno);
                    }
                }
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },
