    /**
     * Método que valida que el campo de municipio escogido pertenezca a la upgd seleccionada
     * @param {*} mensaje 
     * @param {*} cod_mun 
     * @param {*} cod_pre 
     * @returns 
     */
    validarMunicipioContraUPGD: function (mensaje, cod_mun, cod_pre) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: null,
                mensaje: null
            };
            try {
                //Por defecto se deja en 5 ya que sólo para el caso de Bogotá se tomará solo dos dígitos
                var cortarCadena = 5;
                //Verifica que municipio no sea vacío
                if (cod_mun == undefined && cod_mun != "") {
                    retorno.valido = false;
                    retorno.mensaje = "-1";
                    retorno.mensajePersonalizado = mensaje;
                }
                else {
                    //Pregunta si se encuentra con el caso de Bogotá para cortar la cadena a dos
                    if (cod_mun.startsWith("11")) {
                        cortarCadena = 2;
                    }
                    //Compara las dos cadenas en los dígitos necesarios
                    if (parseInt(cod_mun.substring(0, cortarCadena)) == parseInt(cod_pre.substring(0, cortarCadena))) {
                        retorno.valido = true;
                        retorno.mensaje = "";
                        retorno.mensajePersonalizado = "";
                    }
                    //Si no encuentra errores devuelve un falso no debe ir nunca nulo
                    else {
                        retorno.valido = false;
                        retorno.mensaje = "-1";
                        retorno.mensajePersonalizado = mensaje;
                    }
                }
                //console.log("validarMunicipioContraUPGD retorno ", retorno)
                resolve(retorno);
            } catch (e) {
                retorno.valido = true;
                retorno.mensaje = '';
                reject(retorno);
            }
        });
    },

