    /**
 * Método que valida que varias opciones de varios controles no puedan ser seleccionados de acuerdo al rango de edad en meses del paciente
 * @param {*} mensaje 
 * @param {*} valor 
 * @param {*} edad 
 * @param {*} unidadMedida 
 * @param {*} rangosCondiciones 
 * @param {*} valorComparar 
 */
    valoresPermitidosDependiendoEdad: function (mensaje, valor, edad, unidadMedida, edadMenorAEnMeses, valoresNoAdmitidos, ...campos) {
        return new Promise((resolve, reject) => {
            let retorno = {
                valido: true,
                mensaje: null
            };
            try {
                if (!Comun.tieneValor(valor)) { resolve(retorno); return retorno; }
                //conversión de la edad a meses para realizar la comparación
                const noAdmitidosDivididos = valoresNoAdmitidos.split('/');
                var edadMeses = 0;
                if (unidadMedida == 1) {
                    edadMeses = parseInt(edad) * 12;
                }
                else if (unidadMedida == 3) {
                    edadMeses = parseInt(edad) / 30;
                }
                else if (unidadMedida == 4) {
                    edadMeses = parseInt(edad) / 720;
                }
                else if (unidadMedida == 5) {
                    edadMeses = parseInt(edad) / 43200;
                }
                else {
                    edadMeses = parseInt(edad);
                }

                if (edadMeses < edadMenorAEnMeses) {
                    const camposValidados = [...campos];
                    camposValidados.forEach((campo, index) => {
                        const arregloOpciones = noAdmitidosDivididos[index].split(',');
                        if (arregloOpciones.includes(campo)) {
                            retorno.valido = false;
                            retorno.mensaje = mensaje;
                            retorno.mensajePersonalizado = mensaje;
                            resolve(retorno);
                            return retorno;
                        }
                    })
                } else {
                    retorno.valido = true;
                    retorno.mensaje = ""
                    retorno.mensajePersonalizado = mensaje;
                }
                resolve(retorno);
            } catch (ex) {
                retorno.valido = true;
                retorno.mensaje = "";
                reject(retorno);
            }
        })
    },

