
export default {
    name:'TRadioGroupControl',
    props:['options','deshabilitar', "valorCampo","hayError","mensajeError"],
    data(){
        return{
            valorCrudo: this.valorCampo,
        }
    },
    methods:{
        emitirSeleccion() {
            this.$emit("seleccion",this.valor);
        }
    },
    computed: {
      valor: {
        get () {
          return this.valorCrudo == null ? "" : this.valorCrudo
        },
        set (val) {
            this.valorCrudo = val        
        }
      }
    }
}
