      desactFecAjus: true, //desactivar campos de fecha ajuste
      desactRes: false, //desactivar campos de fecha resultado
      desactcamRes: false, //desactivar campos de resultado
      desactVal: false, //desactivar campos de valor resultado
      reqfec_exa: false, //requeridos para fechas
