    /**
    * Recibe la obligatoriedad de la fecha de recepción dado el código de evento
    */
    obtenerFechaRecepcion() {
      LaboratoriosProvider.obtenerRequerido(this.fecharecepcion).then((res) => {
        let captura = res.data;
        if (captura.length == 0) { //encontre uno que no necesita esta fecha
          if (this.ajustar == false) {//Se está realizando un ajuste y por tanto no es requerido
            this.reqfec_rec = false;
          }
          else {
            this.reqfec_rec = true;
          }
        }
        else {
          this.reqfec_rec = false;
        }

      })
    },
    /**
  * Recibe la obligatoriedad de la fecha de examen dado el código de evento
  */
    obtenerFechaExamen() {
      LaboratoriosProvider.obtenerRequerido(this.fechaexa).then((res) => {
        let captura = res.data;
        if (captura.length == 0) { //encontre uno que no necesita esta fecha
          if (this.ajustar == false) {//Se está haciendo ajuste, este campo no es requerido
            this.reqfec_exa = false;
          }
          else {
            this.reqfec_exa = true;
          }
        }
        else {
          this.reqfec_exa = false;
        }

      })
    },
    /**
 * Devuelve el mensaje de error dependiendo del campo y del error del campo
 * @param {*} campo 
 */
    mensajeError(campo) {
      if (campo === 'Ano') {
        if (!this.$v.form.Ano.required) return 'Campo requerido'
        if (!this.$v.form.Ano.validYear) return 'Año inválido, el dato digitado debe corresponder a la vigencia o al año inmediatamente anterior.'
        if (!this.$v.form.Ano.minLength) return 'El campo debe tener 4 caractéres'
      }
      if (campo === 'Semana') {
        if (!this.$v.form.Semana.required) return 'Campo requerido'

      }
      if (campo === 'Tip_ide') {
        if (!this.$v.Tip_ide.required) return 'Campo requerido'

      }
      if (campo === 'Num_ide') {
        if (!this.$v.form.Num_ide.required) return 'Campo requerido'
      }
      if (campo === 'Pri_nom') {
        if (!this.$v.form.Pri_nom.required) return 'Campo requerido'
        if (!this.$v.form.Pri_nom.minLength) return 'El campo debe tener mínimo 2 caractéres'
      }
      if (campo === 'Seg_nom') {
        if (!this.$v.form.Seg_nom.minLength) return 'El campo debe tener mínimo 2 caractéres'
      }
      if (campo === 'Pri_ape') {
        if (!this.$v.form.Pri_ape.required) return 'Campo requerido'
        if (!this.$v.form.Pri_ape.minLength) return 'El campo debe tener mínimo 2 caractéres'
      }
      if (campo === 'Seg_ape') {
        if (!this.$v.form.Seg_ape.minLength) return 'El campo debe tener mínimo 2 caractéres'
      }
      if (campo === 'Telefono') {
        if (!this.$v.form.Telefono.required) return 'Campo requerido'
        if (!this.$v.form.Telefono.minLength) return 'El campo debe tener mínimo 7 caractéres'
        if (!this.$v.form.Telefono.minLength) return 'El campo debe tener máximo 12 caractéres'
      }
      if (campo === 'Valor') {
        if (!this.$v.form.Valor.isNameValid) return 'El campo solo puede tener letras y números'
      }
      if (campo === 'Direccion') {
        if (!this.$v.form.Direccion.isNameValid) return 'El campo solo puede tener letras, números o el caracter numeral (#)'
        if (!this.$v.form.Direccion.required) return 'Campo requerido'
      }
      if (campo === 'Tip_reg_sa') {
        if (!this.$v.form.Tip_reg_sa.required) return 'Campo requerido'

      }
      if (campo === 'Fec_exa') {
        if (!this.$v.form.Fec_exa.req) return 'Campo requerido'
        if (!this.$v.form.Fec_exa.minLength) return 'El campo debe tener mínimo 10 caractéres'
        if (!this.$v.form.Fec_exa.beforeCurrentYear) return 'Fecha invalida, la fecha no puede ser superior a la fecha actual'
        if (!this.$v.form.Fec_exa.isDate) return 'Formato de fecha inválido'
      }
      if (campo === 'Fec_rec') {
        if (!this.$v.form.Fec_rec.req) return 'Campo requerido'
        if (!this.$v.form.Fec_rec.minLength) return 'El campo debe tener mínimo 10 caractéres'
        if (!this.$v.form.Fec_rec.beforeCurrentYear) return 'Fecha invalida, la fecha no puede ser superior a la fecha actual'
        if (!this.$v.form.Fec_rec.isDate) return 'Formato de fecha inválido'
        if (!this.$v.form.Fec_rec.afterDate) return 'La fecha de recepción debe ser superior a la de toma del examen.'
      }
      if (campo === 'Fec_exp') {
        if (!this.$v.form.Fec_exp.req) return 'Campo requerido'
        if (!this.$v.form.Fec_exp.minLength) return 'El campo debe tener mínimo 10 caractéres'
        if (!this.$v.form.Fec_exp.beforeCurrentYear) return 'Fecha invalida, la fecha no puede ser superior a la fecha actual'
        if (!this.$v.form.Fec_exp.isDate) return 'Formato de fecha inválido'
        if (!this.$v.form.Fec_exp.afterExa) return 'La fecha de resultado debe ser superior a la de toma del examen.'
        if (!this.$v.form.Fec_exp.afterRec) return 'La fecha de resultado debe ser superior a la de recepción.'
      }
    },
