    /*
    Modificado por: Wilson aguilar
    Fecha: 25/10/2023
    Documenta la constante y cambia la especificación de TI.exprEspecial

    Un tipo de documento de identidad se caracteriza por:
        longitud: 
            longitud máxima permitida para el número de documento. Para los documentos de personas
            extranjeras, el número máximo asginado tiene en cuenta 3 caracteres que se deben usar para anteponer el código de país ;
            de acuerdo a lo establecido en la circular 24 de 2019 del Ministerio de Proctección social.

        exprNormal: 
            expresión regular que establece los caracteres admisibles para el número de documento cuando no se incluye en él la
            especificación del número de hijo(s)

        exprEspecial: 
            expresión regular que establece los caracteres admisibles para el número de documento cuando en él se incluye la
            especificación del número de hijo(s)

        mensajeLongitud: 
            mensaje de error cuando el número de identidad supera el número de caracteres máximo permitido para él

        formato: 
            mensaje de error cuando el número de identidad no satisface la especificación del formato establecido para él

        esExtrangero: 
            indica si el tipo de documento aplica solo para personas extranjeras 
    */

