import axios from "axios";
import global from "../Config";
//import comun from "../utilidades/comun/metodosComunes"

//let token = comun.obtenerToken();
//console.log(token);

let headers =  {
    "Content-Type":"application/json"
}

class SeguimientosService {

    /**
     * Método que obtiene la lista de todos los seguimientos de un contacto.
     * @param {*} params
     */
    ConsultarSeguimientosContacto(params) {
        return axios.post(global.apiUrl + "contactos/ConsultarSeguimientosContacto",params,{headers})
    }


    /**
     * Método que guarda un nuevo seguimiento de un contacto.
     * @param {*} params
     */
    GuardarSeguimientoContacto(params) {
        return axios.post(global.apiUrl + "contactos/guardarSeguimientoContacto",params,{headers})
    }

}

export default new SeguimientosService();