import axios from "axios";
import global from "../Config";
import {db} from "../utilidades/offline/dbModule";

class ColectivosService {

    obtenerComplementarioColectivo(params) {
        return axios.post(global.apiUrl + "Colectivos/obtenerColectivo",params,{headers:global.headersToken})
    }

    guardarDatosColectivos(params,tipoColectivo) {  
        if(tipoColectivo == 's'){
            return axios.post(global.apiUrl + "SvcCrud/GuardarColectivosSemana",params ,{headers:global.headersToken})
        } else if(tipoColectivo == 'm'){
            return axios.post(global.apiUrl + "SvcCrud/GuardarColectivosMes",params ,{headers:global.headersToken})
        } else if(tipoColectivo == 'b'){
            return axios.post(global.apiUrl + "SvcCrud/GuardarBrotes",params ,{headers:global.headersToken})
        }
    }

    async guardarColectivosLocal(params,tipoColectivo){
        let res = {data:{response:true}};
        try{
            switch(tipoColectivo){
                case 's':
                    await db.EventosColectivosSemana.add(params);
                break
                case 'm':
                    await db.EventosColectivosMes.add(params);
                break
                case 'b':
                    await db.Brotes.add(params);
                break
            }
            return res
        }catch(err){
            console.log(err);
            res.data.response = false;
            res.data.mensaje = "No pudo guardarse el registro, por favor consulte al administrador del sistema";
            return res;
        }
    }

}


export default new ColectivosService();