import InputControl from "../../componentes/controles/InputControl";
import DropDownControl from "../../componentes/utilidad/DropDownControlUtil";
import ComunProvider from "../../providers/ComunProvider";
import metodosComunes from "../../utilidades/comun/metodosComunes";
import imprimir from "../../utilidades/impresion/imprimir"
import * as moment from "moment/moment";
import IndividualesProvider from "../../providers/IndividualesProvider";
import LaboratoriosProvider from "../../providers/LaboratoriosProvider";
import global from "../../Config";
import fechasValida from "../../utilidades/validaciones/libreriaValidaciones/fechasValida";
import { Notify } from 'quasar'
import VueOnlineOffline from "vue-online-offline";

export default {
  name: "Individuales",
  components: { VueOnlineOffline, InputControl, DropDownControl },
  props: ['idEvento'],
  data() {
    return {
      formularioTocado: false,
      guardando: false,
      inactivar: false,
      capturaVariables: {},
      capturaVariablesComp: {},
      listaEventos: null,
      metaDatos: [],
      metaDatosComp: [],
      eventoPermitido: true,
      tab: "basicos",
      load: false,
      loadComp: false,
      alert: false,
      msg: null,
      envio: false,
      cod_eve: null,
      cod_sub: null,
      tieneLabs: false,
      rawOmitirBDUA: false,
      tienecomplementarios: false,
      eventoCrudo: this.idEvento,
      camposComplementarios: [],
      camposbdua: [],
      codigoBDUA: null,
      codigoHex: null,
      tablas: [],
      tablasComp: [],
      ajustar: (this.idEvento == 0 || this.idEvento.startsWith("OF")),
      puedeEjecutarAfectantes: false,
      cargadosAntes: false,
      formularioDebeSerTocado: true,
      camposFechaFormatear: [],
      inabilitaAjustar: false,
      variablesFormulario: {
        cod_eve: null,
        complementariosCargados: false,
        IndividualesCargados: false,
        ajustar: (this.idEvento == 0 || this.idEvento.startsWith("OF")),
        idEvento: this.idEvento,
        cambioTab: false,
        esNuevo: this.idEvento.toString() == '0',
        bduaCargado: true,
        propiedadesDelEvento: null,
        est_ing:null,
      },
      labelsImpresion: null,
      textosListas: {},
      idLatLng: "",
      puedeSalir: true,
      propiedadesDelEvento: null,
      clasificacionAlGuardar: null,
      requerirLaboratorios: false //variable que indica si se debe redirigir al formulario de laboratorios o no
    };
  },
  computed: {
    evento: {
      get() {
        return this.eventoCrudo
      },
      set(val) {
        this.eventoCrudo = val
      }
    },
    omitirBdua: {
      get() {
        return this.rawOmitirBDUA
      },
      set(val) {
        this.rawOmitirBDUA = val
      }
    }
  },
  methods: {
    establecerLatLng(place_id_lat_lng) {
      if (place_id_lat_lng != undefined && place_id_lat_lng != "")
        this.idLatLng = place_id_lat_lng
    },
    cambiarEstadoAjuste() {
      this.puedeSalir = false;
      let this_ = this;
      this.variablesFormulario.IndividualesCargados = false;
      this.ajustar = true;
      this.variablesFormulario.ajustar = true
      this.$refs.Ajuste[0].deshabilitar = false
      this.$refs.Ajuste[0].valor = ''
      this.$refs.Ajuste[0].$el.scrollIntoView({ block: 'end', scrollBehavior: 'smooth' });
      setTimeout(function () {
        this_.variablesFormulario.IndividualesCargados = true;
      }, 500);
    },
    Unificidad(evento) {
      this.variablesFormulario.IndividualesCargados = false;
      Object.keys(this.$refs).filter((x) => x.match(/^[A-Z].*/)).forEach(element => {
        if (evento[0][element] != null && element != 'Cod_eve' && element != 'Ajuste' && element != "Cod_pre" && element != "Cod_sub") {
          this.$refs[element][0].valor = evento[0][element]
        }
        if (element == "Nom_dil_fi") { //Eliminacion del codigo de retencion del bdua
          var matches = evento[0][element].match(/\[(.*?)\]/);
          if (matches) {
            this.$refs[element][0].valor = evento[0][element].split(" SISSIV[")[0];
          } else {
            this.$refs[element][0].valor = evento[0][element]
          }
        }
        if (element == "Cod_mun_r") { //modificacion del codigo de municipio para adjuntar el codigo de dpto
          this.$refs[element][0].valor = evento[0]['Cod_dpto_r'] + evento[0][element]
        }
        if (element == "Cod_mun_o") {
          this.$refs[element][0].valor = evento[0]['Cod_dpto_o'] + evento[0][element]
        }
      });
      var campos = Object.keys(this.capturaVariables);
      this.ejecutarAfectantes(campos, 'unificidad').then(() => {
        this.variablesFormulario.IndividualesCargados = true;
      });
    },
    retornarDatosDelPaciente(datosDelPaciente) {
      Object.keys(datosDelPaciente).forEach(element => {
        if (datosDelPaciente[element] != null) {
          this.$refs[element][0].valor = datosDelPaciente[element]
        }
      });
      if (Object.keys(datosDelPaciente).length != 0) {
        this.notificar('Se han recuperado datos existentes del paciente', 5000, 1)
        setTimeout(() => {
          this.$refs.Edad[0].validar();
          this.$refs.Ocupacion[0].valor = datosDelPaciente.Ocupacion
          this.$refs.Edad[0].encadenaAfectantes = true;
        }, 500);
      } else {
        this.$refs.Edad[0].encadenaAfectantes = true;
      }
    },
    Bdua(datosBdua) {
      if (global.isOnline) {
        this.variablesFormulario.bduaCargado = false;
        this.retencionBdua('Tip_ide', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        this.retencionBdua('Num_ide', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        this.$refs.Pri_nom[0].valor = datosBdua.PrimerNombre ?? this.$refs.Pri_nom[0].valor
        this.$refs.Seg_nom[0].valor = datosBdua.SegundoNombre ?? this.$refs.Seg_nom[0].valor
        this.$refs.Pri_ape[0].valor = datosBdua.PrimerApellido ?? this.$refs.Pri_ape[0].valor
        this.$refs.Seg_ape[0].valor = datosBdua.SegundoApellido ?? this.$refs.Seg_ape[0].valor
        this.$refs.Tip_ss[0].valor = datosBdua.regimen ?? this.$refs.Tip_ss[0].valor
        this.$refs.Cod_ase[0].valor = datosBdua.EPSAfiliacion ?? this.$refs.Cod_ase[0].valor
        if (metodosComunes.tieneValor(datosBdua.FechaNacimiento) && datosBdua.FechaNacimiento != this.$refs.Fecha_nto[0].valor && !this.$refs.Edad[0].encadenaAfectantes) {
          this.$refs.Edad[0].encadenaAfectantes = false;
        }
        if (!global.usarBdua) this.$refs.Fecha_nto[0].valor = datosBdua.FechaNacimiento ?? this.$refs.Fecha_nto[0].valor
        this.retencionBdua('Pri_nom', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        this.retencionBdua('Seg_nom', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        this.retencionBdua('Pri_ape', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        this.retencionBdua('Seg_ape', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        this.retencionBdua('Tip_ss', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        this.retencionBdua('Cod_ase', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        if (!global.usarBdua) this.retencionBdua('Fecha_nto', (Object.keys(datosBdua).length != 0 ? 1 : 0));
        if (datosBdua && !this.omitirBdua) {
          this.$modal.alertar({ titulo: "Atención", mensaje: 'Datos del BDUA sincronizados con exito', tipo: 1 });
        }
        setTimeout(() => { this.variablesFormulario.bduaCargado = true; }, 1);
      }
    },
    Ajustar(ajuste) {
      if (this.variablesFormulario.IndividualesCargados) {
        this.variablesFormulario.IndividualesCargados = false;
        if (ajuste.toString() == '7') {
          this.formularioDebeSerTocado = true;
          Object.keys(this.$refs).forEach(element => {
            if (element != 'Cod_eve' && element != 'Ajuste' && this.$refs[element][0] != null) {
              this.$refs[element][0].deshabilitar = false
            }
          });
          this.$refs.Fec_aju[0].deshabilitar = true;
          this.$refs.Cod_pre[0].deshabilitar = true;
          this.$refs.Tip_ide[0].deshabilitar = true;
          this.$refs.Num_ide[0].deshabilitar = true;
          this.$refs.Fec_not[0].deshabilitar = true;
          this.$refs.Semana[0].deshabilitar = true;
          this.$refs.Año[0].deshabilitar = true;
          this.$refs.Tip_cas[0].deshabilitar = true; //Se bloquea a solicitud de Veronica
          this.$refs.Nom_dil_fi[0].deshabilitar = this.$refs.Nom_dil_fi[0].valor.includes("CORONAPP MEDICOS");

          ComunProvider.obtenerFechaActual().then(res => {
            this.$refs.Fec_aju[0].valor = moment(res.data).format("DD/MM/YYYY");
          })

          var campos = Object.keys(this.capturaVariables);
          this.ejecutarAfectantes(campos, 'ajustar').then(() => {
            this.variablesFormulario.IndividualesCargados = true;
            this.bloquearCamposBdua();
          });

        } else if (ajuste != 0) {
          this.formularioDebeSerTocado = true;
          Object.keys(this.$refs).filter((x) => x.match(/^[A-Z].*/) && x != "btnGuardarB" && x != "btnGuardarC").forEach(element => {
            if (element != 'Cod_eve' && element != 'Ajuste' && this.$refs[element][0] != null) {
              this.$refs[element][0].deshabilitar = true
            }
          });
          this.variablesFormulario.IndividualesCargados = true;
        } else {
          this.variablesFormulario.IndividualesCargados = true;
        }
      }
    },
    ajustarComplementario() {
      if (this.ajustar) {
        if (this.$refs.Ajuste[0].valor != '0' && this.$refs.Ajuste[0].valor != '7') {
          let retenciones = metodosComunes.obtenerRetenciones(this.cod_eve, this.$refs.Ajuste[0].valor);
          retenciones = retenciones.map(x => x.toLowerCase());
          let campos = Object.keys(this.$refs);
          campos = campos.filter((x) => x.match(/^[a-z].*/) && x != "btnGuardarB" && x != "btnGuardarC");
          campos.forEach(element => {
            if (!retenciones.includes(element)) {
              this.$refs[element][0].deshabilitar = true;
              this.$refs[element][0].puedeValidar = false;
              this.$refs[element][0].validar();
            }
          });
        } else if (this.ajuste == '7') {
          let retenciones = metodosComunes.obtenerRetencionesEn7(this.cod_eve);
          let campos = Object.keys(this.$refs);
          campos = campos.filter((x) => x.match(/^[a-z].*/) && x != "btnGuardarB" && x != "btnGuardarC");
          campos.forEach(element => {
            if (retenciones.includes(element.toUpperCase())) {
              this.$refs[element][0].deshabilitar = true;
              this.$refs[element][0].puedeValidar = false;
              this.$refs[element][0].validar();
            } else {
              this.$refs[element][0].puedeValidar = true;
            }
          });
        }
      } else if (this.idEvento != 0 && !this.idEvento.startsWith("OF")) {
        let campos = Object.keys(this.$refs);
        campos = campos.filter((x) => x.match(/^[a-z].*/) && x != "btnGuardarB" && x != "btnGuardarC");
        campos.forEach(element => {
          this.$refs[element][0].deshabilitar = true;
        });
      }
    },
    obtenerBasico() {
      IndividualesProvider.obtenerBasico({ idEvento: this.idEvento }).then((res) => {
        this.eventoPermitido = true;
        this.capturaVariables = res.data[0];
        this.variablesFormulario.est_ing = this.capturaVariables.Est_ingr;
        this.cod_sub = this.capturaVariables.Cod_sub;
        this.$refs.Cod_eve.valor = this.capturaVariables.Cod_eve;
        this.$refs.Cod_eve.emitirSeleccion();
        //Cuando el evento es 000 negativa no se le puede permitir hacer ningún cambio
        if (res.data[0].Cod_eve == "000") {
          this.inabilitaAjustar = true;
          this.inactivar = true;
        }
        this.revisarLaboratorios(this.capturaVariables.Cod_eve); //para ver si habilitar o deshabilitar el btn
        //Se hace el envío de la variable de fecha de notificación para que comprube si se debe
        //permitir el ajuste o no dependiendo de la vigencia
        var fecNot = res.data[0].Fec_not.split("-");
        var diaNot = fecNot[2].split("T");
        var fechaNotificacion = diaNot[0] + "/" + fecNot[1] + "/" + fecNot[0];
        // se realiza la consulta para saber si debe habilitar o no el ajuste
        return new Promise((resolve,) => {
          fechasValida.fechaPeriodoActual("", fechaNotificacion, this.capturaVariables.Cod_eve).then(res => {
            //Si la respuesta de la validación es falsa, debe inhabilitar el botón de guardado y
            //no cambiarlo a ajuste
            if (!res.valido) {
              this.inabilitaAjustar = true;
              this.inactivar = true;
            }
          }, err => {
            console.log(err);
            resolve(false)
          });
        });
      }, err => {
        if (err.response.status == 403) {
          this.eventoPermitido = false;
        }
      })
    },
    establecerValor({ clave, valor, jsonAsociado, texto }) {
      if (jsonAsociado == "capturaVariablesComp" && this.variablesFormulario.complementariosCargados && !this.variablesFormulario.cambioTab) {
        this.formularioTocado = true;
      }
      if (jsonAsociado == "capturaVariables" && this[jsonAsociado][clave] != valor && this.variablesFormulario.IndividualesCargados) {
        this.retencionBdua(clave, 0);
      }
      this[jsonAsociado][clave] = valor;
      if (texto != null && texto != undefined) this.textosListas[clave] = texto;
    },
    FocoSiguienteInput(campo, indiceGrupo, indiceControl) {
      if (campo[0] == campo[0].toLowerCase()) {
        if (this.metaDatosComp[indiceGrupo].controles.length > indiceControl + 1) {
          const nombreSiguienteControl = this.metaDatosComp[indiceGrupo].controles[indiceControl + 1].campo;
          this.$refs[nombreSiguienteControl][0].$el.focus();
        } else if (this.metaDatosComp.length < indiceGrupo + 1) {
          const nombreSiguienteControl = this.metaDatosComp[indiceGrupo + 1].controles[0].campo;
          this.$refs[nombreSiguienteControl][0].$el.focus();
        } else {
          this.$refs.btnGuardarC.focus();
        }
      } else {
        if (this.metaDatos[indiceGrupo].controles.length <= indiceControl + 1) {
          const nombreSiguienteControl = this.metaDatos[indiceGrupo].controles[indiceControl + 1].campo;
          this.$refs[nombreSiguienteControl][0].$el.focus();
        } else if (this.metaDatos.length <= indiceGrupo + 1) {
          const nombreSiguienteControl = this.metaDatos[indiceGrupo + 1].controles[0].campo;
          this.$refs[nombreSiguienteControl][0].$el.focus();
        } else {
          this.$refs.btnGuardarB.focus();
        }
      }
    },
    estableceTexto({ clave, texto }) {
      if (texto != null && texto != undefined) this.textosListas[clave] = texto;
    },
    actualizarValidacion({ clave, jsonAsociado }) {
      if (this.$refs[clave][0].validaciones) {
        metodosComunes.procesarObjetoValidaciones(this.$refs[clave][0].validaciones.validacionesArreglo, this[jsonAsociado], this.capturaVariables, this.variablesFormulario, this.capturaVariablesComp)
        this.$refs[clave][0].validaciones.valor = this[jsonAsociado][clave];
      }
    },
    emitirFuncionesAfectantes(funcionesAfectantes, jsonAsociado, fuente, validacionesAfectantes) {
      if (this.puedeEjecutarAfectantes) {
        funcionesAfectantes.filter(x => x.evento == fuente).forEach(({ funcion, clave, parametro }) => {
          this.configuraFuncionalidad(clave, funcion, jsonAsociado, fuente, parametro);
        });

        if (fuente == 'change')
          validacionesAfectantes.forEach(({ clave }) => {//funcion
            this.configuraValidacion(clave); //, funcion, jsonAsociado,fuente
          });

      }
    },
    configuraFuncionalidad(clave, funcionalidad, jsonAsociado, fuente, parametro) {
      if (this.$refs[clave] != undefined && this.$refs[clave][0].funcionalidades) {
        metodosComunes.procesarObjetoFuncionalidad(this.$refs[clave][0].funcionalidades.funcionalidadesArreglo, this[jsonAsociado], funcionalidad, this.capturaVariables, this.variablesFormulario, this.capturaVariablesComp, this.$refs)
        this.$refs[clave][0].ejecutarComportamiento(funcionalidad, fuente, parametro);
      }
    },
    configuraValidacion(clave) {//, validacion, jsonAsociado,fuente
      var revisaCarga = this.tab == "complementarios" ? this.variablesFormulario.complementariosCargados : this.variablesFormulario.IndividualesCargados;
      if (this.$refs[clave] != undefined) {
        if (this.$refs[clave][0].validaciones != null && revisaCarga) {
          this.$refs[clave][0].validar();
        }
      }
    },
    verTextos() {
      console.log(this.capturaVariablesComp);
    },
    estableceEvento(evento, texto) {
      this.puedeSalir = this.idEvento == 0 ? false : true;
      this.capturaVariables = this.idEvento == 0 ? {} : this.capturaVariables;
      if (this.cod_eve != evento) {
        this.establecerPropiedadesEvento(evento);
        this.variablesFormulario.complementariosCargados = false;
        this.metaDatosComp = [];
        this.textosListas = {};
        this.cod_eve = evento
        this.variablesFormulario.cod_eve = evento
        this.obtenerFormulario(evento).then(() => {
          if (this.tab == "complementarios") {
            this.revisarTab();
          }
        });
        if (texto != null) this.textosListas["Cod_eve"] = texto;
        IndividualesProvider.obtenerCamposComplementarios(evento).then(respuesta => {
          this.camposComplementarios = respuesta.data;
        })
      }
    },
    /**
     * Este metodo se usa para extraer del evento (tipoEvento) propiedades como gru_per, clas_per, lab_incrus etc...
     * @param {*} evento 
     */
    async establecerPropiedadesEvento(evento) {
      try {
        this.propiedadesDelEvento = (await IndividualesProvider.obtenerPropiedadesEvento(evento)).data;
        this.variablesFormulario.propiedadesDelEvento = this.propiedadesDelEvento;
      } catch (error) {
        console.error(error);
      }
    },
    obtenerEventos() {
      ComunProvider.retornarListaEventos()
        .then((res) => {
          this.listaEventos = res.data.map((a) => {
            return { value: a.cod_eve, label: a.nom_eve };
          });
          if (this.idEvento != 0) {
            this.obtenerBasico();
          }
        });
    },
    obtenerFormulario(cod_eve) {
      this.$q.loading.show({
        message: 'Cargando formulario',
        customClass: 'opaco'
      });
      return new Promise((resolve, reject) => {
        this.load = true;
        this.variablesFormulario.IndividualesCargados = false;
        this.metaDatos = [];
        this.cod_eve = cod_eve;
        let paramBase = null;
        paramBase = { cod_eve: cod_eve }
        ComunProvider.retornarTipoEvento(paramBase).then(res => {
          if (res.data.length > 0) {
            this.tienecomplementarios = res.data[0].formulario > 0 && res.data[0].datos_basicos != 1;
          } else {
            this.tienecomplementarios = false;
          }
        })
        IndividualesProvider.retornarEstructuraBasicos({ cod_eve: "Eventos" }).then(resA => {
          this.capturaVariables = resA.data.reduce((a, b) => (a[b.nombre] = (this.capturaVariables[b.nombre] ?? ''), a), {});
          this.capturaVariables["Cod_eve"] = this.cod_eve;
          this.capturaVariables["Cod_pre"] = this.capturaVariables["Cod_pre"] + this.capturaVariables["Cod_sub"];

          this.capturaVariables["Cod_mun_r"] = this.capturaVariables["Cod_dpto_r"] + this.capturaVariables["Cod_mun_r"];
          this.capturaVariables["Cod_mun_o"] = this.capturaVariables["Cod_dpto_o"] + this.capturaVariables["Cod_mun_o"];

          this.capturaVariables["Cod_dpto_r"] = metodosComunes.completarCod_dpto(this.capturaVariables["Cod_dpto_r"], this.capturaVariables["Cod_mun_r"]);
          this.capturaVariables["Cod_dpto_o"] = metodosComunes.completarCod_dpto(this.capturaVariables["Cod_dpto_o"], this.capturaVariables["Cod_mun_o"]);

          var matches = this.capturaVariables["Nom_dil_fi"].match(/\[(.*?)\]/);
          if (matches) {
            this.codigoHex = matches[1];
            this.codigoBDUA = (parseInt(matches[1], 16).toString(2)).padStart(this.camposbdua.length, '0').split("").reverse().join("");
          }
          if (this.idEvento == 0)
            this.capturaVariables["Ajuste"] = "0";
          let params = { cod_eve };
          IndividualesProvider.retornarCamposBasicos(params)
            .then((res) => {
              metodosComunes.obtenerValidaciones('Eventos|' + cod_eve).then((validaciones) => {
                metodosComunes.obtenerFuncionalidades('Eventos|' + cod_eve).then((funcionalidades) => {
                  funcionalidades = metodosComunes.separarFuncionesUnidas(funcionalidades);
                  metodosComunes.obtenerValidacionesAfectantes('Eventos|' + cod_eve).then((validacionesAfectantes) => {
                    metodosComunes.obtenerFuncionalidadesAfectantes('Eventos|' + cod_eve).then((funcionalidadesAfectantes) => {
                      funcionalidadesAfectantes = metodosComunes.separarFuncionesAfectantesUnidas(funcionalidadesAfectantes);
                      funcionalidadesAfectantes = metodosComunes.agruparArregloPorLlave(funcionalidadesAfectantes, "parametro");
                      validacionesAfectantes = metodosComunes.agruparArregloPorLlave(validacionesAfectantes, "parametro");
                      const datos = metodosComunes.procesarJsonCrudo(res.data, validaciones, funcionalidades, funcionalidadesAfectantes, validacionesAfectantes);
                      metodosComunes.agruparArreglo(datos, 'grupo').then(res => {
                        metodosComunes.agruparTabla(res).then(tablas => {
                          this.tablas = tablas[0]
                          this.metaDatos = res;
                          this.load = false;
                          this.$nextTick(() => {
                            Object.keys(this.$refs).filter((x) => x.match(/^[A-Z].*/)).forEach(element => {
                              if (element != 'Cod_eve' && this.$refs[element][0] != null && this.capturaVariables[element]) {
                                this.$refs[element][0].valor = this.capturaVariables[element];
                              }
                            });
                            this.$refs.Año[0].deshabilitar = true;
                            this.$refs.Semana[0].deshabilitar = true;
                            if (this.idEvento == 0 || this.idEvento.startsWith("OF")) {
                              this.$refs.Ajuste[0].deshabilitar = true
                              this.$refs.Fec_aju[0].deshabilitar = true;
                              ComunProvider.obtenerFechaActual().then(res => {
                                this.$refs.Fec_aju[0].valor = moment(res.data).format("DD/MM/YYYY");
                              })
                            }
                            const campos = Object.keys(this.capturaVariables);
                            /*Se añade cod_pais_r manualemnte debido a que la estructura de guardado 
                            no trae este campo al ser un campo inexistente en el formato de Eventos normal */
                            campos.push("Cod_pais_r");
                            this.ejecutarAfectantes(campos, 'estructura básicos').then(async () => {
                              if (this.idEvento != 0 && !this.idEvento.startsWith("OF") && ((this.variablesFormulario.cod_eve != "550" && this.variablesFormulario.cod_eve != "560") || this.variablesFormulario.est_ing != "2")) {
                                Object.keys(this.$refs).filter((x) => x.match(/^[A-Z].*/)).forEach(element => {
                                  if (element != 'Cod_eve' && this.$refs[element][0] != null) {
                                    this.$refs[element][0].deshabilitar = true;
                                  }
                                });
                              }
                              // this.obtenerFormularioComplementarios();
                              this.variablesFormulario.IndividualesCargados = true;
                              
                              if (this.idEvento != 0 && !this.idEvento.startsWith("OF") && this.tienecomplementarios) {
                                await this.cargarComplementariosAlIncio();
                              } else { this.$q.loading.hide() }

                              if(this.variablesFormulario.est_ing == "2" && (this.variablesFormulario.cod_eve == "550" || this.variablesFormulario.cod_eve == "560")){
                                this.Ajustar("0");
                                this.ajustar = true;
                                this.$refs.Fec_aju[0].deshabilitar = true;
                                this.$refs.Ajuste[0].deshabilitar = true;
                                this.$refs.Ajuste[0].valor = "0";
                                this.$refs.Ajuste[0].cambiaEstadoOpciones([0],[1,2,3,4,5,6]);
                              }
                              resolve(1);
                            }, (err) => reject(err));
                          });
                        });
                      });
                    });
                  });
                });
              });
            });
        });
      })
    },
    obtenerFormularioComplementarios() {
      // this.puedeEjecutarAfectantes = false;
      return new Promise((resolve, reject) => {
        const cod_eve = this.cod_eve;
        IndividualesProvider.retornarEstructuraBasicos({ cod_eve }).then(resA => {
          console.log('rta1: ', resA);
          this.capturaVariablesComp = resA.data.reduce((a, b) => (a[b.nombre] = (''), a), {});
          let params = { cod_eve };
          IndividualesProvider.retornarDatosComplementarios(params).then((res) => {
            console.log('rta2: ', res.data);
            this.labelsImpresion = res.data;
            this.camposFechaFormatear = this.labelsImpresion.filter(x => x.tipo == "ctrlFecha").reduce((a, b) => (a[b.campo] = b.campo, a), {});
            metodosComunes.obtenerValidaciones(cod_eve).then((validaciones) => {
              metodosComunes.obtenerFuncionalidades(cod_eve).then((funcionalidades) => {
                metodosComunes.obtenerFuncionalidadesAfectantes(cod_eve).then((funcionalidadesAfectantes) => {
                  metodosComunes.obtenerValidacionesAfectantes(cod_eve).then((validacionesAfectantes) => {
                    funcionalidadesAfectantes = metodosComunes.agruparArregloPorLlave(funcionalidadesAfectantes, "parametro");
                    validacionesAfectantes = metodosComunes.agruparArregloPorLlave(validacionesAfectantes, "parametro");
                    var datos = metodosComunes.procesarJsonCrudo(res.data, validaciones, funcionalidades, funcionalidadesAfectantes, validacionesAfectantes);
                    metodosComunes.agruparArreglo(datos, 'grupo').then(respuesta => {
                      metodosComunes.agruparTabla(respuesta).then(tablas => {
                        this.tablasComp = tablas;
                        this.metaDatosComp = respuesta;
                        this.ObtenerComplementario().then((res) => {
                          resolve(res);
                        }, (err) => reject(err))
                      });
                    });
                  });
                });
              });
            });
          })
        })
      })
    },
    emitirMetodo(metodo, ...Paremetros) {
      this[metodo](...Paremetros);
    },
    ignorarBdua(omitir) {
      this.omitirBdua = omitir
    },
    reload(id) {
      if (this.$route.path != '/Individuales/' + id) {
        this.puedeSalir = true;
        this.$router.push({ path: '/Individuales/' + id });
        location.reload();
      }
    },
    mostrarAlertaBDUA() {
      if (this.codigoBDUA.includes("1")) {
        const msg = `El sistema ha establecido auntomáticamente varios valores de la ficha!
        No podrá hacer actualizaciones de esos valores!
        ¿Esta seguro que desea guardar?`
        this.$modal.alertar({
          titulo: "Atención",
          mensaje: msg,
          tipo: 3,
          onConfirm: () => this.enviar(),
          cancelButton: true
        })
      } else {
        this.enviar();
      }
    },
    async enviar() {
      await this.actualizarFechaAjuste();
      console.log(this.capturaVariables.Fec_aju)
      if ((this.formularioTocado || !this.formularioDebeSerTocado) || !this.tienecomplementarios) {
        const resultado = await this.ejecutaValidaciones(1);
        const resultadoB = await this.ejecutaValidaciones(2);
        if (!resultado.filter(x => x.valido == false).length > 0) {
          if (!resultadoB.filter(x => x.valido == false).length > 0) {
            this.capturaVariables.Num_ide = this.CalcularNumIdAdultoYMenorSinIdentificacion(
              this.capturaVariables.Tip_ide, 
              this.capturaVariables.Num_ide, 
              this.capturaVariables.Nacionalid, 
              this.textosListas["Nacionalid"]
            );
            let params = Object.assign({}, this.capturaVariables);
            this.guardando = true;
            this.inactivar = true;
            this.clasificacionAlGuardar = (this.idEvento == 0) ? params.Tip_cas : params.Ajuste
            this.requerirLaboratorios = (this.clasificacionAlGuardar == '3' && this.propiedadesDelEvento.lab_incrus === 'F')
            params.Cod_sub = params.Cod_pre.substring(params.Cod_pre.length - 2, params.Cod_pre.length);
            params.Cod_pre = params.Cod_pre.substring(0, params.Cod_pre.length - 2);

            params.Cod_dpto_o = params.Cod_mun_o.substring(0, 2);
            params.Cod_dpto_r = params.Cod_mun_r.substring(0, 2);
            params.Cod_mun_o = params.Cod_mun_o.substring(2);
            params.Cod_mun_r = params.Cod_mun_r.substring(2);

            params.Version = 'Captura Offline ' + global.version;
            params.lat_dir = "";
            params.long_dir = "";

            if (global.isOnline) { //Si esta online busca la localizacion mediante el api de google
              let geografia = this.textosListas["Cod_pais_r"] + "," + this.textosListas["Cod_dpto_r"] + "," + this.textosListas["Cod_mun_r"] + ","
              let direccion = (geografia.replace(",", " ").trim() == "") ? params.Dir_res : geografia + params.Dir_res;
              let res = await ComunProvider.obtenerGeocode(direccion.replace(/[^a-zA-Z0-9]/g, ' '))
              if (res != null && res.data.results != null && res.data.status == "OK") {
                params.lat_dir = res.data.results[0].geometry.location.lat;
                params.long_dir = res.data.results[0].geometry.location.lng;
              }
            }

            params.Fec_not = params.Fec_not ? moment(params.Fec_not, "DD/MM/YYYY").valueOf() : "";
            params.Fec_con = params.Fec_con ? moment(params.Fec_con, "DD/MM/YYYY").valueOf() : "";
            params.Ini_sin = params.Ini_sin ? moment(params.Ini_sin, "DD/MM/YYYY").valueOf() : "";
            params.Fec_hos = params.Fec_hos ? moment(params.Fec_hos, "DD/MM/YYYY").valueOf() : "";
            params.Fec_def = params.Fec_def ? moment(params.Fec_def, "DD/MM/YYYY").valueOf() : "";
            params.Fecha_nto = params.Fecha_nto ? moment(params.Fecha_nto, "DD/MM/YYYY").valueOf() : "";
            params.Fec_arc_pl = params.Fec_arc_pl ? moment(params.Fec_arc_pl, "DD/MM/YYYY").valueOf() : "";
            params.Fec_aju = params.Fec_aju ? moment(params.Fec_aju, "DD/MM/YYYY").valueOf() : "";
            params.Fec_reacti = params.Fec_reacti ? moment(params.Fec_reacti, "DD/MM/YYYY").valueOf() : "";
            this.cod_sub = params.Cod_sub;

            if (this.codigoHex == null && this.codigoBDUA.includes("1")) {
              let codigobin = this.codigoBDUA.split("").reverse().join("");
              let codigoHex = parseInt(codigobin, 2).toString(16).toUpperCase();
              params.Nom_dil_fi = params.Nom_dil_fi + " SISSIV[" + codigoHex + "]";
            }

            if (this.tienecomplementarios) {
              this.guardarComplementarios(params);//(pend) guardar complementarios
            } else {
              const res = await IndividualesProvider.guardarDatosBasicosLocal(params, null, this.idEvento, this.requerirLaboratorios);
              this.guardando = false;
              this.inactivar = false;
              if (res.data.response) {
                this.puedeSalir = true;
                this.$emit("sincroniza", (mensaje) => {
                  //si es un evento nuevo o un evento previamente guardado en offline se trabaja con el id autogenerado (res.tipo), de lo contrario se trabaja con el id ya existente
                  const idEvento = (this.idEvento == 0 || this.idEvento.startsWith("OF")) ? res.tipo : this.idEvento; 
                  this.alertaGuardadoExitoso(idEvento, mensaje)
                });
                this.puedeSalir = true;
              } else {
                this.$modal.alertar({ titulo: "Atención", mensaje: res.data.message, tipo: 2 })
              }
            }
          } else {
            this.guardando = false;
            this.inactivar = false;
            this.tab = "complementarios";
            const validacionFallida = resultadoB.filter(x => x.valido == false)[0];
            const msg = (validacionFallida.mensajePersonalizado != null) ? validacionFallida.mensajePersonalizado : "Existen validaciones de datos complementarios que debe realizar"
            this.$modal.alertar({ titulo: "Atención", mensaje: msg, tipo: 3 })
          }
        } else {
          this.guardando = false;
          this.inactivar = false;
          this.tab = "basicos";
          const validacionFallida = resultado.filter(x => x.valido == false)[0];
          const msg = (validacionFallida.mensajePersonalizado != null) ? validacionFallida.mensajePersonalizado : "Existen validaciones de datos básicos que debe realizar"
          this.$modal.alertar({ titulo: "Atención", mensaje: msg, tipo: 3 })
        }
      } else {
        this.guardando = false;
        this.inactivar = false;
        this.tab = "complementarios";
        this.revisarTab().then(() => {
          // this.cargarComplementariosPrmeraVez().then((res)=>{
          this.$modal.alertar({
            titulo: "Atención", mensaje: "¿Está seguro de guardar el registro sin haber diligenciado ninguna variable de datos complementarios?", tipo: 2, onConfirm: () => {
              this.formularioTocado = true;
              this.enviar();
            }, cancelButton: true
          })
        })

      }
    },
    CalcularNumIdAdultoYMenorSinIdentificacion(Tip_ide, Num_ide, Nacionalid = 0, textoNacionalidad = "") {
      const cod_Pais = textoNacionalidad.match(/\(([^)]+)\)/) ?? [];
      if (Tip_ide == 'AS' && this.idEvento == 0) {
        Num_ide = Math.floor(Math.random() * (Date.now() - 10000000) + 10000000); //Genera un numero aleatorio para asignarlo como numID
        if (Nacionalid != 170 && Nacionalid != 0) {
          Num_ide = (cod_Pais[1] || "") + Num_ide
          this.notificar('Se antepusieron automáticamente al número de documento, las tres primeras letras identificadoras de la nacionalidad por cuanto se trata de un paciente extranjero', 5000, 1)
        }
      }
      if (Tip_ide == 'MS' && this.idEvento == 0 && Nacionalid != 170) {
        Num_ide = (cod_Pais[1] || "") + Num_ide
        this.notificar('Se antepusieron automáticamente al número de documento, las tres primeras letras identificadoras de la nacionalidad por cuanto se trata de un paciente extranjero', 5000, 1)
      }
      return Num_ide
    },
    ObtenerComplementario() {
      return new Promise((resolve, reject) => {
        IndividualesProvider.obtenerComplementario({ idEvento: this.evento }).then((res) => {
          try {
            if (typeof res.data[0] !== typeof undefiend && (res.data.length > 0 || 'JsonEvento' in res.data[0])) {//si el evento ya tiene un complementario registrado o si localmente hay una copia con complementario
              let jsonEvento = JSON.parse(res.data[0].JsonEvento);//obtiene el json del evento
              //obtiene los valores del complementario existente sin eliminar los campos de utilidad que ya existen en this.capturaVariables
              jsonEvento = this._.mapKeys(jsonEvento, function (v, k) { return k.toLowerCase(); });
              this.capturaVariablesComp = this._.assign(this.capturaVariablesComp, this._.pick(jsonEvento, this._.keys(this.capturaVariablesComp)))
              this.capturaVariablesComp = this._.mapKeys(this.capturaVariablesComp, function (v, k) { return k.toLowerCase(); });
              this.cargarValoresFormularioComplementarios();
            }
            var campos = Object.keys(this.capturaVariablesComp);
            campos = campos.filter((x) => x.match(/^[a-z].*/));
            this.ejecutarAfectantes(campos, 'obtener complementarios').then(() => {
              this.variablesFormulario.cambioTab = false;
              this.variablesFormulario.complementariosCargados = true;
              this.variablesFormulario.IndividualesCargados = true;
              resolve(1);
            })
          } catch (er) { console.log(er); reject(er) }
        }, (err) => reject(err));
      })
    },
    async guardarComplementarios(basico) {
      metodosComunes.darFormatoInsAFechas(Object.keys(this.camposFechaFormatear), this.capturaVariablesComp);
      let jsonEvento = {};
      if (this.cod_eve == 113) {
        this.capturaVariablesComp.num_ide_ma = this.CalcularNumIdAdultoYMenorSinIdentificacion(this.capturaVariablesComp.tip_ide_ma, this.capturaVariablesComp.num_ide_ma);
      }

      this.camposComplementarios.forEach(campoJson => {
        if (this.capturaVariablesComp[campoJson.toLowerCase()])
          jsonEvento[campoJson] = this.capturaVariablesComp[campoJson.toLowerCase()].toString();
        else {
          jsonEvento[campoJson] = '';
        }
      });
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('cod_eve', this.camposComplementarios)] = this.capturaVariables.Cod_eve.toString();
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('semana', this.camposComplementarios)] = this.capturaVariables.Semana.toString();
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('ano', this.camposComplementarios)] = this.capturaVariables.Año.toString();
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('cod_pre', this.camposComplementarios)] = this.capturaVariables.Cod_pre.substring(0, this.capturaVariables.Cod_pre.length - 2);
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('cod_sub', this.camposComplementarios)] = this.cod_sub ?? this.capturaVariables.Cod_pre.substring(this.capturaVariables.Cod_pre.length - 2, this.capturaVariables.Cod_pre.length);
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('tip_ide', this.camposComplementarios)] = this.capturaVariables.Tip_ide.toString();
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('num_ide', this.camposComplementarios)] = this.capturaVariables.Num_ide.toString();
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('ajuste', this.camposComplementarios)] = this.capturaVariables.Ajuste.toString(); //"7";
      jsonEvento[metodosComunes.encontrarPropiedadOriginal('fec_aju', this.camposComplementarios)] = moment(Date.now()).format("YYYY-MM-DD");

      let complementario = {
        id: null,
        JsonEvento: JSON.stringify(jsonEvento),
        Semcarga: "1",
        Oportuna: "1",
        Semanal: "1",
        Fechahora: moment(Date.now()).format("YYYY-MM-DD"),
        creadoPor: "Captrura en linea"
      }
      const res = await IndividualesProvider.guardarDatosBasicosLocal(basico, complementario, this.idEvento, this.requerirLaboratorios);
      this.guardando = false;
      this.inactivar = false;
      if (res.data.response) {
        this.puedeSalir = true;
        this.$emit("sincroniza", (mensaje) => {
          //si es un evento nuevo o un evento previamente guardado en offline se trabaja con el id autogenerado (res.tipo), de lo contrario se trabaja con el id ya existente
          const idEvento = (this.idEvento == 0 || this.idEvento.startsWith("OF")) ? res.tipo : this.idEvento; 
          this.alertaGuardadoExitoso(idEvento, mensaje)
        });
      } else {
        this.$modal.alertar({ titulo: "Atención", mensaje: res.data.message, tipo: 2 })
        MetodosComunes.darFormatoCapturaAFechas(Object.keys(this.camposFechaFormatear), this.capturaVariablesComp);
      }
    },
    /**
     * Método que muestra la alerta de guardado exitoso, y que se usa para enviar al usuario al 
     * formulario de laboratorios en los casos de que el diligenciamiento sea obligatorio.
     * @param {*} idEvento 
     */
    alertaGuardadoExitoso(idEvento, mensaje) {
      if (this.requerirLaboratorios) { //Si el evento requiere la insercion de laboratorios  
        this.$modal.alertar({
          titulo: "Atención", mensaje: "Este evento ha sido confirmado por laboratorio y es necesario que usted diligencie los datos de  laboratorio. Será llevado a la pantalla pertinente para su registro.", tipo: 3, onConfirm: () => {
            this.$router.push(`/Laboratorios/${idEvento}/0`);
          }
        })
      } else {
        this.$modal.alertar({
          titulo: "Atención", mensaje: mensaje, tipo: 1, onConfirm: () => {
            this.$router.push('/Individuales');
          }
        })
      }
    },
    ejecutaValidaciones(formulario) {
      const valida = global.validaActivo;
      let promesasValidacion = [];
      // let arrCampos = []; //debug que campo falla validacion
      return new Promise((resolve) => {
        if (valida) {
          var regex = formulario == 1 ? /^[A-Z].*/ : /^[a-z].*/;
          Object.keys(this.$refs).filter((x) => x.match(regex)).forEach(element => {
            if (element != 'Cod_eve' && this.$refs[element][0] != null) {
              // arrCampos.push(this.$refs[element][0].campo); //debug que campo falla validacion
              promesasValidacion.push(this.$refs[element][0].validar());
            }
          });
          Promise.all(promesasValidacion).then(resultado => {
            resolve(resultado);
          }, (err) => { console.log(err) })
        } else {
          promesasValidacion[{ valido: true }];
        }
      })
    },

    revisarTab() {
      return new Promise((resolve) => {
        const cargadosAntes = !this.variablesFormulario.complementariosCargados
        const tabComp = this.tab == "complementarios";
        this.variablesFormulario.cambioTab = true;
        if (tabComp) {
          if (cargadosAntes) {
            this.cargarComplementariosPrmeraVez().then(() => {
              this.ajustarComplementario();
              resolve(1);
            });
          } else {
            var campos = Object.keys(this.capturaVariablesComp);
            this.variablesFormulario.IndividualesCargados = false;
            this.ejecutarAfectantes(campos).then(() => {
              this.variablesFormulario.cambioTab = false;
              this.variablesFormulario.IndividualesCargados = true;
              this.ajustarComplementario();
              resolve(1);
            });
          }
        } else {
          this.variablesFormulario.cambioTab = false;
          let campos = Object.keys(this.$refs);
          campos = campos.filter((x) => x.match(/^[a-z].*/) && x != "btnGuardarB" && x != "btnGuardarC");
          campos.forEach(element => {
            this.$refs[element][0].deshabilitar = false;
          });
          resolve(1);
        }
      })
    },
    async cargarComplementariosAlIncio() {
      this.variablesFormulario.cambioTab = true;
      this.tab = "complementarios";
      await this.cargarComplementariosPrmeraVez();
      this.tab = "basicos";

      this.$q.loading.hide();
    },

    generarFicha() {
      this.$q.loading.show({
        message: 'Obteniendo datos para la ficha',
        customClass: 'opaco'
      })
      if (!this.variablesFormulario.complementariosCargados && this.tienecomplementarios) {
        this.tab = "complementarios";
        this.variablesFormulario.cambioTab = true;
        this.cargarComplementariosPrmeraVez().then(() => {
          this.imprimirBasicos();
          this.tab = "basicos";
        })
      } else {
        this.imprimirBasicos();
      }
    },
    async imprimirBasicos() {
      let res = await IndividualesProvider.retornarDetalleEventoEspecifico(this.evento);
      let detalleEvento = "";
      let variablesBasicos = "";
      let tieneComp = true;
      if (res.data) {
        detalleEvento = JSON.parse(res.data.JsonEvento);
        const llaves = Object.keys(detalleEvento);
        this.labelsImpresion = await IndividualesProvider.retornarLabelsImpresion(this.cod_eve, llaves);
        this.labelsImpresion = this.labelsImpresion.data;
      } else {
        tieneComp = false;
        detalleEvento = { Complementario: "No tiene complementario asociado" };
        this.labelsImpresion = [];
      }

      detalleEvento = this._.mapKeys(detalleEvento, function (v, k) { return k.toLowerCase(); });// propiedades del detalle a minuscula
      let variablesImpresion = this._.clone(detalleEvento); //clona el objeto para no sobreescribirlo
      variablesImpresion = this._.assign(variablesImpresion, this.filtraObjetoConMinusculas(this.textosListas)); //reemplaza los valores de lista con sus respectivos textos
      variablesBasicos = this._.clone(this.capturaVariables); //clona el objeto para no sobreescribirlo
      variablesBasicos = this._.assign(variablesBasicos, this.filtraObjetoConMayusculas(this.textosListas)); //reemplaza los valores de lista con sus respectivos textos
      variablesBasicos = this._.mapValues(variablesBasicos, this._.method('toUpperCase'));

      const keyMap = this.labelsImpresion.filter(x => x.campo != null).reduce((a, b) => (a[b.campo] = b.labelImpresion, a), {}); // crea un keymap de reemplazo con los labels para impresión
      const camposDesactivados = this.labelsImpresion.filter(x => x.cl_inactivo == 1).map(({ campo }) => campo);//.map(({ cl_inactivo }) => cl_inactivo)) // crea un keymap de reemplazo con los campos desactivados y que no van en la ficha de impresion
      metodosComunes.eliminarCamposDeJson(camposDesactivados, variablesImpresion);
      variablesImpresion = this._.mapKeys(variablesImpresion, function (value, key) { return keyMap[key]; }); // reemplaza las descripciones con los lables de impresión (legibles para el usuario)
      delete variablesImpresion.undefined;
      variablesImpresion.COD_PRE = this.capturaVariables.Cod_pre + " - " + variablesBasicos.Cod_pre;
      imprimir.generarpdf(variablesBasicos, variablesImpresion, true, tieneComp);
      this.$q.loading.hide();
    },
    cargarValoresFormularioComplementarios() {
      var campos = Object.keys(this.capturaVariablesComp);
      campos.forEach(campo => {
        if (this.$refs[campo] && this.$refs[campo][0] != null) {
          this.$refs[campo][0].valor = this.capturaVariablesComp[campo];
        }
      })
    },
    // eslint-disable-next-line no-unused-vars
    ejecutarAfectantes(campos, desde) {
      this.variablesFormulario.IndividualesCargados = false;
      return new Promise((resolve) => {
        this.puedeEjecutarAfectantes = true;
        let longitudMenosNoPintados = campos.length;
        for (let index = 0; index < longitudMenosNoPintados; index++) {
          const nombreCampo = campos[index];
          this.$nextTick(() => {
            if (this.$refs[nombreCampo] && this.$refs[nombreCampo][0] != null) {
              this.llenaVariablesParaFichaImpresion(nombreCampo);
              this.$refs[nombreCampo][0].emitirFuncionesAfectantes("change");
              if (nombreCampo == "procedimie")
                console.log(nombreCampo, ' valooor ', this.$refs[nombreCampo][0].valor.toString());
            }
            if (index == longitudMenosNoPintados - 1) {
              resolve(true);
            }
          })
        }
      })
    },
    filtraObjetoConMinusculas() {
      return this._.pickBy(this.textosListas, function (value, key) { return key.match(/^[a-z].*/) });
    },
    filtraObjetoConMayusculas() {
      return this._.pickBy(this.textosListas, function (value, key) { return key.match(/^[A-Z].*/) });
    },
    llenaVariablesParaFichaImpresion(nombreCampo) {
      if (Object.hasOwn(this.$refs[nombreCampo][0].$refs[nombreCampo + "Ref"], "establecerTexto")) {
        this.$refs[nombreCampo][0].$refs[nombreCampo + "Ref"].establecerTexto();
      }
    },
    abrirLaboratorios() {
      this.irA("/Laboratorios/" + this.eventoCrudo);
    },
    cargarComplementariosPrmeraVez() {
      //this.variablesFormulario.complementariosCargados = true;
      this.puedeEjecutarAfectantes = false;
      return new Promise((resolve, reject) => {
        this.obtenerFormularioComplementarios().then(() => {
          resolve(1);
        }, (err) => reject(err))
      })
    },
    irA(ruta) {
      this.$router.push(ruta);
    },
    revisarLaboratorios(codigo) {
      LaboratoriosProvider.obtenerEventosConLaboratorios(this.evento).then(res => {
        var evs = res.data;
        var names = evs.map(function (item) {
          return item['cod_eve'];
        });
        if (names.includes(codigo)) {
          this.tieneLabs = true;
        }
      })
    },
    atras() {
      this.$router.go(-1);
    },
    eventoGuardado(jsonEvento) {
      if (jsonEvento[metodosComunes.encontrarPropiedadOriginal('cod_eve', this.camposComplementarios)].toString() == '365' && this.variablesFormulario.esNuevo && this.textosListas.cod_sust.toLowerCase().indexOf('glifo') != -1) {
        this.obtenerPdfAlarma();
      } else {
        this.$modal.alertar({
          titulo: "Atención", mensaje: "Evento guardado con éxito", tipo: 1, onConfirm: () => {
            this.$router.push('/Individuales');
          }
        });
      }
    },
    bloquearCamposBdua() {
      let binary = this.codigoBDUA;
      for (var i = 0; i < binary.length; i++) {
        if (binary[i] === '1') {
          let campo = this.camposbdua[i];
          this.$refs[campo][0].deshabilitar = true;
        }
      }
    },
    /**
     * Metodo que contiene la logica para marcar los campos que fueron retenidos por ser traidos por el BDUA 
     * @param {string} campo campo de datosbasicos
     * @param {bool} retenido 1=retenido, 0=sin retener
     */
    retencionBdua(campo, retenido) {
      let campos = this.camposbdua;
      if (this.codigoBDUA == null) {
        this.codigoBDUA = ''.padStart(campos.length, "0");
      }
      let posicion = campos.indexOf(campo);
      if (posicion != -1)
        this.codigoBDUA = metodosComunes.replaceAt(this.codigoBDUA, posicion, retenido);
    },
    async obtenerPdfAlarma() {
      let res = await IndividualesProvider.retornarDetalleEventoEspecifico(this.evento);
      let detalleEvento = "";
      let variablesBasicos = "";
      let tieneComp = true;
      if (res.data) {
        detalleEvento = JSON.parse(res.data.JsonEvento);
        const llaves = Object.keys(detalleEvento);
        this.labelsImpresion = await IndividualesProvider.retornarLabelsImpresion(this.cod_eve, llaves);
        this.labelsImpresion = this.labelsImpresion.data;
      } else {
        tieneComp = false;
        detalleEvento = { Complementario: "No tiene complementario asociado" };
        this.labelsImpresion = {};
      }
      detalleEvento = this._.mapKeys(detalleEvento, function (v, k) { return k.toLowerCase(); });// propiedades del detalle a minuscula
      let variablesImpresion = this._.clone(detalleEvento); //clona el objeto para no sobreescribirlo
      variablesImpresion = this._.assign(variablesImpresion, this.filtraObjetoConMinusculas(this.textosListas)); //reemplaza los valores de lista con sus respectivos textos

      variablesBasicos = this._.clone(this.capturaVariables); //clona el objeto para no sobreescribirlo
      variablesBasicos = this._.assign(variablesBasicos, this.filtraObjetoConMayusculas(this.textosListas)); //reemplaza los valores de lista con sus respectivos textos
      variablesBasicos = this._.mapValues(variablesBasicos, this._.method('toUpperCase'));

      const keyMap = this.labelsImpresion.filter(x => x.campo != null).reduce((a, b) => (a[b.campo] = b.labelImpresion, a), {}); // crea un keymap de reemplazo con los labels para impresión
      variablesImpresion = this._.mapKeys(variablesImpresion, function (value, key) { return keyMap[key]; }); // reemplaza las descripciones con los lables de impresión (legibles para el usuario)
      delete variablesImpresion.undefined;

      const blob = imprimir.generarpdf(variablesBasicos, variablesImpresion, false, tieneComp);
      IndividualesProvider.alarmaIntoxicaciones(blob, this.evento).then(res => {
        console.log(res);
      })
    },
    darFormatoInsAFechas(campos, variables) {
      campos.forEach(function (valor) {
        if (moment(variables[valor], "DD/MM/YYYY") != "Invalid date") {
          variables[valor] = moment(variables[valor], "DD/MM/YYYY").format("YYYY-MM-DD") != "Invalid date" ? moment(variables[valor], "DD/MM/YYYY").format("YYYY-MM-DD") : variables[valor];
        }
      })
    },
    notificar(mensaje, tiempo, tipo) {
      Notify.create({
        type: (tipo == 1) ? 'positive' : (tipo == 2) ? 'negative' : (tipo == 3) ? 'warning' : (tipo == 4) ? 'ongoing' : '',
        message: mensaje,
        timeout: tiempo ?? 2000,
        position: 'bottom-right'
      })
    },

    async actualizarFechaAjuste() {
      console.log("actualizarFechaAjuste");
      const fechaActual = await ComunProvider.obtenerFechaActual();
      this.$refs.Fec_aju[0].valor = moment(fechaActual.data).format("DD/MM/YYYY");
    },
    async actualizarListaEventos() {
      const eventosRequest = await ComunProvider.retornarListaEventos();
      this.listaEventos = eventosRequest.data.map((a) => {
        return { value: a.cod_eve, label: a.nom_eve };
      });
    },
  },
  mounted() {
    this.$emit("disenoPagina", true);
    IndividualesProvider.obtenerEstructuraBdua().then((res) => {
      this.camposbdua = res.data;
      this.obtenerEventos();
    });
  },
  created() {
    window.addEventListener('beforeunload', (event) => {
      if (!this.puedeSalir) {
        event.preventDefault();
        event.returnValue = '';
      }
    });
  },
  beforeDestroy() {
    window.removeEventListener('beforeunload', this.beforeWindowUnload)
  },
  beforeRouteLeave(to, from, next) {
    if (!this.puedeSalir) {
      if (!window.confirm("¿Desea salir sin guardar?")) {
        return;
      }
    }
    next();
  },

};
