
import ComunProvider from "../../providers/ComunProvider";
import { showDialog } from "../../utilidades/plugins/dialogoLookUps/Dialogo.js";

export default {
  /**
   * Este componente es igual al que no tiene la N. Sin embargo, este no tiene el error negado. Además, presenta los elementos ordenados cuando se abre la
   * tabla, y desactiva el campo si al buscar solo hay una opción.
   */
  name: "LookUpTableControlN",
  props: [
    "options",
    "deshabilitar",
    "tooltip",
    "mask",
    "valorCampo",
    "hayError",
    "mensajeError",
    "campo",
    "MostrarDescripcion",
    "campoCaptura",
    "extraparams",
    "bloqueante",
    "esTabla",
    "bloqueanteNoCoincide",
    "longitudMin",
  ],
  data() {
    return {
      valorCrudo: this.valorCampo ?? "",
      valorCrudo2: null,
      Rawitems: [],
      navigationActive: false,
      filter: "",
      selected: [],
      pagination: {},
      modal: false,
      confirm2: false,
      columns: [],
      unaOpcion: false,
      claseTabla: this.esTabla ? "inputsTablas" : "",
    };
  },
  computed: {
    valor: {
      get() {
        return this.valorCrudo == null ? "" : this.valorCrudo;
      },
      set(val) {
        this.valorCrudo = val;
        var a = this.Rawitems.filter((o) =>
          Object.keys(o).some((k) => o[k].toLowerCase() == val.toLowerCase())
        );
        this.valor2 = a.length == 1 ? a[0][Object.keys(a[0])[1]] : "";
        this.$emit("seleccionDescripcion", this.campoCaptura, this.valor2);
      },
    },
    valor2: {
      get() {
        return this.valorCrudo2 == null ? "" : this.valorCrudo2;
      },
      set(val) {
        this.valorCrudo2 = val;
      },
    },
    tableClass() {
      return this.navigationActive === true ? "shadow-8 no-outline" : void 0;
    },
  },
  methods: {
    seleccion(details) {
      this.valor = details.length > 0 ? details[0][Object.keys(details[0])[0]] : "";
      this.valor2 = details.length > 0 ? details[0][Object.keys(details[0])[1]] : "";
      this.modal = false;

      this.$emit("seleccion", this.valor);
      this.$emit("seleccionDescripcion", this.campoCaptura, this.valor2);
    },
    keepFocus() {
      if (this.bloqueante) {
        this.$nextTick(() => this.$refs["input"].focus());
      } else {
        this.$nextTick(() => this.$refs["input"].blur());
      }
    },
    activateNavigation() {
      this.navigationActive = true;
    },
    deactivateNavigation() {
      this.navigationActive = false;
    },

    onKey(evt) {
      if (
        this.navigationActive !== true ||
        [33, 34, 35, 36, 38, 40].indexOf(evt.keyCode) === -1 ||
        this.$refs.myTable === void 0
      ) {
        return;
      }

      evt.preventDefault();

      const { computedRowsNumber, computedRows } = this.$refs.myTable;

      if (computedRows.length === 0) {
        return;
      }

      const currentIndex =
        this.selected.length > 0 ? computedRows.indexOf(this.selected[0]) : -1;
      const currentPage = this.pagination.page;
      const rowsPerPage =
        this.pagination.rowsPerPage === 0
          ? computedRowsNumber
          : this.pagination.rowsPerPage;
      const lastIndex = computedRows.length - 1;
      const lastPage = Math.ceil(computedRowsNumber / rowsPerPage);

      let index = currentIndex;
      let page = currentPage;

      switch (evt.keyCode) {
        case 36: // Home
          page = 1;
          index = 0;
          break;
        case 35: // End
          page = lastPage;
          index = rowsPerPage - 1;
          break;
        case 33: // PageUp
          page = currentPage <= 1 ? lastPage : currentPage - 1;
          if (index < 0) {
            index = 0;
          }
          break;
        case 34: // PageDown
          page = currentPage >= lastPage ? 1 : currentPage + 1;
          if (index < 0) {
            index = rowsPerPage - 1;
          }
          break;
        case 38: // ArrowUp
          if (currentIndex <= 0) {
            page = currentPage <= 1 ? lastPage : currentPage - 1;
            index = rowsPerPage - 1;
          } else {
            index = currentIndex - 1;
          }
          break;
        case 40: // ArrowDown
          if (currentIndex >= lastIndex) {
            page = currentPage >= lastPage ? 1 : currentPage + 1;
            index = 0;
          } else {
            index = currentIndex + 1;
          }
          break;
      }

      if (page !== this.pagination.page) {
        this.pagination = {
          ...this.pagination,
          page,
        };

        this.$nextTick(() => {
          const { computedRows } = this.$refs.myTable;
          this.selected = [computedRows[Math.min(index, computedRows.length - 1)]];
        });
      } else {
        this.selected = [computedRows[index]];
      }
    },
    validarDato() {
      if (this.valor == "" || this.valor == null || this.valor == undefined) {
        if (!this.modal && this.bloqueante && this.bloqueante != undefined) {
          showDialog(() => { this.modal = true }, () => this.keepFocus());
        }
      } else if (this.longitudMin != undefined && this.valor.length < this.longitudMin) {
        this.confirm2 = true;
      } else {
        var a = this.Rawitems.filter((o) =>
          o[Object.keys(o)[0]].toLowerCase() == this.valor.toLowerCase()
        );
        if (a.length != 1 && !this.modal && this.bloqueanteNoCoincide == undefined) {
          showDialog(() => { this.modal = true }, () => this.keepFocus());
        }
      }
    },
    emitirSeleccion() {
      this.$emit("seleccion", this.valor, this.valor2);
      this.$emit("seleccionDescripcion", this.campoCaptura, this.valor2);
    },
    CargarOpciones(params) {
      ComunProvider.retornarPOST(this.options, params).then((res) => {
        this.Rawitems = res.data ?? {};
        this.columns = Object.keys(res.data[0] ?? []).map((b) => {
          return {
            name: b,
            label: b,
            field: b,
            align: "center",
            style: "table-layout: fixed;white-space: normal;",
          };
        });
        //ordena la tabla
        //let col = this.columns[0].name;
        this.pagination = {
          //sortBy: col,
          descending: false,
          rowsPerPage: 7,
        };
        if (this.Rawitems.length == 1) {
          this.valor = this.Rawitems[0][Object.keys(this.Rawitems[0])[0]];
          this.valor2 = this.Rawitems[0][Object.keys(this.Rawitems[0])[1]];
          this.$emit("seleccion", this.valor);
          this.$emit("seleccionDescripcion", this.campoCaptura, this.valor2);
          this.unaOpcion = true;
        } else {
          var a = res.data.filter((o) =>
            o[Object.keys(o)[0]].toLowerCase() == this.valor.toLowerCase()
          );
          if (a.length == 1) {
            this.valor2 = a[0][Object.keys(a[0])[1]];
            this.$emit("seleccionDescripcion", this.campoCaptura, this.valor2);
          }
        }
        this.$emit("estableceTexto", this.valor2);
      });
    },
    blur() {
      this.$emit("emitirFuncionesAfectantes", "LookUp");
      this.$emit("blur", this.valor);
    },
    establecerTexto() {
      this.$emit("estableceTexto", this.valor2);
    },
  },
  mounted() {
    if (this.extraparams != null && this.extraparams != undefined) {
      this.CargarOpciones(this.extraparams);
    } else {
      this.CargarOpciones();
    }
  },
};
