    /**
     * Método para cambiar el texto del campo localidad o zona por NA si no se digita nada
     */
    cambiarNA(e) {
      if (this.form.LOC_O_ZONA.trim() === "") {
        this.form.LOC_O_ZONA = "NA";
      }
      else{
        if(e != 1 && e != 2 && e != 3 && e != 4 && e != 5 && e != 6 && e != 7 && e != 8 && e != 9 && e != 10 &&
          e != 11 && e != 12 && e != 13 && e != 14 && e != 15 && e != 16 && e != 17 && e != 18 && e != 19 && e != 20)
        {
          if(e > 20 || e < 1){
            this.form.LOC_O_ZONA = "NA";
            this.$modal.alertar({titulo: "Alerta",mensaje: "Digite un valor del conjunto de valores admitidos para el ítem.",tipo: 2})
          }
          else{
            var resultado = e;
            var i = 0;
            var ultimo = 0;
            for(i = 1; i <= e.length; i ++){
              ultimo = e.charAt(e.length - i);
              if(resultado != ""){
                if(ultimo == 1 || ultimo == 2 || ultimo == 3 || ultimo == 4 || ultimo == 5
                  || ultimo == 6 || ultimo == 7 || ultimo == 8 || ultimo == 9 || ultimo == 0){
                  resultado = ultimo;
                }
                else{
                  resultado = resultado.replace(ultimo, "");
                }
              }
            }
            if(resultado > 12 || resultado == ""){
              this.$modal.alertar({titulo: "Alerta",mensaje: "Digite un valor del conjunto de valores admitidos para el ítem.",tipo: 2})
              this.form.LOC_O_ZONA = "NA";
            }
            else{
              this.$modal.alertar({titulo: "Alerta",mensaje: "Digite un valor del conjunto de valores admitidos para el ítem.",tipo: 2})
              this.form.LOC_O_ZONA = resultado;
            }
          }
        }
      }
    },
