    /**
     * Abre la ventana modal de creación o consulta uci
     */
    showModal: function () {
      caracterizacionProvider.consultarUcis(this.form)
      .then((res) => {
        if(res.data.length > 0){
          this.ajustarUci = false;
          this.cargarOpcionesUci(this.form);
          this.modalConsultaUci = true;
        }
        else{
          this.camposAjusteUci = false;
          this.camposAjusteUciSiete = false;
          this.modalUci = true;
          this.ajustarUci = true;
        }
          
        this.modal = false;
        this.modalConsulta = false;
        this.modalConsultaUpgds = false;
        this.modalNuevaUPGDS = false;
        this.modalTalento = false;
      })
      .catch(error => {
        console.log(error);
      });
    },
