    //Método que habilita el campo de ajuste para que el usuario pueda elegir la opción
    habilitarAjusteUci(){
      this.seleccionAjustarUci = false;
    },
    //Método que habilita los campos de acuerdo a la opción seleccionada por el usuario
    validarTipoAjusteUci(seleccion){
      if(seleccion == 7){
        this.camposAjusteUciSiete = false;
        this.ajustarUciGuardar = false;
        this.ajustarUci = true;
      }
      else{
        this.camposAjusteUciSiete = true;
        this.ajustarUciGuardar = false;
        this.ajustarUci = true;
      }
    },
    /**
     * Método que guarda las Uci asociadas a la Upgds siempre que se cumplan las validaciones
     */
    guardarUCI(){
      if(this.form.NOTIF_IAD == 1 && this.form.NIV != "1" ||
         this.form.NOTIF_IAD == true && this.form.NIV != "1"){
          var validacion = false;
          if(this.form.Tipo_uci.trim() == "" && validacion == false){
            this.$refs.Tipo_uci.focus();
            this.$modal.alertar({titulo: "Alerta",mensaje: "El tipo uci no puede ser nulo.",tipo: 2})
            this.envio = false;
            validacion = true;
          }
          if(this.form.Tot_uci.trim() == "" && validacion == false){
            this.$refs.totcama.focus();
            this.$modal.alertar({titulo: "Alerta",mensaje: "El total de camas no puede ser nulo.",tipo: 2})
            this.envio = false;
            validacion = true;
          }
          if(this.form.Sub_tipuci.trim() == "" && validacion == false){
            this.$refs.subtipoUci.focus();
            this.$modal.alertar({titulo: "Alerta",mensaje: "El subtipo uci no puede ser nulo.",tipo: 2})
            this.envio = false;
            validacion = true;
          }
          //Proceso validación total camas
          if(parseInt(this.form.Tot_uci) > parseInt(this.form.TOT_CAMAS)){
            validacion = true;
            this.$modal.alertar({titulo: "Alerta",mensaje: "El total de camas de la UPGD no puede ser superior a la sumatoria de camas de las UCI caracterizada.",tipo: 2})
          }
          if(validacion == false && this.form.Ajuste != 6){
            let param = null;
            param = { Cod_pre: this.form.COD_PRE + this.form.COD_SUB }
            caracterizacionProvider.consultarCamasCaracterizadasUci(param).then(res => {
              if(res.data.length > 0){
                var totalUci = 0;
                for(const element of res.data){
                  if(this.form.Id == element.id){
                    totalUci = totalUci + parseInt(this.form.Tot_uci);
                  }
                  else{
                    totalUci = totalUci + parseInt(element.Tot_uci);
                  }
                }
                if(this.form.Id == ""){
                  totalUci = totalUci + parseInt(this.form.Tot_uci);
                    if(parseInt(totalUci) > parseInt(this.form.TOT_CAMAS)){
                      validacion = true;
                      this.$modal.alertar({titulo: "Alerta",mensaje: "El total de camas de la UPGD no puede ser superior a la sumatoria de camas de las UCI caracterizada.",tipo: 2})
                    }
                    else{
                      caracterizacionProvider.guardarCaracterizacionUci(this.form)
                      .then(() => {
                        this.$modal.alertar({titulo: "Alerta",mensaje: "Se ha guardado la Uci con éxito.",tipo: 1})
                        this.envio = false;
                        this.modalUci = false;
                      })
                      .catch(error => {
                        console.log(error);
                      });
                    }
                }
                else{
                  let params = null;
                  params = { Cod_pre: this.form.COD_PRE + this.form.COD_SUB, id: this.form.Id}
                  caracterizacionProvider.consultarAjusteAnteriorUci(params)
                  .then(resAjuste => {
                    if(resAjuste.data.length > 0){//Quiere decir que este ítem estaba en ajuste descartado se suma el valor a ajustar
                      totalUci = totalUci + parseInt(this.form.Tot_uci);
                    }
                    if(parseInt(totalUci) > parseInt(this.form.TOT_CAMAS)){
                      validacion = true;
                      this.$modal.alertar({titulo: "Alerta",mensaje: "El total de camas de la UPGD no puede ser superior a la sumatoria de camas de las UCI caracterizada.",tipo: 2})
                    }
                    else{
                      caracterizacionProvider.guardarCaracterizacionUci(this.form)
                      .then(() => {
                        this.$modal.alertar({titulo: "Alerta",mensaje: "Se ha guardado la Uci con éxito.",tipo: 1})
                        this.envio = false;
                        this.modalUci = false;
                      })
                      .catch(error => {
                        console.log(error);
                      });
                    }
                  })
                  .catch(error => {
                    console.log(error);
                  });
                }
              }
              else{
                caracterizacionProvider.guardarCaracterizacionUci(this.form)
                  .then(() => {
                    this.$modal.alertar({titulo: "Alerta",mensaje: "Se ha guardado la Uci con éxito.",tipo: 1})
                    this.envio = false;
                    this.modalUci = false;
                  })
                  .catch(error => {
                    console.log(error);
                  });
              }
            });
          }
          else if(validacion == false){
            caracterizacionProvider.guardarCaracterizacionUci(this.form)
              .then(() => {
                this.$modal.alertar({titulo: "Alerta",mensaje: "Se ha guardado la Uci con éxito.",tipo: 1})
                this.envio = false;
                this.modalUci = false;
              })
              .catch(error => {
                console.log(error);
              });
          }
      }
    },
    mensajeError(campo) {
      if (campo === 'COD_PRE') {
        if (!this.$v.form.COD_PRE.required) return 'Campo requerido'
      }
      if (campo === 'razSocial') {
        if (!this.$v.form.RAZ_SOC.required) return 'Campo requerido'
      }
      if (campo === 'subIndice') {
        if (!this.$v.form.COD_SUB.required) return 'Campo requerido'
      }
      if (campo === 'FEC_CAR') {
        if (!this.$v.form.FEC_CAR.required) return 'Campo requerido'
        if (!this.$v.form.FEC_CAR.minLength) return 'Formato DD/MM/AAAA'
      }
      if (campo === 'NIT_UPGD') {
        if (!this.$v.form.NIT_UPGD.required) return 'Campo requerido'
        if (!this.$v.form.NIT_UPGD.minLength) return 'El campo debe contener mínimo 5 caractéres'
      }
      if (campo === 'DIR') {
        if (!this.$v.form.DIR.required) return 'Campo requerido'
      }
      if (campo === 'REP_LEG') {
        if (!this.$v.form.REP_LEG.required) return 'Campo requerido'
        if (!this.$v.form.REP_LEG.minLength) return 'El campo debe contener mínimo 2 caracteres'
      }
      if (campo === 'COR_ELE') {
        if (!this.$v.form.COR_ELE.required) return 'Campo requerido'
        if (!this.$v.form.COR_ELE.email) return 'Campo email'
      }
      if (campo === 'RES_NOT') {
        if (!this.$v.form.RES_NOT.required) return 'Campo requerido'
        if (!this.$v.form.RES_NOT.minLength) return 'El campo debe contener mínimo 2 caracteres'
      }
      if (campo === 'TEL') {
        if (!this.$v.form.TEL.required) return 'Campo requerido'
        if (!this.$v.form.TEL.minLength) return 'El campo debe contener mínimo 7 caractéres'
      }
      if (campo === 'FEC_CON') {
        if (!this.$v.form.FEC_CON.required) return 'Campo requerido'
        if (!this.$v.form.FEC_CON.minLength) return 'Formato DD/MM/AAAA'
      }
      if (campo === 'NAT_JUR') {
        if (!this.$v.form.NAT_JUR.required) return 'Campo requerido'
      }
      if (campo === 'NIV') {
        if (!this.$v.form.NIV.required) return 'Campo requerido'
      }
      if (campo === 'ES_UNI_NOT') {
        if (!this.$v.form.ES_UNI_NOT.required) return 'Campo requerido'
      }
      if (campo === 'ESTADOUPGD') {
        if (!this.$v.form.ESTADOUPGD.required) return 'Campo requerido'
      }
    },
