import axios from "axios";
import global from "../Config";

class OfflineService {
    sincronizar(params) {
        return axios.post(global.apiUrl + "offline/sincronizarTablas", params, { headers: global.headersToken })
    }

    retornarInactivos() {
        return axios.post(global.apiUrl + "offline/retornarInactivos", null, { headers: global.headersToken })
    }
}

export default new OfflineService();


