import axios from "axios";
import global from "../Config";
import { db } from "../utilidades/offline/dbModule";
// 'Authorization': 'Bearer ' + token

class GridLaboratoriosService {

    /**
     * Esta función retorna los laboratorios de un básico con idEvento dado
     * @param {} params 
     */
    retornarLaboratorios(params) {
        return axios.post(global.apiUrl + "laboratorios/consultarLaboratoriosDeUnBasico", params, { headers: global.headersToken })
    }

    async retornarLaboratoriosLocales(idEvento) {
        const evento = await db.Eventos.get(parseInt(idEvento.replace('OF', '')));
        return db.AnalisisLaboratorio.toCollection().filter(function (item) {
            return (
                item.cod_eve == evento.cod_eve &&
                item.semana == evento.semana &&
                item.ano == evento.año &&
                item.cod_pre == evento.cod_pre &&  
                item.cod_sub == evento.cod_sub &&
                item.tip_ide == evento.tip_ide &&
                item.num_ide == evento.num_ide
            )
        }).toArray()
    }
}

export default new GridLaboratoriosService();
