
export default {
  name: "CheckBoxControl",
  props: ["deshabilitar", "label", "valorCampo", "hayError", "mensajeError","tooltip"],
  data() {
    return {
      valorCrudo: (this.valorCampo == 1) ?? false,
      valorPorDefecto: 2
    };
  },
  methods: {
    emitirSeleccion() {
      this.$emit("seleccion", (this.valor) ? 1 : 2,(this.valor) ? 'Si' : 'No');
    },
    ponerFoco(){
      
    }
  },
  computed: {
    valor: {
      get () {
        return (this.valorCrudo == 1) ?? false
      },
      set (val) {
          this.valorCrudo = (val == 1 || val == 2) ? val : 2 
      }
    }
  },
  mounted(){
    this.emitirSeleccion();
  }
};
