import InputControl from "../../componentes/controles/InputControl";
import SearchFormUPGD from "../../componentes/controles/SearchFormUPGD";
import DropDownControl from "../../componentes/controles/DropDownControl";
import GridCaracterizacionProvider from "../../providers/GridCaracterizacionProvider";
import moment from 'moment';

export default {
  name: "GridCaracterizacion",
  components: { InputControl, DropDownControl, SearchFormUPGD },
  data() {
    return {
      pagesNumber:0,
      filter: '',
      filtro: null,
      loading: false,
      /**
       * Configuración inicial de la grilla
       */
      pagination: {

        descending: false,
        page: 1,
        rowsPerPage: 50,
        rowsNumber: 0
      },
      columns: [

        {
          name: 'name', align: 'center', label: '', field: row => row.idEntidad, sortable: false
        },
        { name: 'COD_PRE', align: 'center', label: 'Cod_pre', field: 'COD_PRE', sortable: true },
        { name: 'COD_SUB', align: 'center', label: 'Cod_sub', field: 'COD_SUB', sortable: true },
        { name: 'RAZ_SOC', align: 'center', label: 'Raz_soc', field: 'RAZ_SOC', sortable: true },
        {
          name: 'FEC_INICIAR', align: 'center', label: 'Fec_iniciar', field: 'FEC_INICIAR', sortable: true, format: (val) => {
            var dateTime = new Date(val);
            return dateTime = moment(dateTime).format("DD/MM/YYYY");
          }
        },
        {
          name: 'FEC_CAR', align: 'center', label: 'Fec_car', field: 'FEC_CAR', sortable: true, format: (val) => {
            var dateTime = new Date(val);
            return dateTime = moment(dateTime).format("DD/MM/YYYY");
          }
        },
        { name: 'NIT_UPGD', align: 'center', label: 'Nit_upgd', field: 'NIT_UPGD', sortable: true },
        { name: 'DIR', align: 'center', label: 'Dir', field: 'DIR', sortable: true },
        { name: 'REP_LEG', align: 'center', label: 'Rep_leg', field: 'REP_LEG', sortable: true },
        { name: 'COR_ELE', align: 'center', label: 'Cor_ele', field: 'COR_ELE', sortable: true },
        { name: 'RES_NOT', align: 'center', label: 'Res_not', field: 'RES_NOT', sortable: true },
        { name: 'TEL', align: 'center', label: 'Tel', field: 'TEL', sortable: true },
        {
          name: 'FEC_CON', align: 'center', label: 'Fec_con', field: 'FEC_CON', sortable: true, format: (val) => {
            var dateTime = new Date(val);
            return dateTime = moment(dateTime).format("DD/MM/YYYY");
          }
        },
        { name: 'NAT_JUR', align: 'center', label: 'Nat_jur', field: 'NAT_JUR', sortable: true },
        { name: 'NIV', align: 'center', label: 'Niv', field: 'NIV', sortable: true },
        { name: 'ES_UNI_NOT', align: 'center', label: 'Es_uni_not', field: 'ES_UNI_NOT', sortable: true },
        { name: 'ESTADOUPGD', align: 'center', label: 'EstadoUPGD', field: 'ESTADOUPGD', sortable: true },
        { name: 'LOC_O_ZONA', align: 'center', label: 'Loc_o_zona', field: 'LOC_O_ZONA', sortable: true },
        { name: 'NOTIF_IAD', align: 'center', label: 'Notif_iad', field: 'NOTIF_IAD', sortable: true },
        { name: 'NOTIF_ISO', align: 'center', label: 'Notif_iso', field: 'NOTIF_ISO', sortable: true },
        { name: 'NOTIF_CAB', align: 'center', label: 'Notif_cab', field: 'NOTIF_CAB', sortable: true },
        { name: 'ACT_SIV', align: 'center', label: 'Act_siv', field: 'ACT_SIV', sortable: true },
      ],
      data: [],
      original: [

      ],
      filas: 0,
      busq: false,
      drawerLeft: false,
    }
  },

  mounted() {
    // let idUsuario = { idUsuario: '1' };
    this.loading = true;
    this.onRequest({pagination: this.pagination,filter: undefined});
    this.$emit("disenoPagina", true);

  },
  methods: {
    avanza(){
      this.onRequest({pagination: this.pagination, filter:this.filter, filtro:this.filtro});
    },
    /**
     * Abre la upgd con el id dado
     * @param {*} id
     */
    enviar(id) {
      let ruta = "Caracterizacion/" + id;
      this.$router.push(ruta);
    },
    onRequest(props) {
      const { page, rowsPerPage} = props.pagination;

      let filtros = {};
      if(props.filter != '' && props.filter != undefined){
        const filtro = this.$refs['buscador'].buscaDesdeBarra(props.filter);
        filtros = {filtros,...filtro};
      }else if(props.filtro != undefined && props.filtro != null){
        filtros = props.filtro;
      }

      // calculate starting row of data
      let startRow = (page - 1) * rowsPerPage
      if(startRow < 0)
        startRow = 0
      this.fetchFromServer(startRow, rowsPerPage,filtros,page);
    },

    // Filtra solo por las llaves dadas
    fetchFromServer(startRow, count, parametros, page) {
      this.loading = true;
      parametros = {startRow, numeroReg: count, ...parametros};
      GridCaracterizacionProvider.retornarUPGDs(parametros).then(res => {
        this.data = res.data;
        this.pagination.rowsNumber = this.data.length > 0 ? this.data[0].Oportuna : 0;
        this.pagination.page = page;
        this.pagesNumber = Math.ceil(this.pagination.rowsNumber/this.pagination.rowsPerPage);
        this.loading = false;
      }, err => {
        if (err.response.status == 401) {
          this.$modal.alertar({
            titulo: "Atención", mensaje: "Sesión vencida, por favor vuelva a ingresar", tipo: 2, onConfirm: () => {
              window.location.href = global.siv40;
            }
          })
        }
      })
    },

    // Cuenta el número de filas
    getRowsNumberCount(filter) {
      if (!filter) {
        this.filas = this.original.length
      }
    },
    /**
    * Hace el toggle para abrir y cerrar la búsqueda avanzada
    */
    search() {
      this.drawerLeft = !this.drawerLeft;
    },
    /**
     * Busca filas que tengan los valores dados en las llaves dadas
     */
    buscar() {
      this.filter = '';
      this.fetchFromServer(0,50,{limitaRegistros:'0',...this.filtro},1);
      this.loading = false;
    },
    /**
     * Limpia la búsqueda avanzada
     */
    reinicio(){
      this.filtro = '';
      this.onRequest({
        pagination: this.pagination,
        filter: undefined
      })
    },
    setFiltro(objeto){
      this.filtro = objeto;
    }
  }
}
