// Grilla que muestra la información de los laboratorios al ingresar en la opción de individuales
import InputControl from "../../componentes/controles/InputControl";
import SearchForm from "../../componentes/controles/SearchForm";
import DropDownControl from "../../componentes/controles/DropDownControl";
import GridLaboratoriosProvider from "../../providers/GridLaboratoriosProvider";
import VueOnlineOffline from "vue-online-offline";
import global from "@/Config";
import moment from 'moment';

export default {
  name: "GridLaboratorios",
  components: { InputControl, DropDownControl, SearchForm, VueOnlineOffline },
  props: ['idEvento'],
  data() {
    return {
      filter: '',
      loading: false,
      /**
       * Configuración inicial de la grilla
       */
      pagination: {
        sortBy: 'Cod_pre',
        descending: false,
        page: 1,
        rowsPerPage: 50,
        rowsNumber: 10,
      },
      columns: [

        { name: 'name', align: 'center', label: '', field: row => row.id, sortable: true },
        { name: 'Cod_pre', align: 'center', label: 'Cod_pre', field: 'Cod_pre', sortable: true },
        { name: 'Cod_sub', align: 'center', label: 'Cod_sub', field: 'Cod_sub', sortable: true },
        { name: 'Ano', align: 'center', label: 'Año', field: 'Ano', sortable: true },
        { name: 'Semana', align: 'center', label: 'Semana', field: 'Semana', sortable: true },
        { name: 'Tip_ide', align: 'center', label: 'Tip ide', field: 'Tip_ide', sortable: true },
        { name: 'Num_ide', align: 'center', label: 'Num ide', field: 'Num_ide', sortable: true },
        { name: 'Pri_nom', align: 'center', label: 'Pri_nom', field: 'Pri_nom', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Seg_nom', align: 'center', label: 'Seg_nom', field: 'Seg_nom', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Pri_ape', align: 'center', label: 'Pri_ape', field: 'Pri_ape', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Seg_ape', align: 'center', label: 'Seg_ape', field: 'Seg_ape', sortable: true, format: (val) => val.toUpperCase() },
        { name: 'Cod_eve', align: 'center', label: 'Cod eve', field: 'Cod_eve', sortable: true },

        {
          name: 'Fec_exa', align: 'center', label: 'Fec_exa', field: 'Fec_exa', sortable: true, format: (val) => {
            return (val != null) ? moment(val).format("DD/MM/YYYY") : '';
          }
        },
        {
          name: 'Fec_rec', align: 'center', label: 'Fec_rec', field: 'Fec_rec', sortable: true, format: (val) => {
            return (val != null) ? moment(val).format("DD/MM/YYYY") : '';
          }
        },
        { name: 'Muestra', align: 'center', label: 'Muestra', field: 'Muestra', sortable: false },
        { name: 'Prueba', align: 'center', label: 'Prueba', field: 'Prueba', sortable: false },
        { name: 'Agente', align: 'center', label: 'Agente', field: 'Agente', sortable: false },
        { name: 'Resultado', align: 'center', label: 'Resultado', field: 'Resultado', sortable: false },
        {
          name: 'Fec_exp', align: 'center', label: 'Fec_exp', field: 'Fec_exp', sortable: true, format: (val) => {
            return (val != null) ? moment(val).format("DD/MM/YYYY") : '';
          }
        },
        { name: 'Valor', align: 'center', label: 'Valor', field: 'Valor', sortable: false, format: (val) => val.toUpperCase() },
      ],
      data: [],
      original: [

      ],
      vacio: true,
      filas: 0,
    }
  },

  mounted() {
    let idEvento = { idEvento: this.idEvento };
    this.loading = true;
    // get initial data from server (1st page)
    /**
       * Configuración inicial de la grilla, retorna los laboratorios del evento con idEvento
       */
    GridLaboratoriosProvider.retornarLaboratorios(idEvento).then(res => {
      //Sí la respuesta es vacía indica que no se ha ingresado ningún registro previamente
      if (res.data.length === 0) {
        this.vacio = true;
        this.loading = false;
        this.filas = 0;

      }
      //Caso contrario se ingresa la información encontrada y se página la cantidad de registros
      else {
        this.original = res.data;
        this.loading = false;
        this.filas = this.original.length;
        this.onRequest({
          pagination: this.pagination,
          filter: undefined
        })
      }
    })
    this.$emit("disenoPagina", true);

  },
  methods: {

    /**
     * Abre el laboratorio con el id de eventado y id de laboratorio dado
     * @param {*} id
     */
    enviar(id) {
      console.log(id);
      let ruta = `/Laboratorios/${this.idEvento}/${id}`;
      this.$router.push(ruta);
    },
    /**
     * va hacia atrás
     */
    atras() {
      //let ruta = "../Individuales/" + this.idEvento;
      this.$router.go(-1);
    },
    onRequest(props) {
      const { page, rowsPerPage, sortBy, descending } = props.pagination
      const filter = props.filter

      this.loading = true

      // emulate server
      //setTimeout(() => {
      // update rowsCount with appropriate value

      // Obtener todas las filas si se selecciona "Todas" (0)
      const fetchCount = rowsPerPage === 0 ? this.pagination.rowsNumber : rowsPerPage

      // Calcular la fila inicial de datos
      const startRow = (page - 1) * rowsPerPage

      // Obtener datos del "servidor"
      const returnedData = this.fetchFromServer(startRow, fetchCount, filter, sortBy, descending)

      // Borrar los datos existentes y agregar nuevos
      this.data.splice(0, this.data.length, ...returnedData)

      // Actualizar el objeto de paginación local
      this.pagination.page = page
      this.pagination.rowsPerPage = rowsPerPage
      this.pagination.sortBy = sortBy
      this.pagination.descending = descending

      // Indicador de giro de carga
      this.loading = false
      //}, 1500)
    },

    async fetchData(startRow, count, parametros, page) {
      await new Promise(resolve => setTimeout(resolve, 100));
      if (global.isOnline) {
        this.fetchFromServer(startRow, count, parametros, page);
      }
      else {
        parametros = { startRow, numeroReg: count, ...parametros };
        try {
          this.data = await GridLaboratoriosProvider.retornarLaboratoriosLocales(this.idEvento);
          this.pagination.rowsNumber = this.data.length > 0 ? this.data.length : 0;
          this.pagination.page = page.toString() == '0' ? 1 : page;
          this.pagesNumber = Math.ceil(this.pagination.rowsNumber / this.pagination.rowsPerPage);
          this.loading = false;
          this.registrosEnPagina = this.pagination.page * this.pagination.rowsPerPage > this.pagination.rowsNumber ?
            this.pagination.rowsNumber : this.pagination.page * this.pagination.rowsPerPage;
        } catch (err) {
          console.log(err);
        }
      }
    },

    // Filtra solo por las llaves dadas
    fetchFromServer(startRow, count, filter, sortBy, descending) {
      const data = filter ? this.original.filter((o) => Object.keys(o).filter((x) => (
        x == "Tip_ide" ||
        x == "Num_ide" ||
        x == "Pri_nom" ||
        x == "Seg_nom" ||
        x == "Pri_ape" ||
        x == "Seg_ape" ||
        x == "Ano" ||
        x == "Semana" ||
        x == "Cod_pre"
      )
      ).some((k) => { //Convierte la cadena en minúscula para comparación
        if (o[k] != null) {
          return o[k].toString().toLowerCase().includes(filter.toString().toLowerCase())
        }
      })) : this.original.slice()

      // Manejar ordenar por
      if (sortBy) {
        const sortFn = sortBy === 'desc'
          ? (descending
            ? (a, b) => (a.name > b.name ? -1 : a.name < b.name ? 1 : 0)
            : (a, b) => (a.name > b.name ? 1 : a.name < b.name ? -1 : 0)
          )
          : (descending
            ? (a, b) => (parseFloat(b[sortBy]) - parseFloat(a[sortBy]))
            : (a, b) => (parseFloat(a[sortBy]) - parseFloat(b[sortBy]))
          )
        data.sort(sortFn)
      }

      this.filas = data.length;
      this.pagination.rowsNumber = data.length;

      return data.slice(startRow, startRow + count)
    },

    // Cuenta el número de filas
    getRowsNumberCount(filter) {
      if (!filter) {
        this.filas = this.original.length
      }
    }
  }
}
