    cambiarValorEpidemiologico: function (valor, inputControl, control, valorDependencia, valorFecNot, campo, cod_eve, cambioTab, adicionales) {
        let params = null;
        let mostrarAlert = false;
        valorDependencia = moment(valorDependencia, "DD/MM/YYYY", true);
        valorFecNot = moment(valorFecNot, "DD/MM/YYYY", true);
        if (adicionales[0] && !cambioTab) {
            if (valorDependencia.isValid() && cod_eve != "000") {
                params = { fecha: valorDependencia.format("DD/MM/YYYY") }
                mostrarAlert = comun.tieneValor(valor) ? true : false;
            } else if (valorFecNot.isValid()) {
                params = { fecha: valorFecNot.format("DD/MM/YYYY") }
                mostrarAlert = false;
            }
            if (params != null) {
                if (campo == "mes") {
                    inputControl.valor = moment(params.fecha, "DD/MM/YYYY").format("M");
                } else {
                    ComunProvider.obtenerFechaEpidemiologica(params).then(res => {
                        if (res.data != null) {
                            if (mostrarAlert && inputControl.valor != res.data[campo]) {
                                if (!adicionales[3].colectivo) {
                                    inputControl.mostrarAlerta("Alerta", "Se cambiará la semana epidemiológica en función de la fecha que acaba de digitar, por cuanto la definición de este evento de salud así lo establece.", 3)
                                } else {
                                    inputControl.mostrarAlerta("Alerta", "Se ajustará la semana epidemiológica en función de la fecha que acaba de ingresar.", 3)
                                }
                            }
                            inputControl.valor = res.data[campo];
                        } else {
                            inputControl.valor = "";
                        }
                    })
                }
            }
        }
    },
