    /**
     * Método que permite sumar n campos y asignarle el valor a otro campo que inactiva para que el usuario no lo pueda alterar
     * @param {*} valor
     * @param {*} inputControl
     * @param  {...any} args
     */
    cambiarValorSumatoria: function (valor, inputControl, ...args) {
        inputControl.deshabilitar = true;
        let rawParams = args.slice(1, args.length - 1);
        var i = 0;
        var sumatoria = 0;
        for (i = 0; i < rawParams.length; i++) {
            if (rawParams[i] != "") {
                sumatoria = sumatoria + parseInt(rawParams[i]);
            }
            else {
                sumatoria = sumatoria + 0;
            }
            if (!args[args.length - 1][3].cambioTab);
            inputControl.valor = sumatoria;
        }
    },
    /**
     * Método que suma diferentes campos checks y registra su valor en un campo diferente al que también deja de solo lectura
     * @param {*} valor
     * @param {*} inputControl
     * @param  {...any} args
     */
    cambiarValorSumatoriaChecks: function (valor, inputControl, control, ...args) {
        inputControl.deshabilitar = true;
        let rawParams = args.slice(0, args.length - 1);
        const tamanio = rawParams.filter(x => x == 1).length;
        if (!args[args.length - 1][3].cambioTab)
            inputControl.valor = tamanio;
    },
    /**
     * Método que valida un/varios valores para determinar si debe mostrar un mensaje o no
     * @param {*} valor
     * @param {*} inputControl
     * @param {*} control
     * @param {*} valores
     * @param {*} mensaje
     * @param {*} valoresDependiente
     */
    mostrarMensaje: function (valor, inputControl, control, valores, mensaje, valoresDependiente, muestraAlCargar, adicionales) {
        
        let condicion = valoresDependiente.split(',');
        adicionales = !comun.tieneValor(adicionales) ? muestraAlCargar : adicionales;  // solución para no afectar las que ya estaban parametrizadas en la bd y que no tienen los nuevos parametros
        muestraAlCargar = !comun.tieneValor(adicionales) ? "0" : muestraAlCargar;
        if (condicion.includes(valores.toString())) {
            if (muestraAlCargar == null || muestraAlCargar == "1") {
                console.log("debería mostrar mensaje");
                inputControl.mostrarAlerta("Alerta", mensaje, 3);
            } else {
                if (adicionales[0] && !adicionales[3].cambioTab) {
                    inputControl.mostrarAlerta("Alerta", mensaje, 3);
                }
            }

        }
    },
