    calcularDDD: function (valor, inputControl, control, numeroGramos, mes, año, camas, proporcionOcupacion, dividido, adicionales) {
        inputControl.deshabilitar = true;
        //Calcula los días de acuerdo al mes y al año seleccionado
        if (año != "" && mes != "") {
            var diasMes = new Date(año, mes, 0).getDate();
        }
        else {
            diasMes = 1;
        }
        if (camas == "") {
            camas = 1;
        }
        if (proporcionOcupacion == "") {
            proporcionOcupacion = 1;
        }
        var ddd = ((numeroGramos / parseFloat(dividido)) / (diasMes * camas * proporcionOcupacion)) * 100;
        ddd = Math.round(ddd * 1000) / 1000;
        if (!adicionales[3].cambioTab)
            inputControl.valor = ddd.toString();
    },
