

export default {
  name: "FechaControl",
  props: ["deshabilitar", "tooltip", "mask", "valorCampo","hayError","mensajeError","esTabla", "campo"],
  data() {
    return {
      valorCrudo: this.valorCampo,
      claseTabla: this.esTabla ? "inputsTablasFecha" : "",
    };
  },
  methods: {
    emitirSeleccion() {
      this.$refs.qDateProxy.hide();
      this.$emit("seleccion", this.valor);
    },
    emitirComportamiento() {
      if(this.campo == "Fecha_nto"){
        if(this.valor == ""){
          this.$q.dialog({
              title: '<i class="fas fa-question-circle" style="color: #fb8c00;font-size: x-large;"></i> Atencion',
              message: '¿Desea continuar sin ingresar la fecha de nacimiento?',
              ok: {
                color: 'primary',
                label: "Si"
              },
              cancel: {
                flat: true,
                color: 'primary',
                label: "No"
              },
              persistent: true,
              html: true
            }).onOk(() => {
              this.$refs["input"].blur();
            }).onCancel(() => {
              this.$refs["input"].focus();
            })
        } else {
          this.$emit("blur", this.valor);
        }        
      } else {
        this.$emit("blur", this.valor);
      }
    }
  },
  computed: {
    valor: {
      get () {
        return this.valorCrudo == null ? "" : this.valorCrudo
      },
      set (val) {
          this.valorCrudo = val        
      }
    }
  }
};
