import axios from "axios";
import global from "../Config";


// 'Authorization': 'Bearer ' + token

class GridCaracterizacionService {

    retornarUPGDs(params) {
        return axios.post(global.apiUrl + "upgds/obtenerUltimasUpgds",params,{headers:global.headersToken})
    }

    retornarUpgdsParametros(params) {
        return axios.post(global.apiUrl + "upgds/retornarUpgdsParametros",params,{headers:global.headersToken})
    }
}

export default new GridCaracterizacionService();
