import axios from "axios";
import global from "../Config";
import { db } from "../utilidades/offline/dbModule";

let headers = {
    "Content-Type": "application/json",
}

// let headersArchivo =  {'Accept': 'application/json','Content-Type': 'multipart/form-data',}


// 'Authorization': 'Bearer ' + token

class IndividualesService {

    retornarCamposBasicos(params) {
        return axios.post(global.apiUrl + "Individuales/retornarDatosBasicos", params, { headers })
    }

    retornarEstructuraBasicos(params) {
        return axios.post(global.apiUrl + "Individuales/retornarEstructuraGuardadoBasicos", params, { headers })
    }

    retornarDatosComplementarios(params) {
        console.log(params);
        return axios.post(global.apiUrl + "Individuales/retornarDatosComplementarios", params, { headers })
    }

    retornarLabelsImpresion(cod_eve, llaves) {
        const campos = llaves.toString();
        let params = { cod_eve, campos };
        console.log(campos);
        return axios.post(global.apiUrl + "Individuales/retornarLabelsImpresion", params, { headers })
    }


    obtenerComplementario(params) {
        return axios.post(global.apiUrl + "Individuales/obtenerComplementario", params, { headers })
    }

    obtenerBasico(params) {
        //Si el id del evento es OFXX, la informacion debe sacarse de la base de datos local (Dexie)
        if (params.idEvento.startsWith('OF')) {
            return this.obtenerBasicoOffline(params.idEvento);
        } else {
            return axios.post(global.apiUrl + "Individuales/obtenerBasico", params, { headers: global.headersToken })
        }
    }

    async obtenerBasicoOffline(idEventoOffline) {
        const idEvento = parseInt(idEventoOffline.replace('OF', ''));
        let doc = await db.Eventos.get(idEvento);
        doc.id = doc._id
        doc.Fec_aju = this.formatDate(doc.Fec_aju);
        doc.Fec_arc_pl = this.formatDate(doc.Fec_arc_pl);
        doc.Fec_con = this.formatDate(doc.Fec_con);
        doc.Fec_def = this.formatDate(doc.Fec_def);
        doc.Fec_hos = this.formatDate(doc.Fec_hos);
        doc.Fec_not = this.formatDate(doc.Fec_not);
        doc.Fec_reacti = this.formatDate(doc.Fec_reacti);
        doc.Fecha_nto = this.formatDate(doc.Fecha_nto);
        doc.Ini_sin = this.formatDate(doc.Ini_sin);
        return { data: [doc] }; //retorna un objeto con la propiedad data para que el flujo funcione como si viniera de una peticion
    }

    formatDate(date) {
        if (date == '' || date == null) return;
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();

        if (month.length < 2)
            month = '0' + month;
        if (day.length < 2)
            day = '0' + day;

        return [year, month, day].join('-');
    }

    obtenerPropiedadesEvento(codigoEvento) {
        return axios.post(global.apiUrl + "Individuales/obtenerTipoEventoPorCodigo", { codigoEvento }, { headers: global.headersToken })
    }

    obtenerCamposComplementarios(params) {
        params = { cod_eve: params }
        return axios.post(global.apiUrl + "Individuales/obtenerCamposComplementarios", params, { headers })
    }

    retornarDetalleEventoEspecifico(params) {
        params = { idEvento: params }
        return axios.post(global.apiUrl + "Individuales/retornarDetalleEventoEspecifico", params, { headers })
    }

    guardarDatosBasicos(params) {
        return axios.post(global.apiUrl + "SvcCrud/Eventos", params, { headers: global.headersToken })
    }

    guardarDatosComplementarios(params) {
        return axios.post(global.apiUrl + "SvcCrud/DetallesEventos", params, { headers: global.headersToken })
    }

    obtenerEstructuraBdua() {
        return axios.post(global.apiUrl + "comunCaptura/retornarOrdenCamposBDUA", null, { headers: global.headersToken })
    }

    alarmaIntoxicaciones(params, idEvento) {
        var formData = new FormData();
        formData.append("file", params);
        formData.append("idEvento", idEvento);
        return axios.post(global.apiUrl + "Individuales/alarmaIntoxicaciones", formData, { headers: headers })
    }

    guardarIndividual(params) {
        return axios.post(global.apiUrl + "SvcCrud/Evento", params, { headers: global.headersToken })
    }

    async guardarDatosBasicosLocal(basico, complementario, idOffline, requerirLaboratorios) {
        let res = { data: { response: true } };
        try {
            if (complementario) basico.complementario = complementario;
            basico.SyncError = 0;
            let idEventoDexie;
            if (idOffline.startsWith("OF"))
                idEventoDexie = await db.Eventos.update(parseInt(idOffline.replace("OF", "")), basico);
            else
                idEventoDexie = await db.Eventos.add(basico);
            idEventoDexie = `OF${idEventoDexie}`;
            res.tipo = idEventoDexie
            res.message = "Evento cargado con éxito";
            if (requerirLaboratorios) this.guardarRelacionEventoLaboratorio(idEventoDexie);
            return res;
        } catch (err) {
            console.log(err);
            res.data.response = false;
            res.data.mensaje = "No pudo guardarse el registro, por favor consulte al administrador del sistema";
            return res;
        }
    }

    /**
     * Este metodo se usa para guardar una relacion entre los id de eventos locales y los id sicronizados
     * con el proposito de, al guardar laboratorios, vincularlos luego al id que corresponda
     * @param {*} idOffline 
     */
    async guardarRelacionEventoLaboratorio(idOffline) {
        const relacionEventosLab = await db.RelacionEventosLaboratorios.where("idEventoOffline").equals(idOffline).first();
        if (!relacionEventosLab) await db.RelacionEventosLaboratorios.add({ idEventoOffline: idOffline, idEventoOnline: null });
    }

    guardarEvento(basico, complementario) {
        let params = { evento: basico, complementario }
        return axios.post(global.apiUrl + "SvcCrud/Evento", params, { headers: global.headersToken })
    }

}

export default new IndividualesService();
